/* Tabs */
.tabrow section.tabs.vertical {
	display: flex;

	> ul {
		flex: 0;
	}

	> div.tab-content.columns {
		flex: 1 auto;
	}
}
.tabzone {
	margin: 0;
	padding: 2px;
	border-width: 1px;
	border-style: solid;
	border-color: #eee;
}

.tab-boxes, .zoneb {
	display: flex;
	flex-wrap: wrap;
}
@media (max-width: $screen-lg) {
	.tab-boxes {
		flex-direction:column;
		> div.zone1, div.zone2, div.zone3, div.zone4 {
			flex: auto;
		} 
	}
}
/* code copied from legacy\ui\_tabs*/
.tabs {
	display: block;
}

.tab-nav {
	margin: 0;
	padding: 0;
	border-bottom: 1px solid #cecfd2;
	 > li {
		display: inline-block;
		width: auto;
		padding: 0;
		margin: 0 1px -1px 0;
		cursor: default;
		top: 1px;
		-webkit-box-shadow: 0 1px 0 white;
		-moz-box-shadow: 0 1px 0 white;
		box-shadow: 0 1px 0 white;
		> li {
		display: inline-block;
		width: auto;
		padding: 0;
		margin: 0 3px 0 0;
		cursor: default;
		top: 1px;
		-webkit-box-shadow: 0 1px 0 white;
		-moz-box-shadow: 0 1px 0 white;
		box-shadow: 0 1px 0 white;
		> a {
			display: block;
			width: auto;
			padding: 0 8.4px 14px 8.4px;
			margin: 0;
			color: #202021;
			font-family: "Open Sans Regular", "Arial";
			font-weight: 600;
			border: 1px solid #cecfd2;
			border-width: 1px 1px 0 1px;
			text-shadow: 0 1px 1px #e8e9ea;
			background: #dbdcde;
			cursor: pointer;
			-webkit-border-radius: 4px 4px 0 0;
			-moz-border-radius: 4px 4px 0 0;
			-ms-border-radius: 4px 4px 0 0;
			-o-border-radius: 4px 4px 0 0;
			border-radius: 4px 4px 0 0;
			height: 28px;
			line-height: 26px;
			&:hover {
				text-decoration: none;
				background: #dedfe0;
			}
			&:active {
				background: #d6d7d9;
			}
		}
		&.active > a {
			height: 29px;
			line-height: 27px;
			background: white;
			cursor: default;
		}
		&:last-child {
			margin-right: 0;
		}
	}
 }
}

.tab-nav > li:last-child {
	margin-right: 0;
}

.tab-nav > li > a {
	display: block;
	width: auto;
	padding: 0 8.4px 14px 8.4px;
	margin: 0;
	color: #202021;
	font-family: "Open Sans Regular", "Arial";
	font-weight: 600;
	border: 1px solid #cecfd2;
	border-width: 1px 1px 0 1px;
	text-shadow: 0 1px 1px #e8e9ea;
	background: #dbdcde;
	cursor: pointer;
	-webkit-border-radius: 4px 4px 0 0;
	-moz-border-radius: 4px 4px 0 0;
	-ms-border-radius: 4px 4px 0 0;
	-o-border-radius: 4px 4px 0 0;
	border-radius: 4px 4px 0 0;
	height: 28px;
	line-height: 26px;
	&:hover {
		text-decoration: none;
		background: #dedfe0;
	}
	&:active {
		background: #d6d7d9;
	}
}

.tab-nav > li.active > a {
	height: 29px;
	line-height: 27px;
	background: white;
}

.tabs.pill .tab-nav {
	width: 100%; /* remove if you dont want the tabs to span the full container width */
	display: table;
	overflow: hidden;
	border: 1px solid #cecfd2;
	-webkit-border-radius: 4px;
	-moz-border-radius: 4px;
	-ms-border-radius: 4px;
	-o-border-radius: 4px;
	border-radius: 4px;
	 > li {
		display: table-cell;
		margin: 0;
		margin-left: -4px;
		text-align: center;
		top: 0;
		&:first-child {
			margin-left: 0;
		}
		> a {
			border: none;
			border-right: 1px solid #cecfd2;
			-webkit-border-radius: 0;
			-moz-border-radius: 0;
			-ms-border-radius: 0;
			-o-border-radius: 0;
			border-radius: 0;
			height: 28px;
			line-height: 26px;
		}
		&:last-child > a {
			border-right: none;
		}
	}
}

.tab-content {
	display: none;
	padding: 20px 10px;
	&.active {
	display: block;
	}
}

.tabs.vertical {
	.tab-nav {
		border: none;
		> li {
			display: block;
			margin: 0;
			margin-bottom: 5px;
			white-space: nowrap;
			overflow: hidden;
			&.active {
				position: relative;
				z-index: 99;
				> a {
					border-right: 1px solid #ececed;
				}
			}
			> a {
				border: 1px solid #cecfd2;
				-webkit-border-radius: 4px 0 0 4px;
				-moz-border-radius: 4px 0 0 4px;
				-ms-border-radius: 4px 0 0 4px;
				-o-border-radius: 4px 0 0 4px;
				border-radius: 4px 0 0 4px;
			}
		}
	}
	.tab-content {
		padding: 10px 0 30px 20px;
		margin-left: -1px;
		border-left: 1px solid #cecfd2;
	}
}

.tabs.horizontal {
	.tab-nav {
		border-bottom: 1px solid #cecfd2;
		> li {
			display: inline-block;
			overflow: visible;
			width: auto;
			padding: 0;
			margin: 0 1px -1px 0;
			top: 1px;
			-webkit-border-radius: 4px 4px 0 0;
			-moz-border-radius: 4px 4px 0 0;
			-ms-border-radius: 4px 4px 0 0;
			-o-border-radius: 4px 4px 0 0;
			border-radius: 4px 4px 0 0;
			&.active {
				position: static;
				z-index: 99;
				> a {
					border-right: 1px solid #cecfd2;
					border-bottom: 1px solid white;
				}
			}
			> a {
				border: 1px solid #cecfd2;
				-webkit-border-radius: 4px 4px 0 0;
				-moz-border-radius: 4px 4px 0 0;
				-ms-border-radius: 4px 4px 0 0;
				-o-border-radius: 4px 4px 0 0;
				border-radius: 4px 4px 0 0;
			}
		}
	}
	.tab-content {
		display: none;
		padding: 20px 10px;
	}
}