
.legend {
	font-size: $font-size-xsmall;
	white-space: nowrap;

	.number {
		font-size: 22px;
		margin-left: -14px;
		color: #333;
	}

	.description {
		font-size: $font-size-xsmall;
		opacity: 0.7;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	.circle {
		font-size: 8px;
		vertical-align: middle;
		margin-bottom: 2px;
		margin-right: -2px;
		padding-right: 4px;

		&.success {
			color: $brand-success;
		}

		&.warning {
			color: $brand-warning
		}

		&.danger {
			color: $brand-danger;
		}

		&.primary {
			color: $brand-primary;
		}

		&.default {
			color: $text-color;
		}
	}

	.legend-text {
		margin: 0 12px 0 5px;
	}
}

.legends {
    text-align: right;
	margin: 0;
    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis;

    @media (max-width: $screen-md) {
        text-align: left;
		margin: 0;
    }
}

.legend-divider {
    border: dashed 1px #666;
    height: 2px;
    width: 60px;
    opacity: 0.2;
    margin: 15px 0;
}


.legend-divider-full-length {
        border: dashed 1px #979797;
        height: 2px;
        opacity: 0.5;
        margin: 15px 0;
    }



.legend-full-length {
    font-size: $font-size-xsmall; // 12px
    white-space: nowrap;

    @media (max-width: $screen-md) {
        font-size: 12px;
        white-space: normal;
    }

    .number {
        font-size: 24px;
        font-weight: bold;
    }

    .percentage {
        font-size: 15px;
        color: black;

        .sign {
            font-size: $font-size-xsmall;
        }
    }

    .description {
        font-size: $font-size-xsmall;
        opacity: 0.7;
    }
}