@import url('https://fonts.googleapis.com/css?family=Roboto+Condensed:400,700|Roboto:300,400,500,700');

.menu-icon .vi {
    font-size: $sidebar-menu-font-size + 3;
    line-height: 14px;
}

.menu-title:empty {
    display: none;
}

.site-left-sidebar {
    top: 0;
    padding-right: 0;
}

@media (min-width: $screen-sm) {
    .site-left-sidebar {
		height: fit-content;
        box-shadow: 0px 40px 50px 0px rgba(0, 0, 0, 0.4);
        transition: width 0.15s;
        z-index: 1050;
    }
}

.layout-mobile.layout-left-sidebar-opened .site-left-sidebar {
    box-shadow: 0px 40px 50px 0px rgba(0, 0, 0, 0.4);
}

.layout-mobile .site-left-sidebar {
    box-shadow: none;
}

.sidebar-backdrop {
    height: auto;
}

// Menu
.sidebar-menu {
    hr {
        margin: 10px 0 0;
        border-color: darken($sidebar-menu-link-color, 30%);
    }

    .menu-title {
        color: $sidebar-menu-title-color;
        font-size: $sidebar-menu-font-size - 1;
        padding-left: 15px;
        font-family: "Roboto Condensed", sans-serif;
        opacity: 1;
        font-weight: 700;
        letter-spacing: 1px;
    }

    .menu-icon {
        font-size: $sidebar-menu-font-size;
		margin-right: 7px;
        margin-top: 0;
    }
    .menu-badge {
        display: flex;
        align-items: center;
    }
    > li:first-child {
        padding-bottom: 10px;
    }

    > li > a {
        color: $sidebar-menu-link-color;
        cursor: pointer;
        display: block;
        font-size: $sidebar-menu-font-size;
        font-weight: 400;
        letter-spacing: 0.5px;
        line-height: 36px;
        padding: 0 15px;
        position: relative;
        text-decoration: none;
        transition: all 0.15s;
        border-bottom: none;
        border-top: none;
        align-items: center;

        &:hover {
            background-color: transparent;
            color: $sidebar-menu-link-hover-color;
        }

        &:hover .menu-icon {
            color: $sidebar-menu-link-hover-color;
        }
        
        &:before,
        .menu-icon {
            color: $sidebar-menu-link-color;
        }
    }

    > li.active > a {
        background-color: $sidebar-menu-link-open-bg;
        color: $sidebar-menu-link-open-color;
        font-weight: 700;

        &:before,
        .menu-icon { 
            color: $sidebar-menu-link-open-color;
        }

        &:hover {
            background-color: lighten($sidebar-menu-link-open-bg, 2%);
            color: $sidebar-menu-link-open-color;

            .menu-icon {
                color: $sidebar-menu-link-open-color;
            }
        }
    }

    > li.open > a {
        background-color: #ffffff;
        color: $navbar-default-link-open;
        font-weight: 700;

        &:before,
        .menu-icon {
            color: $navbar-default-icon;
        }
    }

    > li.with-sub {
        > a:before {
            font-family: "viglobal";
            content: "\e008";
            color: $sidebar-menu-link-color;
            font-size: $sidebar-menu-font-size + 4;
            transition: all 0.15s;
        }

        > a:hover {
            background-color: transparent;
            color: $sidebar-menu-link-hover-color;

            &:before,
            .menu-icon {
                background-color: transparent;
                color: $sidebar-menu-link-hover-color;
            }

            > a:before {
                color: $sidebar-menu-link-hover-color;
            }
        }

        &.with-sub.open {
            .triangle {
                background: $sidebar-submenu-open-bg;
                box-shadow: inset 0 0 8px 1px rgba(0, 0, 0, 0.3);
                clip: rect(0px 9px 9px 0px);
                content: "";
                height: 40px;
                left: 4px;
                position: absolute;
                top: 38px;
                transform: rotate(45deg);
                width: 40px;
                z-index: 1;
            }
        }

        &.open > a {
            color: $sidebar-menu-link-hover-color;
            background-color: $site-left-sidebar-bg;
            font-weight: 700;

            .menu-icon {
                color: $sidebar-menu-link-hover-color;
            }

            &:before {
                color: $sidebar-menu-link-hover-color;
                transform: rotate(180deg);
            }

            &:hover {
                color: $sidebar-menu-link-hover-color;

                &:before,
                .menu-icon {
                    color: $sidebar-menu-link-hover-color;
                }
            }
        }
    }

    .left-sidebar-collapse.hidden-xs {
        background-image: linear-gradient(-180deg, $sidebar-submenu-open-bg 100%, #f3f3f3 100%);
        color: $sidebar-menu-link-color;
        font-size: $sidebar-menu-font-size + 1;
        height: 17px;
        line-height: 18px;
        padding: 1px 2px;
        text-align: right;

        &:hover {
            background-image: linear-gradient(-180deg, $sidebar-submenu-open-bg 100%, #ebebeb 100%);
            color: $sidebar-menu-link-hover-color;
            height: 17px;
        }

        i{
            transform:rotate(180deg);
            transition:all 0.4s;
        }
    }

    .badge-circle {
        min-width: 21px;
        min-height: 21px;
        padding: 4px 6px;
        font-size: 13px;
        font-weight: 500;
        border-radius: 13px;
        margin: 0 0 0 7px;
    }
}


.sidebar-submenu {
    background: $sidebar-submenu-open-bg;
    //box-shadow: inset 0 11px 8px -10px rgba(0, 0, 0, 0.3), inset 0 -11px 6px -10px rgba(0, 0, 0, 0.3);

    .sidebar-submenu {
        background: transparent;
        box-shadow: none;

        li :before {
            font-family: "viglobal";
            color: $sidebar-menu-link-color;
            content: '\42';
            display: block;
            float: left;
            font-size: 4px;
            line-height: 19px;
            margin: 0 2px 0 -2px;
            padding: 0;
            text-align: center;
            width: 12px;
        }
    }

    > li {
        > a {
            background-color: transparent;
            color: $sidebar-menu-link-color;
            cursor: pointer;
            display: block;
            font-size: $sidebar-menu-font-size;
            font-weight: 400;
            letter-spacing: 0.5px;
            line-height: 24px;
            padding: 5px 15px 5px 40px;
        }

        &:nth-child(2) {
            margin-top: 15px;
        }

        &:last-child {
            padding-bottom: 15px;
        }

        &:hover > a {
            background-color: transparent;
            color: $sidebar-menu-link-hover-color;
        }
    }

    > li.open {
        > a {
            background-color: transparent;
        }

        > a::before {
            transform: rotate(180deg);
        }
    }

    > li.active {
        > a {
            color: $sidebar-menu-link-open-color;
            background-color: $sidebar-menu-link-open-bg;
            font-weight: 700;

            &:hover {
                background-color: lighten($sidebar-menu-link-open-bg, 2%);
            }
        }
    }

    > li.with-sub.open {
        > a {
            background-color: transparent;

            &:first-child {
                background-color: transparent;
                color: $sidebar-menu-link-open-color;
                font-weight: 700;
            }
        }

        > a::before {
            color: $sidebar-menu-link-open-color;
            content: "\e008";
        }

        > a:hover {
            color: $sidebar-menu-link-open-color;
            font-weight: 700;
        }
    }

    > li.with-sub {
        > a::before {
            font-family: "viglobal";
            content: "\e008";
            float: right;
            color: $sidebar-menu-link-color;
            font-size: $sidebar-menu-font-size + 4;
            margin-right: 3px;
            text-align: center;
            transition: all 0.15s;
            width: 12px;
        }

        > a:hover:before {
            background-color: transparent;
            color: $sidebar-menu-link-hover-color;
        }

        > ul.sidebar-submenu {
            > li {
                > a {
                    background-color: transparent;
                    padding: 5px 15px 5px 40px;
                }

                &:last-child {
                    background-color: transparent;
                    margin-bottom: 0;
                    padding-bottom: 0;
                }

                &:nth-child(2) {
                    margin-top: 0;
                }

                a:hover {
                    background-color: transparent;
                }
            }

            > li.active {
                > a {
                    color: $navbar-active;

                    &:hover {
                        color: $navbar-default-link-hover;
                    }
                }
            }
        }
    }
}

// Menu collapsed
.layout-left-sidebar-collapsed {
	.menu-icon {
        padding-top: 5px;

		.vi {
				line-height: 28px;
		}
	}

    .triangle {
        display: none;
    }

    .left-sidebar-collapse.hidden-xs {
        height: 17px;
        line-height: 14px;
        line-height: 17px;
        text-align: center;

        &:hover {
            height: 17px;
            line-height: 14px;
            line-height: 17px;
            text-align: center;
        }
    }

    .site-left-sidebar {
        .sidebar-menu .with-sub > ul > li  {
            //display: none;
        }

        .sidebar-menu .open > ul > li  {
            display: inline !important;
        }

        .sidebar-menu .with-sub .sidebar-submenu .with-sub {
            .menu-text {
                display: inline;
                overflow: hidden;
            }
            .in {
                display: inline !important;
                position: relative;
                left: 0px;
            }
            .in .menu-subtitle {
                display: none !important;
            }
        }
    }

    .sidebar-submenu {
        > li:nth-child(1) {
            height: 46px !important;
        }

        > li:nth-child(2) {
            margin-top: 1px !important;
        }

        > li.with-sub > ul.sidebar-submenu > li > a {
            padding-left: 15px;
        }
    }

    .sidebar-menu {
        margin-top: 0;

        hr {
            margin: 1px 0;
        }

        .menu-icon {
            margin-top: -6px;
        }

        > li {
            .menu-icon {
                color: $sidebar-menu-link-color;
            }
        }

        > li {
            &:hover > a {
                background-color: $sidebar-submenu-open-bg;
                border: none;

                &:hover .menu-icon {
                    color: $sidebar-menu-link-hover-color;
                }
            }

            &.open > a {
                border: none;
            }

            &.recently-viewed {
                display: none;
            }

            > a {
                border-left: none;
            }
        }

        > li:hover > a {
            background-color: $sidebar-submenu-open-bg;
            border: none;
        }

        > li:first-child {
            padding-bottom: 5px;
        }

        > li.with-sub.open > a {
            font-weight: 400;
        }

        .menu-subtitle {
            background-color: $sidebar-submenu-open-bg;
            margin-left: -1px;
        }

        .left-sidebar-collapse.hidden-xs {
            i{
                transform:rotate(0);
                transition:all 0.4s;
            }
        }
    }
}
