/* Icons */
[class^="icon-"] a:before,
[class*=" icon-"] a:before,
[class^="icon-"] a:after,
[class*=" icon-"] a:after,
i[class^="icon-"],
i[class*=" icon-"] {
  font-family: "#{$icons}";
  position:absolute;
  text-decoration:none;
  zoom: 1;
}

i[class^="icon-"],
i[class*=" icon-"] {
  display: inline-block;
  position: static;
  min-width: 20px;
  margin: 0 5px;
  text-align: center;
}

i { font: inherit; }

.btn > i[class^="icon-"], i[class*=" icon-"] { 
    margin: 0 5px 0 5px; 
}

.glyphicon { display: inline-block; font: normal normal normal 16px/1 'viglobal'; font-size: inherit; text-rendering: auto; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.glyphicon-time:before { content: '\3e'; }
.glyphicon-calendar:before { content: '\73'; }
.glyphicon-chevron-up:before { content: '\e008'; }
.glyphicon-chevron-down:before { content: '\7a'; }
.glyphicon-chevron-left:before { content: '\e009'; }
.glyphicon-chevron-right:before { content: '\79'; }



.vi {
	display: inline-block;
	font: normal normal normal 14px/1 "viglobal";
	font-size: inherit;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
.vi-hc-lg {
  font-size: 1.33333333em;
  line-height: 0.75em;
}
.vi-hc-2x {
  font-size: 2em;
}
.vi-hc-3x {
  font-size: 3em;
}
.vi-hc-4x {
  font-size: 4em;
}
.vi-hc-5x {
  font-size: 5em;
}
.vi-hc-fw {
  width: 1.28571429em;
  text-align: center;
}
.vi-hc-ul {
  padding-left: 0;
  margin-left: 2.14285714em;
  list-style-type: none;
}
.vi-hc-ul > li {
  position: relative;
}
.vi-hc-li {
  position: absolute;
  left: -2.14285714em;
  width: 2.14285714em;
  top: 0.14285714em;
  text-align: center;
}
.vi-hc-li.vi-hc-lg {
  left: -1.85714286em;
}
.vi-hc-border {
  padding: .1em .25em;
  border: solid 0.1em #9e9e9e;
  border-radius: 2px;
}
.vi-hc-border-circle {
  padding: .1em .25em;
  border: solid 0.1em #9e9e9e;
  border-radius: 50%;
}
.vi.pull-left {
  float: left;
  margin-right: .15em;
}
.vi.pull-right {
  float: right;
  margin-left: .15em;
}
.vi-hc-spin {
  -webkit-animation: vi-spin 1.5s infinite linear;
          animation: vi-spin 1.5s infinite linear;
}
.vi-hc-spin-reverse {
  -webkit-animation: vi-spin-reverse 1.5s infinite linear;
          animation: vi-spin-reverse 1.5s infinite linear;
}
@-webkit-keyframes vi-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}
@keyframes vi-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}
@-webkit-keyframes vi-spin-reverse {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(-359deg);
            transform: rotate(-359deg);
  }
}
@keyframes vi-spin-reverse {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(-359deg);
            transform: rotate(-359deg);
  }
}
.vi-hc-rotate-90 {
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
}
.vi-hc-rotate-180 {
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
}
.vi-hc-rotate-270 {
  -webkit-transform: rotate(270deg);
      -ms-transform: rotate(270deg);
          transform: rotate(270deg);
}
.vi-hc-flip-horizontal {
  -webkit-transform: scale(-1, 1);
      -ms-transform: scale(-1, 1);
          transform: scale(-1, 1);
}
.vi-hc-flip-vertical {
  -webkit-transform: scale(1, -1);
      -ms-transform: scale(1, -1);
          transform: scale(1, -1);
}
.vi-hc-stack {
  position: relative;
  display: inline-block;
  width: 2em;
  height: 2em;
  line-height: 2em;
  vertical-align: middle;
}
.vi-hc-stack-1x,
.vi-hc-stack-2x {
  position: absolute;
  left: 0;
  width: 100%;
  text-align: center;
}
.vi-hc-stack-1x {
  line-height: inherit;
}
.vi-hc-stack-2x {
  font-size: 2em;
}
.vi-hc-inverse {
  color: #ffffff;
}

