	}

	.page-filter {
		border-radius: 0;
		background-color: $timeline-default-color;
		box-shadow: inset 0px 11px 5px -10px rgba(0,0,0,0.15), inset 0px -11px 5px -10px rgba(0,0,0,0.15);
		color: black;
		border-radius: 3px;

		.filter-icon {
			background-color: $timeline-darker-color;
			box-shadow: inset 0px 11px 5px -10px rgba(0,0,0,0.15), inset 0px -11px 5px -10px rgba(0,0,0,0.15);
			color: $timeline-default-color;
		}

