				color: #333;
			}
		}
	}

	.panel-group {
		.panel {
			border: 0px;
			border-top: 1px solid #dddddd !important;
			box-shadow: none;

			.panel-title {
				color: $panel-title-color;
				font-size: 16px;
				font-weight: $headings-font-weight;
				line-height: 25px;
				font-size: 16px;

