				padding-top: 4px;
				padding-left: 4px;
			}

			.vi {
				padding: 5px;
				vertical-align: -4px;
				font-size: 20px;
			}
		}

		.panel-body {
			&:first-child > ul.nav {
				margin-top: -26px;
				margin-left: -20px;

				@media screen and (max-width: $screen-sm) {
					margin-left: 0;
