				margin: 0;
			}
		}
	}

	.panel-options-container {
		@include clearfix;
		margin-top: 10px;

		.panel-option {
			float: left !important;
			width: auto;
			padding: 0 30px 15px 0;

			@media (min-width: $screen-md) {
