				width: auto;
				padding-right: 0;

				@media (max-width: $screen-md) {
					float: left !important;
					text-align: left;
				}
			}
		}

		@media (max-width: $screen-sm) {
			.panel-option {
				padding-right: 0;
			}
		}
	}

	.row {
		margin-left: 0px;
		margin-right: 0px;
	}
