				font-family: "roboto condensed";
				content: "txt";
				display: block;
				font-size: 9px;
				text-transform: uppercase;
				position: absolute;
				top: 26px;
				left: 20px;
				font-weight: normal;
				color: #fff;
				letter-spacing: 0.5px;
				pointer-events: none;
			}
		}

		.document-name {
			vertical-align: middle;
			font-size: $font-size-small;
			line-height: 14px;
			display: inline-block;
			width: 80%;
			position: relative;
			top: -12px;
			padding-left: 5px;
		}
	}

	.item-info {
		min-width: 250px;
		position: relative;
		height: 100%;
		margin: 0 -6px;
		line-height: 20px;

		.info-header {
			display: block;
			padding: 12px 12px 4px 12px;
			color: #333;

			&.nowrap {
				overflow-x: hidden;
				margin-right: 10px;
			}
		}

		.info-subtitle {
			padding: 0 12px;
			font-size: $font-size-xxsmall;
			opacity: 0.6;

			span {
				&:empty {
					display: none;
				}

				&:not(:last-child) {
					padding-right: 10px;
				}
			}

			> span:not(:first-child) {
				padding-left: 0px;
			}
		}

		.info-details {
			font-size: $font-size-xsmall;
			padding: 10px 12px 20px 12px;
			display: inline-block;
			margin-bottom: 28px;

			.info-details-row {
				display: table;

				i {
					padding-right: 5px;
				}

				> span {
					display: table-cell;

					a {
						display: inline-block;
						margin: -2px 0 0 0;
						padding: 0px 8px;
						vertical-align: middle;
						color: #007dc6;

						&:hover {
							transform: scale(1.2);
						}
					}
				}

				&:before {
					font-size: $font-size-xsmall;
					min-width: 16px;
				}

				.link {
					display: inline-block;
					vertical-align: middle;
				}

				.numeric {
					display: inline-block;
					position: relative;
					min-width: 38px;
					padding: 1px 3px 0px 5px;
					margin: 0 8px 1px 0;
					border-top-left-radius: 2px;
					border-bottom-left-radius: 2px;
					text-align: left;
					background-color: #acacac;
					color: #fff;
					font-weight: 600;
					text-align: left;

					&:after {
						content: " ";
						display: block;
						width: 0;
						height: 0;
						border-top: 9px solid transparent;
						border-bottom: 9px solid transparent;
						border-left: 5px solid #acacac;
						position: absolute;
						top: 50%;
						margin-top: -9px;
						left: 100%;
					}
				}
			}
		}

		.no-footer {
			margin-bottom: 0;
		}

		.label-warning:not(:empty)::before {
			content: none;
		}

		.label-danger:not(:empty)::before {
			content: none;
		}

		.label-primary:not(:empty)::before {
			content: none;
		}

		.label-success:not(:empty)::before {
			content: none;
		}

		.label-info:not(:empty)::before {
			content: none;
		}

		.info-footer {
			position: absolute;
			display: flex;
			font-size: $font-size-xsmall;
			padding: 8px 12px;
			bottom: 0;
			left: 0;
			background: #fff;
			width: 100%;
			border-radius: 0 0 4px 4px;

			a {
				color: #007dc6;
				display: inline-block;

				&:hover {
					color: #135fa1;
				}

				&:not(:first-child) {
					padding-left: 15px;
				}

				> i {
					font-size: $font-size-xsmall + 1;
					vertical-align: middle;
					display: inline-block;
					margin: -1px 0 0 -1px;
					opacity: 0.5;
				}
			}

			div:not(:first-child) {
				padding-left: 15px;
			}
		}
	}

	.btn-ico-table {
		margin-top: -3px;
		width: 22px;
		height: 22px;
		font-size: 12px;
	}

	.vi {
		vertical-align: -15%;
	}
}

td {
	.inline-repeater-item {
		.item-info {
			padding: 0 0 6px 0;

			.info-details {
				padding-top: 0;
			}

			.info-details {
				margin-bottom: 2px;
			}

			.info-footer {
