//

.form-design {
	margin-top: 20px;

	.form-container {
		min-height: 100px;
		background-color: #fff;
		margin-left: -10px;
		margin-top: -15px;

		.form-section {
			div {
				margin-top: 10px;
			}

			.row-draggable-preview {
				margin-top: -25px !important;
			}
		}

		.form-help {
			border-radius: 2px;
			border: 5px #eee dotted !important;
			opacity: 1;
			margin-top: -18px;
			padding: 20px;
			width: 100%;
			min-height: 50px;
		}

		.form-help-preview {
			display: none;
		}

		.section-draggable {
			border: 2px dotted #cccccc;
			border-left: solid 4px #007dc6;

			&:hover {
				background-color: rgba(0, 125, 198, 0.05);
				cursor: -webkit-grab;
				cursor: -moz-grab;
				cursor: -o-grab;
				cursor: grab;
				cursor: -ms-grab;

				.section-delete {
					opacity: initial;
				}
			}

			> .section-delete {
				float: right;
				opacity: 0;
				display: block !important;
				position: relative;
				transition: opacity 0.3s;
				top: -10px;
				right: -10px;
				height: 20px;
				width: 20px;
				margin: auto;

				.btn > i[class^="icon-"] {
					margin: 0;
					padding: 0;
					display: block;
					position: relative;
					background: white;
				}

				.btn-ico-round {
					width: 22px;
					height: 22px;
					font-size: 20px;
					line-height: 20px;
					opacity: 1;
				}

				> a {
					transform: scale(0.7);
					margin-top: 0;
					margin-right: 0;
					padding: 0;
					display: block;
					top: 0;
					right: 0;
					height: 20px;
					width: 20px;
					margin: 0;
					transition: all 0.2s;

					&:hover {
						zoom: normal;
						transform: scale(1);
						margin-top: 0;
						margin-right: 0;
						display: block;
						box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.2);
						-webkit-transform: translateZ(0);
						transform: translateZ(0);
					}
				}
			}

			> .control-draggable {
				border-width: 0px;
			}
		}

		.section-help {
			min-height: 50px;
			width: 100%;
		}

		.section-help-preview {
			display: none;
		}

		.row-draggable {
			border: 1px dotted #cccccc;
			padding-right: 10px;
			margin-bottom: 10px !important;

			&:hover {
				background-color: darken(#fff, 1%);

				.row-delete {
					opacity: initial;
				}
			}

			> .col {
				margin-top: -10px !important;
			}

			> .row-delete {
				opacity: 0;
				display: block;
				position: relative;
				transition: opacity 0.3s;
				height: 20px;
				width: 100%;
				margin: 0;
				padding: 0;
				top: -10px;
				right: -22px;

				.btn > i[class^="icon-"] {
					margin: 0;
					padding: 0;
					display: block;
					position: relative;
					background: white;
				}

				.btn-ico-round {
					width: 22px;
					height: 22px;
					font-size: 20px;
					line-height: 20px;
					opacity: 1;
				}

				> a {
					transform: scale(0.7);
					margin-top: 0;
					margin-right: 0;
					padding: 0;
					display: block;
					top: 0;
					right: 0;
					height: 20px;
					width: 20px;
					margin: 0;
					transition: all 0.2s;

					&:hover {
						zoom: normal;
						transform: scale(1);
						margin-top: 0;
						margin-right: 0;
						display: block;
						box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.2);
					}
				}
			}
		}

		.col-droppable {
			border-right: none !important;
		}

		.control-draggable {
			border: 1px solid transparent;
			background-color: transparent;
			padding-left: 10px;
			padding-bottom: 10px;
			margin: 10px 0px 10px 10px;

			&:hover {
				border: 1px dotted #cccccc;
				background-color: #fff;

				> .btn-control-edit, > .btn-control-delete {
					opacity: initial;
				}
			}

			> .btn-control-edit, > .btn-control-delete {
				zoom: normal;
				transform: scale(0.7);
				opacity: 0;

				&.btn-ico-round {
					width: 22px !important;
					height: 22px;
					border-radius: 0;
					font-size: 20px;
					font-weight: normal;
					line-height: 20px;
					padding: 0;
					margin: 0 -5px 0 5px;
					transition: all 0.2s;

					&:hover {
						transform: scale(1);
						-webkit-transform: translateZ(0);
						transform: translateZ(0);
					}
				}
			}
		}

		.control-help {
			width: 100%;

			> div {
				margin: 0px;
				padding: 0px;
				border: 2px dotted #cccccc;
				min-height: 65px;
				margin: 10px 0 10px 10px;
				padding-left: 10px;
			}
		}

		.control-help-preview {
			display: none;
		}

		&.ui-droppable-active {
			background-color: #E5F2F9 !important;
			border: 5px #fff solid !important;
			opacity: 0.5;
			color: #7F9EB0;

			.form-help {
				&:after {
					content: "+";
					font-size: 50px;
					font-weight: 600;
					color: #007DC6;
					padding: 7px 46px;
					border: 2px #007DC6 dashed !important;
					border-radius: 2px;
					background-color: rgba(0, 125, 198, 0.15);
					height: 140px;
					width: 100%;
					display: inline-block;
					line-height: 120px;
