		}

		.toolbox-items {
			div:not(:last-child) {
				padding-right: 5px;
			}

			.toolbox-item-content {
				margin-bottom: 5px;
				border-radius: 2px;
				background-color: #f6f6f6;

				.toolbox-control {
					padding: 10px 10px 5px 10px;
					margin-top: 0;

					.form-section {
						border: 2px dotted #cccccc;
						border-left: solid 4px #007dc6;
						height: 25px;
						margin-left: -10px;
					}

					.col-droppable:last-of-type {
						border-right: 1px dotted #aaa !important;
						border-top-right-radius: 2px;
						border-bottom-right-radius: 2px;
						cursor: -webkit-grab;
						cursor: -moz-grab;
						cursor: -o-grab;
						cursor: grab;
						cursor: -ms-grab;
						pointer-events: none;
					}

					.col-droppable {
						border-top: 1px dotted #aaa !important;
						border-left: 1px dotted #aaa !important;
						border-bottom: 1px dotted #aaa !important;
						border-radius: 2px;
						cursor: -webkit-grab;
						cursor: -moz-grab;
						cursor: -o-grab;
						cursor: grab;
						cursor: -ms-grab;
						pointer-events: none;
						background-color: #fff !important;
						min-height: 25px;
						margin-bottom: 5px;
					}
				}

				.form-group {
					margin: 0 !important;
					padding: 0 !important;
				}

				.toolbox-image {
					width: 100%;
					text-align: center;
					padding: 0 !important;
					opacity: 0.4;
				}

				.toolbox-text {
					width: 100%;
					text-align: center;
					font-family: "Roboto Condensed";
					font-size: $font-size-small;
					font-stretch: condensed;
					overflow-x: hidden;
					text-overflow: clip;
					white-space: nowrap;
					padding-top: 0px;
					margin-top: -5px;
				}
			}
		}

		.row-draggable {
			margin: 0 !important;
		}

		.row-draggable:hover {
			background-color: darken(#ffffff, 3%);
			cursor: -webkit-grab;
			cursor: -moz-grab;
			cursor: -o-grab;
			cursor: grab;
			cursor: -ms-grab;

			> .col-droppable {
				cursor: -webkit-grab;
				cursor: -moz-grab;
				cursor: -o-grab;
				cursor: grab;
				cursor: -ms-grab;
			}
		}

		.control-draggable:hover {
			background-color: #f7f7f7;
			cursor: -webkit-grab;
			cursor: -moz-grab;
			cursor: -o-grab;
			cursor: grab;
			cursor: -ms-grab;
		}

		.control-label {
			text-align: left;
		}

		.ui-draggable {
			border-radius: 2px;
			border: 1px solid #eee;

			&:active {
				background-color: #fff;
				background-image: repeating-linear-gradient(-45deg, rgba(238, 238, 238, 0.4), rgba(238, 238, 238, 0.4) 1px, transparent 1px, transparent 6px);
				cursor: -webkit-grabbing;
				cursor: -moz-grabbing;
				cursor: -o-grabbing;
				cursor: grabbing;
				cursor: -ms-grabbing;

				.toolbox-image {
					opacity: 0.4;
					transform: scale(1.2, 1.2);
				}

				.toolbox-text {
					opacity: 1;
				}
			}

			&:hover {
				background-color: #E5F2F9;
				border: 1px dashed rgba(0, 125, 198, 0.5);

				.toolbox-image {
					opacity: 0.8;
					transform: scale(1.2, 1.2);
				}
			}
		}

		.ui-draggable-dragging {
			min-width: 100px;
			background-color: darken(#ffffff, 3%);
			cursor: -webkit-grabbing;
			cursor: -moz-grabbing;
			cursor: -o-grabbing;
			cursor: grabbing;
			cursor: -ms-grabbing;
			z-index: 1000;
			box-shadow: 0 6px 15px 0 rgba(0, 0, 0, 0.18);
			transform: translateY(-5px);

			.toolbox-image {
				opacity: 0.8;
				transform: scale(1.2, 1.2);
			}
		}
	}

	.section-draggable {
		border-left: 0px;
		cursor: -webkit-grab;
	}

	.row-draggable {
		margin: 10px 10px 0 0 !important;
		cursor: -webkit-grab;
		cursor: -moz-grab;
		cursor: -o-grab;
		cursor: grab;
		cursor: -ms-grab;
	}

	.col-droppable {
		background-color: transparent !important;
		cursor: default;
		min-height: 35px;

		&:last-of-type {
			border-right-width: 0 !important;
		}
	}

	.control-draggable {
		cursor: -webkit-grab;
		z-index: 9999;
	}

	.preview {
		border: 0px solid #fff;
	}
}

