    div.actionItemDate {
        width: 200px;
    }

    div.actionItemStatus {
		min-width: 150px;

        i.vi-check {
            color: $brand-success;
            font-weight: bold;
            padding: 0 0.25em;
        }
        i.vi-alert-circle {
            color: $brand-danger;
            font-weight: bold;
            padding: 0 0.25em;
        }
    }

    div.actionItemCounts {
        .actionItemCount {
            background-color: black;
            color: white;
            border-radius: 15px;
            height: 30px;
            width: 30px;
            display: inline-block;
            padding-left: 9px;
            padding-top: 3px;
        }
    }

    div.actionItemActionLinks {
        border-right: none;
        float:right;
        line-height: 40px;

      
        button, input[type=submit] {
            height: 26px;
            background-color: #f3f3f3;
            color: #666666;
            font-size: 11px;
            border: solid 1px #979797;
            border-radius: 13px;
            line-height: 12px;
            margin: 0 5px;

			&:last-child {
				margin-right: 0;
			}
        }
    }

    div.actionItemEmails {
        clear: both;
        float: none;
        padding:3px 15px;
        height: 30px;
        border-radius: 15px;
        background-color: #ffffff;
        border: solid 1px #dfdfdf;
        margin: 0 15px;
        position: relative;
        top: 7px;
        overflow:hidden;

        @media (max-width: $screen-md) {
            float: none;
            border-radius: 5px;
            height: inherit;
            padding: 10px 15px;
        }

        &.actionItemEmailsTwoFields {

            width: 49%;
            float: left;
            margin: 0 0.5%;

            &.secondField {
                clear: none;
                float: right;

                @media (max-width: $screen-md) {
                    float: none;

                }
            }

            @media (max-width: $screen-md) {
                width: inherit;
                margin: 0 15px;
                float: none;
                margin-bottom: 7px;
            }
        }

        text-transform: uppercase;

        span {
            font-size: 11px;
        }

        > span {
            margin:0 10px;
        }

        .actionItemEmailName {
            font-weight: bold;

            @media (max-width: $screen-md) {
                display: block;
            }
        }

        i.vi-check-circle {color: #58b957}
        i.vi-close-circle {color: #f5a623}
        i.vi-alert-circle {color: #b82217}

        .actionItemEmailStatusNumber {
            color: #888888;
        }
    }
}

//
// 2 sortable/drag&drop listboxes
//
.connectedSortable li {
    border-left: 0 !important;
    border-right: 0 !important;
}

.connectedSortable li:first-child {
    border-top: 0 !important;
}

.connectedSortable li.ui-sortable-helper {
    border: 1px solid #dddddd;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.1);
    border-radius: 4px;
}

.connectedSortable li.ui-sortable-placeholder {
    visibility: visible !important;
    border: #ccc 1px dashed;
    margin: 5px auto;
    padding: 3px 5px;
    background: #fff;
    width: 95% !important;
    border-radius: 4px;
}

//
// Add Remove Items
//

.add-remove-container {
	display: flex;
	height: 100%;

	@media screen and (max-width: $screen-sm) {
		display: inline-block;
	}

	.add-remove-list {
		align-self: baseline;
		width: 40%;

		@media screen and (max-width: $screen-sm) {
			width: 100%;
		}
	}

	.add-remove-buttons {
		align-self: center;
		width: 120px;
		padding-right: 15px;

		@media screen and (max-width: $screen-sm) {
			width: 100%;
			margin-top: 20px;
		}

		> input {
			width: 100%;
			margin-bottom: 2px;

			&.btn-danger {
				margin-top: 8px;
			}
		}
	}
}
