.slogan-container {
    position: relative;
    background-position: center center;
    height: 280px;
    width: 100%;
//    margin-top: -25px;
    box-sizing: content-box;
    > .event-panel {
        position: absolute;
        bottom: 0;
        right: 0;
        width: 450px;
        height: 220px;
        border-radius: 7px 0 0 0;
        margin: 0;
    }
}
.bullentin-position-holder {
    margin-top: 0;
}
.banner-slogan {
    margin: 0;
    height: 280px;
    width: 100%;
    background-image: -webkit-gradient(linear, 0% 0%, 100% 100%,  from(rgba(0,0,0,1)), color-stop(0, rgb(0,0,0)), to(rgba(0,0,0,0)));
    background-image: linear-gradient(to right, rgba(0,0,0,1), rgba(0,0,0,0));
}

.custom-banner {
	padding: 62px 0 50px 30px;
	height: 50px;
	width: 500px;

	> span {
		font-size: 24px;
		font-weight: 600;
		width: 500px;
		color: #fff;
		margin: 15px 0 0 0;
		display: inline-block;
	}

	> img {
		height: 50px;
	}
}  
.custom-slogan {
    margin: 45px 0 0 30px;
    padding-left: 15px;
    border-left: 1px solid #d01818;
    width: 500px;
    height: 90px;
    overflow: hidden;
    > h3 {
        margin: 0 0 5px 0;
        font-size: 22px;
        font-weight: 400;
        color: #fff;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;  
    }
    > p {
        font-size: 11px;
        line-height: 1px;
        color: rgba(255,255,255,0.7);  
        display: inline;
    }
}
.event-bulletin {
    .slick-dots {
      bottom: 3px;
    }
    .slick-carousel-caption {
    padding: 0 25px 0 25px;
    height: 110px;
    }
    .slick-carousel-control {
    top: 0;
    bottom: 3px;
        .glyphicon-chevron-left, .glyphicon-chevron-right {
            top: 43px;
        }
    }
}

@media (max-width: $screen-lg) {
    .slogan-container {
        //margin-top: 0;
        
		> .event-panel {
            position: relative;
            width: 100%;
            height: 215px;
            padding: 5px 0 0 0;
        }
    }
    .event-bulletin {
        .slick-dots {
          bottom: -3px;
        }
    }
    .bullentin-position-holder {
        margin-top: 235px;
    }
}
@media (max-width: $screen-note) {
    .slogan-container {
        height: 250px;
    }
    .banner-slogan {
        height: 250px;
    }
    .custom-banner {
        padding: 20px 0 0 13px;
        width: 350px;
        > h2 {
            width: 345px;
        }
    }
    .custom-slogan {
        margin: 45px 0 0 17px;
        width: 320px;
        height: 135px;
        overflow: hidden;
    }  
    .custom-slogan-title {
        white-space: normal;
        overflow: hidden;
        text-overflow: ellipsis;
        max-height: 50px;
    }
    .event-bulletin .slick-carousel-caption {
        padding: 5px 20px 0 20px;
        height: 120px;
    }
}
@media (max-width: $screen-xtn) {
    .custom-banner {
        padding: 10px 0 0 1px;
        width: 340px;
    }
    .custom-slogan {
        margin: 35px 0 0 2px;
        width: 320px;
        height: 145px;
        overflow: hidden;
    } 
}