
$breadcrumb-bg: rgb(240, 240, 240);
$breadcrumb-item-bg: rgb(160, 150, 140);
$breadcrumb-item-color: #f0f0f0;
$breadcrumb-item-hover-bg: darken(rgb(160, 150, 140), 4%);
$breadcrumb-item-hover-color: #fff;
$breadcrumb-back-bg: rgb(228, 154, 28);

.breadcrumb-container {
    background-color: $breadcrumb-bg;
    height: 25px;
    margin-top: 0;
    overflow: hidden;

    .btn-breadcrumb {
        .btn-default {
            display: inline-block;
            margin-top: 0px;
            padding: 2px 10px 2px 21px !important;
            border-top: 1px solid $breadcrumb-item-bg !important;
            border-bottom: 1px solid $breadcrumb-item-bg !important;
            background-color: $breadcrumb-item-bg;
            color: $breadcrumb-item-color;
            font-size: $font-size-small;

            &:first-child {
                padding: 6px 6px 6px 10px;
            }

            &:last-child {
                padding: 6px 18px 6px 24px;
                border-right: 0px solid transparent;
                background-color: $breadcrumb-bg;
                border-top: 1px solid $breadcrumb-bg !important;
                border-bottom: 1px solid $breadcrumb-bg !important;
                color: rgba(0,0,0,0.5);
            }

            &:hover {
                border-top: 1px solid $breadcrumb-bg;
                color: $breadcrumb-item-hover-color !important;
            }

            &:not(:last-child):after {
                content: " ";
                display: block;
                width: 0;
                height: 0;
                border-top: 17px solid transparent;
                border-bottom: 17px solid transparent;
                border-left: 10px solid $breadcrumb-item-bg;
                position: absolute;
                top: 50%;
                margin-top: -17px;
                left: 100%;
                z-index: 3;
            }

            &:not(:last-child):before {
                content: " ";
                display: block;
                width: 0;
                height: 0;
                border-top: 17px solid transparent;
                border-bottom: 17px solid transparent;
                border-left: 10px solid $breadcrumb-item-bg;
                position: absolute;
                top: 50%;
                margin-top: -17px;
                margin-left: 3px;
                left: 100%;
                z-index: 3;
            }

            &:not(:last-child) {
                &:after {
                    border-left: 10px solid $breadcrumb-item-bg;
                }

                &:before {
                    border-left: 10px solid #fff;
                }
            }

            &:hover:not(:last-child) {
                background-color: $breadcrumb-item-hover-bg;

                &:after {
                    border-left: 10px solid $breadcrumb-item-hover-bg;
                }

                &:before {
                    border-left: 10px solid #fff;
                }
            }
        }

        .btn-apps {
            background-color: #000;
            border-left: 1px solid $breadcrumb-bg;
            color: darken($breadcrumb-item-color, 10%);
            border-right: 0px;
            padding-left: 9px;
            padding-top: 3px;
            width: 28px;
            margin-top: 0px;

            @media (max-width: $screen-sm) {
                border-left: none;
                display: none !important;
            }

            i {
                margin-left: -5px;
            }

            &:hover {
                color: #ffffff;
                width: 27px !important;
                margin-right: 1px;
            }
        }

        .btn-back {
            width: 60px;
            background-color: $breadcrumb-back-bg;
            border-top: 1px solid $breadcrumb-back-bg !important;
            border-bottom: 1px solid $breadcrumb-back-bg !important;
            border-left: 0px;
            padding-left: 27px !important;
            color: rgba(255,255,255,0.9);

            @media (max-width: $screen-sm) {
                width: 54px;
                padding-left: 18px !important;
            }

            i {
                margin-left: -19px;
                line-height: 19px;
                padding-right: 5px;
                padding-top: 0px;
                float: left;

                @media (max-width: $screen-sm) {
                    display: none;
                }
            }

            &:after {
                border-left: 10px solid $breadcrumb-back-bg !important;
            }

            &:hover:not(:last-child) {
                background-color: darken($breadcrumb-back-bg, 2%) !important;

                &:after {
                    border-left: 10px solid darken($breadcrumb-back-bg, 2%) !important;
                }
            }
        }

        .btn-active {
            pointer-events: none;
            cursor: default;
        }
    }
    /* The responsive part */
    .btn-breadcrumb > * > div {
        /* With less: .text-overflow(); */
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .btn-breadcrumb > *:nth-child(n+3) {
        display: none;
    }
    /* === For phones =================================== */
    @media (max-width: 767px) {
        .btn-breadcrumb > *:nth-last-child(-n+3) {
            display: block;
        }

        .btn-breadcrumb > * div {
            max-width: 60px;
        }
    }
    /* === For tablets ================================== */
    @media (min-width: 768px) and (max-width:991px) {
        .btn-breadcrumb > *:nth-last-child(-n+4) {
            display: block;
        }

        .btn-breadcrumb > * div {
            max-width: 100px;
        }
    }
    /* === For desktops ================================== */
    @media (min-width: 992px) {
        .btn-breadcrumb > *:nth-last-child(-n+6) {
            display: block;
        }

        .btn-breadcrumb > * div {
            max-width: 170px;
        }
    }
}
/* === breadcrumbs scroll ================================== */
@media (max-width: $screen-xs) {
    .btn-group > .btn, .btn-group-vertical > .btn {
        float: none;
    }

    .breadcrumb-container .btn-breadcrumb a {
        display: inline-block !important;
    }

    .btn-group, .btn-group-vertical {
        white-space: nowrap;
        padding-bottom: 30px;
    }

    .breadcrumb-container {
        overflow-x: auto;
        overflow-y: hidden;
    }

    .breadcrumb-container::-webkit-scrollbar {
        display: none;
    }
}
