.badge {
    &.badge-success {
      @include alert-variant($alert-success-bg, $alert-success-border, $alert-success-text);
    }

    &.badge-primary {
      @include alert-variant($brand-primary, $brand-primary, #ffffff);
    }

    &.badge-info {
      @include alert-variant($alert-info-bg, $alert-info-border, $alert-info-text);
    }

    &.badge-warning {
      @include alert-variant($alert-warning-bg, $alert-warning-border, $alert-warning-text);
    }

    &.badge-danger {
      @include alert-variant($alert-danger-bg, $alert-danger-border, $alert-danger-text);
    }
}

.badge:empty {
	display: none;
}


.badge-circle {
	display: inline;
	min-width: 17px;
	padding: 4px 9px;
	font-size: 15px;
	font-weight: 600;
	color: white;
	line-height: 1;
	vertical-align: middle;
	white-space: nowrap;
	text-align: center;
	border-radius: 17px;

	&.badge-success {
		background-color: $brand-success;
	}

	&.badge-primary {
		background-color: $brand-primary;
	}

	&.badge-warning {
		background-color: $brand-warning;
	}

	&.badge-danger {
		background-color: $brand-danger;
	}

	&.badge-info {
		background-color: $brand-info;
	}
}

.badge-rect {
    padding: 4px 9px;
    margin: 5px;
    font-size: 15px;
    color: white;
    line-height: 1;
    vertical-align: middle;
    white-space: nowrap;
    text-align: center;
    border-radius: 3px;

    &.badge-danger {
      background-color: $brand-danger;
    }
}