
#horizontal-menu {
    .level {
        font-size: 12px;
        padding-bottom: 0;
        letter-spacing: 0.5px;
        opacity: 0.7;
    }

    .period {
        font-size: 14px;
        opacity: 0.7;
    }

    .more > a {
        opacity: 0.7;
    }


    > ul {
        list-style-type: none;
        margin: -20px 0 0 0;
        padding: 0;
        height: 74px;
        font-size: 0;
        text-align: left;
        position: relative;
        bottom: -16px;
        z-index: 100;

        @media screen and (max-width: $screen-sm) {
            margin-left: 10px;
        }

        > li {
            display: inline-block;
            position: relative;

            a {
                font-size: 14px;
                display: block;
                color: #333;
                text-align: left;
                text-decoration: none;
                padding: 5px 0;
                margin: 10px 30px 10px 0;

                @media screen and (max-width: $screen-sm) {
                    padding-bottom: 0;
                }

                .level,
                .period {
                    transition: transform 0.2s;
                }

                + ul {
                    display: none;
                    position: absolute;
                    top: 100%;
                    right: 0;
                    margin-right: 0;
                    box-shadow: 0 5px 15px 0 rgba(0,0,0,0.25);
                    padding: 0 0 5px;
                    border-radius: 4px;
                    margin: 0;
                    background: #fff;

                    li {
                        margin: 0;
                        padding: 1px 0;

                        a {
                            padding: 10px;
                            text-align: left;
                            white-space: nowrap;
                            border-radius: 4px;
                            margin: 5px 5px 0;
                        }

                        a:hover {
                            background: #007DC6;
                            color: #fff;

                            .period {
                                opacity: 1;
                            }
                        }
                    }
                }

                &.active {
                    border-bottom: #007DC6 4px solid;

                    .period {
                        font-weight: bold;
                        opacity: 1;
                    }

                    .level {
                        opacity: 1;
                    }
                }
            }

            &.more {

                > a:before {
                    font-family: "viglobal" !important;
                    content: "\e06C";
                    display: block;
                    height: 18px;
                    font-size: 20px;
                    line-height: 23px;
                    margin-left: -1px;
                }

                &:hover {
                    > a {
                        opacity: 1;
                    }
                }
            }

            &:hover {
                > a {
                    border-bottom: #444 4px solid;

                    &.active {
                        border-bottom: #007DC6 4px solid;
                        cursor: default;

                        .level,
                        .period {
                            transform: translateY(0);
                        }
                    }

                    .level {
                        transform: translateY(-4px);
                    }

                    .period {
                        opacity: 1;
                        transform: translateY(-2px);
                    }
                }

                ul {
                    display: block;
                    border-radius: 4px;
                }
            }
        }
    }
}
