@media (max-width: $screen-note) {
  .slick-status-info {
    border-left: 4px solid $dashboard-success;
  }
  .slick-status-success {
    border-left: 4px solid $brand-success;
  }
  .slick-status-warning {
    border-left: 4px solid $brand-warning;
  }
  .slick-status-danger {
    border-left: 4px solid $brand-danger;
  } 
  .slick-status-primary {
    border-left: 4px solid $brand-primary;
  } 
  .slick-status-default {
    border-left: 4px solid $brand-info;
  } 
  
    .slick-carousel {
      width: 100%;
      height: 180px;
      background-color: #f2f2f2;
      border-radius: 3px;
      padding: 14px 17px 18px 17px;
      margin: 20px 10px;
    
      > a {
          height: 100%;
          justify-content: space-between;
          flex-direction: column;
          text-decoration: none;
          display:-webkit-box;
          display: -moz-box;         
          display: -ms-flexbox;  
          display: -webkit-flex;
          display: flex;  
          > h3 {
          font-size: 14px;
          font-weight: 800;
          color: #333;
          text-shadow: none;
          line-height: 1.5;
          margin: 0;
          }
          > p {
            font-size: 12px;
            margin: 0;
            height: 50px;
            overflow: hidden;
            color: #777;
          }
          > div {
            border-radius: 3px;
            background-color: $brand-primary;
            h2 {
              color: #fff;
              font-size: 28px;
              font-weight: 600;
              display: inline;
            }
            > h4 {
              color: #fff;
              height: 27px;
              font-size: 10px;
              text-transform: uppercase;
              align-items: center;
              justify-content: center;
            }
            h5, h6, span {
              font-size: 11px;
              color: rgba(255,255,255,0.5);
              margin: 0 1px 0 2px;
              display: inline-block;
            }
            h5 {
              display: block;
            }
            > div {
              background-color: #00649e;
              border-radius: 0 0 3px 3px;
              > div:first-child {
                border-right: 2px dotted rgba(255,255,255,0.4);
                padding: 0;
              }
              > div {
                flex: 1 1;
                text-align: center;
                height: 40px;
                margin: 11px 0;
                align-items: center;
                justify-content: center;
                flex-direction: column;
              }
            }
            > div, > div > div, > h4 {
              display:-webkit-box;
              display: -moz-box;         
              display: -ms-flexbox;  
              display: -webkit-flex;
              display: flex;
            }
          }  
      }
    }
}