table.roster td {
	border-bottom: 1px solid #808080;
	border-right: 1px solid #808080;
	padding: 5px 5px 5px 5px;
}

table.roster {
	border: 1px solid #808080;
	color: #222222;
	width: 100%;
}

table.roster thead {
	background: #F0F0F0;
}

table.roster tr {
	border-bottom: 1px solid #808080;
	height: 36px;
}

table.roster th {
	font-weight: bold;
	color: #222222;
	border-bottom: 1px solid #808080;
	border-right: 1px solid #808080;
	padding: 5px 5px 5px 5px;
	background: #F0F0F0;
}
