//
// general overrides
//
.tooltip-inner {
    max-width: 240px;
    border-radius: 4px;
    padding: 3px 10px;
    box-shadow: 0 3px 6px 0 rgba(0,0,0,0.15);
}

//
// timeline tooltip
//
.timeline-tooltip {       
    position: absolute;
    display: inline-block;
    margin-top: -2px;
    margin-left: -2px;
}

.timeline-tooltip-content {
    width: 200px;
    min-height: 140px;
    background-color: #ffffff;
    border: solid 1px #ddd;
    border-radius: 3px;
    position: absolute;
    box-shadow: 0px 5px 15px 0px rgba(0,0,0,0.3);
}

.timeline-tooltip .timeline-tooltip-arrow {
    width: 50px;
    height: 25px;
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    overflow: hidden;
}

.timeline-tooltip .timeline-tooltip-arrow::after {
    content: "";
    position: absolute;
    width: 20px;
    height: 20px;
    background: white;
    transform: translateX(-50%) translateY(-50%) rotate(45deg);
    top: 0;
    left: 50%;
    box-shadow: 1px 1px 20px 0px rgba(0,0,0,0.6);
}
      
.timeline-tooltip-field {
    font-size: $font-size-xsmall;
    color: #999;
    text-transform: uppercase;
    padding: 10px 13px;
}

.date-field .vi-calendar {
    margin-right: 13px;
}

.timeline-tooltip-field strong {
    color: #333;
}

.status-field-success {
    color: #fff;
    background-color:  #1aaf9b;
}
.status-field-warning {
    color: #fff;
    background-color:  $brand-warning
}
.status-field-danger {
    color: #fff;
    background-color:  $brand-danger;
}
.status-field-primary {
    color: #fff;
    background-color: $brand-primary;
}
.status-field-default {
    color: #fff;
    background-color: $text-color;
}

.content-field {
    text-transform: none;
}

.content-field p {
    color: #333;
}

//
// feedback tooltip
//
.feedback-tooltip {       
    position: absolute;
    display: inline-block;
    margin-top: -2px;
    margin-left: -2px;
	z-index: 1;
}

.feedback-tooltip-content {
    width: 220px;
    min-height: 90px;
    background-color: #ffffff;
    border: solid 1px #ddd;
    border-radius: 3px;
    position: absolute;
    box-shadow: 0px 5px 15px 0px rgba(0,0,0,0.3);
}

.feedback-tooltip-header {
	background-color: gray;
	text-transform: uppercase;
	color: #fff;
	
	> div:first-child {
		display: inline-block; 
		padding: 5px;
		opacity: 70%;
		font-size: $font-size-small;
	}
	
	> div:last-child {
		display: inline-block; 
		padding: 5px; 
		float: right; 
		width: 40px;
		background-color: $text-color; 
		font-size: $font-size-large;
		font-weight: bold;
		text-align: center;
		line-height: 20px;
	}
}

.feedback-tooltip-field {
    font-size: $font-size-xsmall;
    color: #999;
    padding: 10px 13px;
	
	div {
		white-space: nowrap;
	}

	p {
		font-size: $font-size-large;
		font-weight: bold;
		color: darken($text-color, 10%);
		line-height: 18px;
	}
}

/* Google Chart Tooltips */
div.google-visualization-tooltip { pointer-events: none }

.chart-tooltip {
    display: inline-block;
    .tooltip-header {
        background: #333333;
        color: white;
        font-size: 11px;
        text-align: center;
        padding-top:5px;
        padding-bottom: 5px;
        text-transform: uppercase;
    }

    .tooltip-count, .tooltip-label{
        padding: 2.5px 20px;
        vertical-align: middle;
        white-space: nowrap;
    }

    .tooltip-count {
        padding-right: 0;
        font-size: 15px;
        font-weight: bold;
        text-align: center;
    }

	.tooltip-percentage {
        padding-right: 0;
        font-size: 9px;        
        text-align: center;
		color: black;		
    }

    .tooltip-label {
        padding-left: 10px;
        font-size: 11px;
        line-height: 14px;
        color: #333;
        opacity:0.5;
    }
}