.application-wrapper {
    box-shadow: -5px 0 75px 0 rgba(0, 0, 0, 0.5);
}

.application-background {
    position: relative;
    min-height: 100vh;
    background: url(../images/bg-login-applications.jpg) no-repeat center center fixed;
    background-size: cover;
}

.masonry-container {
    max-width: 1000px; 
    margin: auto;
}

.application-container {
    padding-top: 100px;
    padding-bottom: 100px;
    margin: 0 auto;
    z-index: 2;
}

.application {
    border-top: 6px solid $brand-primary;
    box-shadow: 0 10px 30px 0 rgba(0,0,0,0.5);
    width: 300px;
    background-color: white;
    margin: 12.5px;
	font-family: Roboto, sans-serif;
    border-radius: 5px;
}

.application-navbar {
	position: absolute;
	top: 0;
	z-index: 1;
	width: 100%;
	padding: 0;
	box-shadow: -5px 0 75px 0 rgba(0, 0, 0, 0.5);
}

.application-navbar > .brand-text {
    line-height: 54px;
    font-size: 20px;
    font-weight: 300;
    color: white;
}

header > h5 {
    text-transform: uppercase;
    font-size: 22px;
    font-weight: 400;
    line-height: 26px;
    padding: 22px 20px;
    color: #444;
    letter-spacing:0.5px;
}

.application > .content {

}

.application > .content > a {
    border-bottom: 1px dashed #cdcdcd;
    display: inline-block;
    width: 100%;
    color: #444444;
    padding: 0.8em 20px;
    
    &:after {
        display: inline-block;
        font-family: "viglobal";
        content: "\79";
        float: right;
        font-size: 14px;
        color: #ccc;
    }
 
    &:hover {
        text-decoration: none;
        color: #00649e;
        background-color: #e9f4fa;

		&:after {
			color: #00649e;
		}
    }

	&:active {
		&:after {
			color: #00649e;
		}
	}

    &:first-child {
        border-top: 1px dashed #cdcdcd;
    }

    &:last-child {
        border-bottom: none;
    }
}

.application > .content > span {
    display: block;
    background: #f5f5f5;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 18px;
    padding: 0.45em 20px;
}

.application > .content > h6 + a {
    border-top: 1px dashed #cdcdcd;
}


.application > .content > h6 {
    font-family: 'Roboto condensed', sans-serif;
    font-size: $font-size-small;
    font-weight: 400;
    letter-spacing:2px;
    text-transform: uppercase;
    opacity: 1;
    display:inline-block;
    background-color: #00649e;
    color: white;
    padding: 8px 20px;
    border-top-right-radius: 4px;
    margin:0;
    margin-top: 22px;
}

.application > .content > hr {
    border: none;
    border-top: 1px dashed #000;
    color: #fff;
    opacity: 0.5;
    background-color: #fff;
    height: 5px;
    width: 100%;
}


.application-footer {
    position: absolute;
    bottom: 20px;
    left: 50px;
    font-size: $font-size-base;
    max-width: 450px;
    z-index: 1;


    img {
        height: 35px;
        margin-top: -25px;
    }

    span {
        display:inline-block;
        color:white;
        padding-left: 1em;
    }
}

span.lbl-process {
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 100;
    letter-spacing: 1px;
    font-size: 11px;
    opacity: 0.5;
    padding-left: 7px;
}