
.nav-tabs-horizontal {
    margin-left: 0px;
    margin-bottom: -5px;
    padding: 10px 15px 0 0px;
    border: none;
    display: flex;
    min-height: unset;


    @media (max-width: 767px) {
        justify-content: center;
    }

    @media (max-width: $screen-xs) {
        padding: 10px 15px 0 15px;
    }

    > li {
        border-top: 1px solid #f0f0f0;
        border-bottom: none;
        border-radius: 6px 6px 0 0;

        &:not([hidden]) {
            border-right: 1px solid #f0f0f0;
            border-left: 0;
        }

        > a {
            display: flex;
            width: 100%;
            justify-content: center;
            align-items: center;
            height: 40px;
            background-color: transparent;
            font-size: $font-size-base;
            border: none;
            padding: 0 14px;
            line-height: 14px;
            letter-spacing: 0.5px;
            color: #666;

            @media (max-width: $screen-md) {
                font-size: 12px;
                letter-spacing: 0px;
            }

            @media (max-width: $screen-xs) {
                font-size: 12px;
                letter-spacing: 0px;
            }

            &:hover {
                color: $nav-tabs-horizontal-link-active-color;
                border: none;
            }


            > span, > div {
                margin-left: 5px;
                margin-top: 0px;
            }

            > div {
                > div:last-child {
                    font-family: 'roboto condensed';
                    font-weight: normal;
                    font-size: 9px;
                    text-transform: uppercase;
                    opacity: 1;
                    color: #999;
                    letter-spacing: 1px;
                    display: flex;

                    > i {
                        font-size: 11px;
                        vertical-align: text-bottom;
                        padding-left: 2px;
                        padding-top: 1px;
                    }
                }
            }

            .badge {
                margin-left: 5px;
            }

            &.no-icon {
                > span,
                > div {
                    margin-left: 0px;

                    @media (max-width: $screen-xs) {
                        display: contents;
                    }
                }
            }
        }

        &.active {
            border-right: 1px solid #f0f0f0;
            border-top: 4px solid $nav-tabs-horizontal-link-active-border-color;
            margin-top: -4px;
            border-top-right-radius: 6px;
            border-top-left-radius: 6px;

            > a {
                &,
                &:hover,
                &:focus {
                    border: none;
                    background-color: $body-bg;
                    color: $nav-tabs-horizontal-link-active-color;
                    font-weight: bold;
                    letter-spacing: 0.5px;

                    > i {
                        color: $nav-tabs-horizontal-link-active-border-color;
                    }
                }

                > span,
                > div {
                    margin-left: 5px;
                    margin-top: 3px;
                }

                &.no-icon {
                    > span,
                    > div {
                        margin-left: 0px;
                    }
                }
            }
        }



        &.active + li {
            border-left: none;
        }

        i {
            opacity: 0.3;
        }

        div i {
            opacity: 1;
        }

        &.active i {
            opacity: 1;
        }

        .badge-circle {
            padding: 2px 5px 2px 6px;
            margin-left: 3px;
            vertical-align: 1px;
            font-size: 12px;
            font-weight: 500;
            font-family: roboto;
        }

        @media (max-width: 767px) {
            > a {
                > span, > div {
                    display: none;
                }
            }

            &.active {
                > a > span, > a > div {
                    display: block;
                }
            }
        }
    }

    li {
        &:first-child {
            border-left: 1px solid #f0f0f0;
        }

        &:last-child {
            border-right: 1px solid #f0f0f0;
        }
    }

    .dropdown-menu {
        margin-top: -4px;
    }

    .badge {
        display: block;
        padding: 5px;
        font-size: 14px;
        font-family: 'roboto condensed';
        min-width: 24px;
        float: right;
        border-radius: 100px;
        margin-left: 17px !important;
        margin-top: 1px;
    }
}

.panel-body {
	.nav-tabs-horizontal {
		> li {
			&.active > a {
				&,
				&:hover,
				&:focus {
					background-color: #fff;
				}
			}
		}
	}
}

.site-header{
    height:55px;
}


.nav-tabs-custom {
  border-bottom-color: $nav-tabs-custom-border-color;
  border-bottom-width: 2px;

  > li {
    margin-bottom: -2px;

    > a {
      background-color: transparent;
      border: none;
      border-bottom: 2px solid transparent;
      border-radius: 0;
      &:hover {
        background-color: transparent;
        border-bottom-color: transparent;
      }
    }

    &.active > a {
      &,
      &:hover,
      &:focus {
        background-color: transparent;
        border: none;
        border-bottom: 2px solid transparent;
        border-bottom-color: $nav-tabs-custom-link-active-border-color;
      }
    }
  }

  &.nav-tabs-justified {
    border-bottom: 0;

    > li > a {
      border-radius: 0;
    }

    .open > a {
      &,
      &:hover,
      &:focus {
        border-color: $nav-tabs-custom-link-active-border-color;
      }
    }

    > .active > a,
    > .active > a:hover,
    > .active > a:focus {
      border: none;
      border-bottom: 2px solid $nav-tabs-justified-link-border-color;
    }

    @media (min-width: $screen-sm-min) {
      > li > a {
        border-bottom: 2px solid $nav-tabs-justified-link-border-color;
        border-radius: 0;
      }
      > .active > a,
      > .active > a:hover,
      > .active > a:focus {
        border-bottom-color: $nav-tabs-custom-link-active-border-color;
      }
    }
  }

  .dropdown-menu {
    margin-top: -2px;
  }
}

