.search-filters {
    margin: 20px 20px 0 20px;

    @media (max-width: $screen-sm) {
        margin: 20px 15px 0 15px;
    }
}

.event-list {
    border-collapse: separate;
    border-spacing: 0 4px;
    width: 100%;
    position: relative;
    top: -20px;

    > tbody {
        > tr {
            &.odd td {
                background: #f7f7f7;
            }

            > td {
                border-spacing: 0;
                padding: 6px 10px 5px;

                @media (max-width: $screen-xs) {
                    padding: 8px 4px 5px 4px;
                }

                &:first-child {
                    border-left: 1px solid #f0f0f0;
                    border-top: 1px solid #f0f0f0;
                    border-bottom: 1px solid #f0f0f0;
                    border-top-left-radius: 4px;
                    border-bottom-left-radius: 4px;
                    width: 99%;
                }

                &:last-child {
                    border-right: 1px solid #f0f0f0;
                    border-top: 1px solid #f0f0f0;
                    border-bottom: 1px solid #f0f0f0;
                    border-top-right-radius: 4px;
                    border-bottom-right-radius: 4px;
                    width: 1%;
                }
            }
        }
    }

    &:before {
        content: "";
        display: block;
        height: 12px;
    }

    .rptAction {
        background: #f0f0f0 !important;
    }
}

.event-info-container {
    > .event-primary-container {
        margin: 8px 0;
        padding-left: 15px;
        margin-left: -10px;

        @media (max-width: $screen-xs) {
            margin-left: -4px;
            padding-right: 25px;
        }

        &.simple {
            border-left: solid 4px transparent;

            @media (max-width: $screen-xs) {
                margin-left: -8px;
            }
        }

        &.detailed {
            border-left: solid 4px #007dc6;
        }

        > .event-name {
            font-weight: 500;
            font-size: 16px;
            color: #444;

            @media (max-width: $screen-xs) {
                font-size: 13px;
                line-height: 16px;
            }
        }

        > .event-banner-container {
            padding-top: 0px;
            font-size: 12px;
            line-height: 15px;
            color: #222222;
            display: -webkit-box;
            display: -moz-box;
            display: -ms-flexbox;
            display: -webkit-flex;
            display: flex;
            align-items: flex-start;
            flex-wrap: wrap;

            > .event-banner-element {
                padding: 5px 15px 5px 20px;
                position: relative;
                margin-top: 2px;

                @media (max-width: $screen-xs) {
                    padding: 5px 15px 0 20px;
                }

                &:before {
                    float: left;
                    color: #007dc6;
                    font-family: "viglobal";
                    font-size: 15px;
                    position: absolute;
                    left: 0;
                    top: 4px;

                    @media (max-width: $screen-xs) {
                        top: 6px;
                    }
                }

                &.event-status {
                    margin-top: -3px;
                    padding-left: 0;

                    @media (max-width: $screen-xs) {
                        display: inline-block;
                        width: 100%;
                        margin-bottom: 5px;
                    }
                }

                &.event-date {
                    @media (max-width: $screen-xs) {
                        display: inline-block;
                        width: 100%;
                    }

                    &:before {
                        content: "s";
                    }
                }

                &.event-time {
                    @media (max-width: $screen-xs) {
                        display: inline-block;
                        width: 100%;
                    }

                    &:before {
                        content: ">";
                    }
                }

                &.event-location {
                    @media (max-width: $screen-xs) {
                        display: inline-block;
                        width: 100%;
                    }

                    &:before {
                        content: "?";
                    }
                }

                &.event-available {
                    &:before {
                        content: "0";
                    }
                }

                &.event-not-scheduled {
                    &:before {
                        content: "\e011";
                    }
                }
            }
        }

        > .event-description {
            padding-top: 0px;
            font-size: $font-size-small;

            @media (max-width: $screen-xs) {
                padding-top: 12px;
                font-size: $font-size-xsmall;
                line-height: 15px;
            }
        }

        > .event-credits {
            padding: 5px 15px 5px 20px;
            position: relative;

            @media (max-width: $screen-xs) {
                font-size: $font-size-xsmall;
                color: #333;
            }

            &:before {
                float: left;
                color: #007dc6;
                font-family: "viglobal";
                font-size: 15px;
                position: absolute;
                left: 0;
                top: 3px;
                content: "\e026";

                @media (max-width: $screen-xs) {
                    top: 2px;
                }
            }
        }
    }

    > .event-format {
        float: right;
        margin-top: -27px;
        line-height: 20px;
        font-size: 25px;
        opacity: .4;

        @media (max-width: $screen-xs) {
            font-size: 20px;
            margin-top: -23px;
            margin-right: 4px;
        }
    }

    > .event-secondary-container {
        display: -webkit-box;
        display: -moz-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        align-items: stretch;
        justify-content: space-between;
        height: 100%;
        padding: 5px 0 10px;

        > .event-secondary-element {
            padding: 0 15px 0 15px;
            width: 50%;
            font-size: $font-size-xsmall;

            &:not(:last-child) {
                border-right: 1px solid #d8d8d8;
            }

            &:not(:first-child) {
                border-left: 1px solid #f9f9f9;
            }

            &:first-child {
                padding-left: 10px;
            }

            > .element-label {
                text-transform: uppercase;
                text-align: left;
                font-size: $font-size-xsmall;
                font-weight: bold;
                letter-spacing: 1px;
                padding-top: 0;
                font-family: "Roboto Condensed", sans-serif;
                opacity: .5;

                @media (max-width: $screen-xs) {
                    font-size: $font-size-xxsmall;
                }
            }
        }
    }

    @media screen and (max-width: $screen-sm) {
        > .event-secondary-container {
            display: block;

            > .event-secondary-element {
                width: 100%;
                border-right: 0px solid transparent !important;
                border-left: 0px solid transparent !important;

                &:not(:first-child) {
                    padding-top: 15px;
                }
            }
        }
    }
}

.search-results-no {
    text-transform: uppercase;
    text-align: left;
    font-family: "Roboto Condensed", sans-serif;
    font-size: $font-size-base;
    font-weight: bold;
    letter-spacing: 0.7px;
    padding-top: 0;
    line-height: 30px;
    color: #1d1e1f;
    padding-right: 50px;

    @media (max-width: $screen-xs) {
        font-size: $font-size-xsmall;
    }
}

.dialog-content {
	.search-filters {
		margin: 0;
	}

	.event-search {
		margin: 0;
	}

	@media (max-width: 1200px) {
		.search-bar {
			margin-left: 0;
			margin-right: 0;
		}
	}
}

.dataTables_processing {
    text-transform: uppercase;
    text-align: left;
    font-size: $font-size-base;
    font-weight: bold;
    letter-spacing: 0.7px;
    padding-top: 0;
    font-family: "Roboto Condensed", sans-serif;
    color: #1d1e1f;
    display: block;
    position: absolute;
    margin-top: -40px;
    width: 150px;
    z-index: 1;
    background-color: #ffffff;

    @media (max-width: $screen-xs) {
        font-size: $font-size-xsmall;
    }
}
