
.sitemap-search {
	padding: 20px 20px 0 20px;
	max-width: 1125px;
}

.sitemap-masonry-container {
    max-width: 1200px;
    margin-left: 11px !important;
    margin-top: 10px;

    @media (max-width: $screen-xs) {
        margin: 0 !important;
    }
}

.sitemap-container {
    padding-bottom: 100px;
    margin: 0 auto;
    z-index: 2;

    @media (max-width: $screen-xs) {
        padding-bottom: 0px;
        width: 100vw !important;
    }
}

.sitemap-element {
    margin: 5px;
    width: 358px;

    @media (max-width: $screen-sm) {
        margin-left: 10px !important;
        margin-right: 10px !important;
    }

    @media (max-width: $screen-xs) {
        margin: 0;
        width: 100vw;
        margin: 12px 0 !important;
    }

    .panel {
        border-radius: 6px;
        background-color: #ffffff;
        box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.1);
        margin: 5px 5px 0px 5px;
        background: white;
        padding-top: 15px;
        padding-right: 15px;
        padding-bottom: 20px;

        @media (max-width: $screen-xs) {
            border-radius: 0;
            margin: 0;
            padding-right: 18px;
        }

        .panel-heading {
            border-color: #ffffff;
            padding: 3px 0 2px 5px;

            > span {
                font-size: $font-size-large;
                font-weight: bold;
                font-style: normal;
                font-family: "roboto condensed";
                letter-spacing: 1px;
                line-height: normal;
                text-transform: uppercase;
                padding-left: 15px;

                .vi {
                    font-size: $font-size-larger;
                    float: right;
                    font-weight: normal;
                    font-stretch: normal;
                    color: lighten($text-color, 20%);
                }
            }
        }
    }

    .sitemap-group {
        list-style: none;
        margin-bottom: 0;
        padding-left: 20px;
        @include clearfix;

        .menu-subtitle {
            display: none;
        }

        .number {
            display: inline-block;
            border-radius: 3px;
            background-color: #555555;
            color: #ffffff;
            width: 40px;
            text-align: center;
            margin-right: 5px;

            &:empty {
                display: none;
            }
        }

        .number-primary {
            background-color: $label-primary-bg !important;
        }

        .number-success {
            background-color: $label-success-bg !important;
        }

        .number-info {
            background-color: $label-info-bg !important;
        }

        .number-warning {
            background-color: $label-warning-bg !important;
        }

        .number-danger {
            background-color: $label-danger-bg !important;
        }

        .dropdown-toggle {
            height: auto;
        }

        > li {
            padding-top: 5px;

            ::after {
                display: table-cell;
                vertical-align: middle;
                float: right;
                font-family: "viglobal";
                content: '\79';
            }

            .vi::after, div::after {
                content: '';
            }

            > ul {
                left: 40px;
                width: auto;
                min-width: 250px;
            }
        }

        > li > a {
            color: $sitemap-menu-sub-link-color;
            background-color: $sitemap-menu-sub-link-bg;
            border-radius: 3px;
            display: block;
            padding: 5px 6px 5px 10px;
            position: relative;
            text-decoration: none;
        }

        > li:hover > a {
            color: $sitemap-link-hover-color;
            background-color: $sitemap-link-hover-bg;
        }

        > li.active > a {
            background-color: $sitemap-menu-sub-link-active-bg;
            color: $sitemap-menu-sub-link-active-color;
        }

        > li > a.dropdown-toggle::before {
            font-family: "viglobal";
            content: "\e00d";
            float: right;
        }

        > li.open > a.dropdown-toggle::before {
            transform: rotate(90deg);
        }

        > li.open {
            background-color: $dropdown-link-hover-bg;
        }

        > li > a.selected {
            background-color: #1d87e4;
            color: #ffffff;

            &:hover {
                background-color: lighten(#1d87e4, 5%);
                color: #ffffff;
            }
        }
    }

    .inline-block.status {
        margin: 0px 10px 0 0px !important;
    }
}
