
.label:empty {
	display: none;
}

.panel-body {
	@media (max-width: $screen-xs) {
		padding: 0 !important;
	}

	.label {
		background-color: transparent;
		display: flex;
		font-size: 100%;
		font-weight: normal;
		vertical-align: middle;
		border: none;
		color: unset;
		padding: 0px;
		line-height: 22px;

		&:not(:empty) {
			&::before {
				font-family: "viglobal";
				font-size: ceil(($font-size-base * 1.2));
				display: table-cell;
				vertical-align: middle;
				line-height: 20px;
				max-width: 22px;
				min-width: 22px;
				text-align: left;
			}
		}
	}

	.label-wrap {
		white-space: pre-wrap;
		text-align: left;
	}

	.no-icon {
		&:not(:empty) {
			&::before {
				display: none !important;
				width: 0;
			}
		}
	}

	.label-primary {
		color: $label-primary-bg;

		&:not(:empty) {
			&::before {
				content: "\e010";
			}
		}
	}

	.label-success {
		color: $label-success-bg;

		&:not(:empty) {
			&::before {
				content: "\26";
			}
		}
	}

	.label-info {
		color: $label-info-bg;

		&:not(:empty) {
			&::before {
				content: "\e00a";
			}
		}
	}

	.label-warning {
		color: $label-warning-bg;

		&:not(:empty) {
			&::before {
				content: "\6c";
			}
		}
	}

	.label-danger {
		color: $label-danger-bg;

		&:not(:empty) {
			&::before {
				content: "\6d";
			}
		}
	}
}

td {
	.label {
		display: inline !important;
		font-size: 80% !important;
		padding: 2px 10px !important;

		&:not(:empty) {
			&::before {
				display: none !important;
			}
		}
	}

	.label-primary {
		border: 1px solid $label-primary-bg !important;
	}

	.label-success {
		border: 1px solid $label-success-bg !important;
	}

	.label-info {
		border: 1px solid $label-info-bg !important;
	}

	.label-default {
		border: 1px solid $label-info-bg !important;
	}

	.label-warning {
		border: 1px solid $label-warning-bg !important;
	}

	.label-danger {
		border: 1px solid $label-danger-bg !important;
	}
}