.topics-update-container {
	position: relative;
	float: right;
	padding-top: 3px;
	padding-right: 5px;
	margin-right: -1px;
	background-color: #007DC6;

	.vi-edit, .vi-delete, .vi-plus, .vi-long-arrow-right, .vi-plus-circle-o {
		font-size: 16px;
		padding: 2px 6px 0 6px;
		color: #fff !important;
	}
}

.jstree-leaf > .jstree-ocl {
	display: none !important;
}

.jstree-icon:empty {
	font-size: 6px;
}
.jstree-default {
    .jstree-anchor {
        margin-left: 2px
    }

    .jstree-icon {
        background-image: url("../images/jstree-32px.svg");
    }

    .jstree-themeicon-custom {
        background-image: none;
    }
}

.jstree-container-dark {
    .jstree-default {
        > .jstree-no-dots {
            .jstree-closed .jstree-ocl {
                background-position: -4px -5px;
            }

            .jstree-open {
                .jstree-ocl {
                    background-position: -36px -5px;
                }

                .jstree-closed .jstree-ocl {
                    background-position: -4px -5px;
                }
            }
        }

        .jstree-themeicon-custom {
            margin-left: -3px;
        }
    }

    .jstree-children .jstree-children .jstree-node {
        margin-left: 13px;
    }
}
.dialog-container {
    .jstree-container-dark {
        .jstree-default {
            > .jstree-no-dots .jstree-open .jstree-closed .jstree-ocl {
                background-position: -131px -69px;
            }

            .jstree-closed > .jstree-ocl {
                background-position: -131px -69px;
            }

            .jstree-open > .jstree-ocl {
                background-position: -163px -69px;
            }
        }

        li[aria-selected=true] {
            &.jstree-closed .jstree-ocl {
                background-position: -4px -4px;
            }

            &.jstree-open .jstree-ocl {
                background-position: -36px -4px;
            }
        }

        .jstree-open li[aria-selected=true] .jstree-ocl {
            transform: translate(5px, 0px);
        }
    }

    .panel-default {
        .page-filter {
            border-radius: 6px;
            background-color: #efefef;
            box-shadow: none;

            > li > div.menu-dropdown {
                max-height: 380px;
                box-shadow: 0 20px 20px 0 rgba(0,0,0,.10);
            }

            li.filter-menu {
                border-right: solid 1px rgba(102,102,102,.2);
                border-top: none;
                height: 36px;

                &.open {
                    color: #333;
                    background-color: #fff;
                    border-top: solid 1px #f1f1f1;
                }
            }
        }
    }
}

.jstree-container-light {
    .jstree-wholerow {
        display: none;
    }

    .jstree-wholerow-hovered {
        display: none;
    }

    .jstree-hovered {
        color: #000 !important;
    }

    li[aria-selected=true] {
        background-color: #007DC6 !important;
        padding-left: 0;
        padding-right: 0;
        padding-bottom: 0;
        margin: 2px 0 2px 25px;
        width: 100%;
        border-radius: 4px;
        border: 1px #007DC6 solid;
        box-shadow: 0 5px 30px 0 rgba(0, 0, 0, 0.1);

        .jstree-hovered {
            background-color: transparent !important;
            color: #fff !important;
        }

        .vi-circle {
            padding-left: 14px !important;
            padding-right: 8px;
            margin-right: 2px;
        }

        .jstree-ocl {
            margin-left: 0 !important;
        }

        &.jstree-open .jstree-children .jstree-open > .jstree-anchor {
            margin-left: 5px;
        }
    }

    .jstree-default {
        > ul {
            > li[aria-selected=true] {
                .vi-circle {
                    padding-left: 15px;
                    padding-right: 15px;
                }

                .jstree-ocl {
                    margin-left: 5px !important;
                }
            }
        }

        .jstree-closed > .jstree-ocl {
            background-position: -131px -69px;
        }

        .jstree-open > .jstree-ocl {
            background-position: -163px -69px;
        }
    }

    li[aria-selected=true] .jstree-children {
        background-color: #F6FCFF;
    }

    li[aria-selected=true] > .jstree-children {
        background-color: #F6FCFF;
        border-radius: 0 0 4px;
        border: 1px #007DC6 solid;
        border-top: none;

        .jstree-hovered {
            background-color: transparent !important;
            color: #000 !important;
        }
    }

    li[aria-selected=true] > .jstree-children > li:first-child {
        padding-top: 8px;
    }

    li[aria-selected=true] > .jstree-children > li:last-child {
        padding-bottom: 8px;
    }

    li[aria-selected=true] .jstree-clicked {
        padding: 2px 0 0 0;
        height: 30px;
    }

    li[aria-selected=true] .jstree-ocl {
        margin-left: 5px;
        margin-top: 1px;
    }

    li[aria-selected=true] {
        &.jstree-closed {
            .jstree-ocl {
                background-position: -4px -4px !important;
            }
        }


        &.jstree-open {
            .jstree-ocl {
                background-position: -36px -4px !important;
            }

            .jstree-children {
                .jstree-closed > .jstree-ocl {
                    background-position: -131px -69px !important;
                }

                .jstree-open > .jstree-ocl {
                    background-position: -163px -69px !important;
                }
            }
        }
    }

    .jstree-open {
        li[aria-selected=true] {
            margin: 2px 0 2px 25px;

            .jstree-ocl {
                transform: translate(5px, 0px) !important;
            }

            .jstree-clicked {
                transform: translate(5px, 0px) !important;
            }

            &.jstree-open .jstree-children .jstree-open .jstree-ocl {
                transform: translate(2px, 0px);
            }

            .vi-circle {
                margin-right: 2px;
                padding-left: 8px !important;
            }
        }
    }

    .jstree-themeicon.vi-circle {
        padding-left: 0px;
        margin-left: -3px
    }

    .jstree-default .jstree-anchor, .jstree-default .jstree-animated, .jstree-default .jstree-wholerow {
        transition: none;
    }
}
