.dashboard-panel-container {
	margin: 20px 20px 65px 20px;

	.panel {
		padding: 15px;
		margin-bottom: 20px;
		border-radius: 6px;

		@media (max-width: $screen-sm) {
			margin-left: -5px;
		}


		.panel-heading {
			border-bottom: none;


			&.with-expand-btn {
				display: flex;
				justify-content: space-between;

				@media (max-width: $screen-xs) {
					padding: 8px 8px 8px 4px;
				}

				.expand-button {
					cursor: pointer;
					position: relative;
					top: -10px;
					right: -10px;
				}
			}

			&.with-action-btn {
				display: flex;
				justify-content: space-between;

				.action-button {
					cursor: pointer;
					position: relative;
					top: -7px;
					right: -5px;

					@media (max-width: $screen-xs) {
						padding-top: 20px;
					}

					> a {
						margin-left: 2px;
					}

					.btn {
						text-transform: uppercase;
						font-size: 14px;
						text-align: left;
						padding: 6px 10px 3px;
						letter-spacing: 1px;

						> div {
							font-size: $font-size-xxsmall;
							font-weight: normal;
							text-transform: initial;
							color: rgba(255, 255, 255, 0.8);
							margin-bottom: -5px;
						}
					}
				}

				.action-button-adjust {
					top: -7px;
				}

				.btn-group-square {
					background-color: #f0f0f0
				}
			}

			@media (max-width: $screen-note) {
				padding: 0 0 8px 0;

				&.with-action-btn {
					flex-direction: column;
					margin: 0 -10px;

					.action-button {
						display: flex;
						position: static;

						> a {
							flex: 1 1;
							margin: 0 6px 0 0;
						}
					}
				}
			}

			@media (max-width: $screen-xs) {
				padding: 8px;
			}

			.panel-title {
				font-size: 20px;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
				margin-left: 0;
				font-family: "Roboto Condensed";
				font-weight: 600;
				letter-spacing: 0.5px;
				margin-bottom: 6px;

				@media (max-width: $screen-sm) {
					white-space: unset;
				}

				@media (max-width: $screen-xs) {
					font-size: 18px;
				}

				.panel-title-icon {
					color: #1181c2;
					font-size: 30px;
				}

				.panel-title-text-with-icon {
					position: relative;
					top: -5px;
					left: 8px;
				}
			}

			.help-block {
				margin-top: 0px;
				margin-bottom: 5px;
			}

			.panel-filter {
				font-size: $font-size-xxsmall; // 12px
				span {
					opacity: 0.7;
					margin: 0 7px 0 5px;

					&.filtered {
						opacity: 1;
						color: $brand-primary;
						font-weight: bold;
					}
				}

				i {
					vertical-align: text-top;
				}
			}
		}

		.panel-banner {
			background-color: #f0f0f0;
			margin-left: -15px;
			margin-right: -15px;

			.total-reviews-container {
				float: left;
				height: 70px;
				margin-right: 10px;

				.total-reviews-content {
					float: left;
					background-color: #007dc6;
					width: 110px;
					padding: 10px 10px 10px 20px;
					text-align: center;
					text-transform: uppercase;
					font-size: 10px;
					color: rgba(255, 255, 255, 0.6);

					.total-reviews-count {
						font-size: 22px;
						color: #ffffff;
						font-weight: bold;
					}
				}

				.right-arrow {
					float: right;
					margin-left: 0px;
				}
			}

			.charts-container {
				.donut-chart-container {
					float: left;
					width: 140px;
					padding: 10px;

					.donut-chart {
						float: left;
					}

					.legend {
						float: left;
						font-size: 12px;
						font-family: 'Roboto Condensed';
						white-space: nowrap;
						padding-top: 5px;
						padding-left: 5px;
					}

					.number {
						font-size: 18px;
						font-family: 'Roboto';
						font-weight: bold;
						margin-top: 0;
						padding-left: 15px;

						&.success {
							color: #009380;
						}

						&.warning {
							color: #faa800;
						}

						&.danger {
							color: #ac2a2a;
						}

						&.primary {
							color: #6450ac;
						}

						&.default {
							color: #111111;
						}
					}
				}
			}

			:after {
				content: "";
				clear: both;
				display: table;
			}
		}

		.panel-footer {
			margin-top: 15px;
			padding-top: 15px;
			padding-left: 25px;
			margin-left: -15px;
			margin-right: -15px;
			font-size: $font-size-base - 1;
			height: 20px;
			border-top: 1px solid #f0f0f0;

			&.no-border {
				border-top: 0px !important;
				margin: 0;
				padding: 0;
				height: 0 !important;
			}

			a:link {
				color: #007dc6;
				text-transform: uppercase;
				letter-spacing: 1px;
				font-size: 12px;
			}
		}

		&.legend-chart-summary {
			@media (max-width: $screen-xs) {
				margin-left: 0;
				padding: 20px 20px 10px 20px;
				border-radius: 0;
			}
		}
	}

	.panel-long {
		.panel-body {
			height: 600px !important;
			overflow-y: hidden !important;
		}

		.has-footer {
			height: 548px !important;
		}
	}

	.panel-medium {
		.panel-body {
			height: 450px !important;
			overflow-y: hidden !important;
		}
	}

	.panel-short {
		.panel-body {
			height: 244px !important;
			overflow-y: hidden !important;
		}

		.has-footer {
			height: 192px !important;

			@media (max-width: $screen-xs) {
				height: 280px !important;
			}
		}

		.legends {

			@media (max-width: $screen-xs) {
				text-align: left;
				margin: 10px 0 15px 5px;
			}
		}
	}

	.panel-shadow {
		display: none;
		height: 100px;
		position: absolute;
		top: 1px;
		height: 25px;
		left: 15px;
		z-index: 1900;
	}

	.panel-shadow::after {
		content: '';
		position: absolute;
		bottom: 0;
		width: 100%;
		height: 15px;
		background: linear-gradient(rgba(255, 255, 255, 0), #fff);
		pointer-events: none;
	}

	.dashboard-tile-with-icon {
		border-radius: 6px;
		box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.1);
		color: white;
		height: 95px;
		margin-bottom: 20px;
		position: relative;

		@media (min-width: $screen-lg) {
			margin-bottom: 0;
		}


		.icon {
			opacity: 0.3;
			background-color: #000000;
			position: absolute;
			top: 0;
			left: 0;
			padding: 24px 12.5px;
			width: 82px;
			height: 100%;
			border-radius: 6px 0 0 6px;
		}

		.content {
			text-align: center;
			height: 100%;
			margin-left: 82px;
			cursor: pointer;
			position: relative;
			overflow: hidden;

			.show-on-hover {
				position: absolute;
				left: -100%;
				transition: all 0.25s;
				width: 100%;
			}

			.hide-on-hover {
				position: absolute;
				left: 0;
				transition: all 0.25s;
				width: 100%;
			}

			&:hover {
				.hide-on-hover {
					left: 100%;
				}

				.show-on-hover {
					left: 0;
				}
			}

			.number {
				top: 50%;
				font-size: 35px;
				margin-top: -35px;
			}

			.vi-arrow-right {
				top: 50%;
				font-size: 35px;
				margin-top: -25px;
			}

			.tile-text {
				opacity: 0.7;
				top: 50%;
				margin-top: 10px;
				line-height: 16px;
				font-size: $font-size-small;
			}
		}
	}

	.dashboard-tile {
		padding: 7.5px 7.5px 7.5px 0;

		@media (max-width: $screen-xs) {
			padding: 7.5px 7.5px 7.5px 10px;
		}

		&:last-child {
			padding: 7.5px 0 7.5px 7.5px;

			@media (max-width: $screen-xs) {
				padding: 7.5px 10px 7.5px 7.5px;
			}
		}

		.content {
			width: 100%;
			padding-bottom: 145px;
			border-radius: 6px;
			text-align: center;
			height: 100%;
			position: relative;
			color: white;
			overflow: hidden;
			cursor: pointer;

			.show-on-hover {
				position: absolute;
				left: -100%;
				transition: all 0.25s;
				width: 100%;
			}

			.hide-on-hover {
				position: absolute;
				left: 0;
				transition: all 0.25s;
				width: 100%;
			}

			&:hover {
				.hide-on-hover {
					left: 100%;
				}

				.show-on-hover {
					left: 0;
				}
			}

			.number {
				position: absolute;
				top: 50%;
				margin: auto;
				font-size: 35px;
				margin-top: -35px;
			}

			.tile-text {
				position: absolute;
				top: 50%;
				margin: auto;
				opacity: 0.7;
				font-size: $font-size-small;
				line-height: 16px;
				margin-top: 14px;

				@media (max-width: $screen-xs) {
					padding: 0 5px;
				}

				&.small {
					font-size: $font-size-xsmall;
				}
			}

			.vi-arrow-right {
				margin-top: -30px;
			}
		}
	}

	.no-data-stacked-small-box {
		text-align: center;
		padding-top: 25px;

		> div {
			font-size: 12px;
			letter-spacing: 1px;
			width: 75%;
			margin: 10px auto 0;
			color: #7CB3D1;
			padding: 0 10px;
			line-height: 16px;

			> h5 {
				text-transform: uppercase;
				letter-spacing: 2px;
				font-family: "Roboto Condensed", sans-serif;
				font-weight: 600;
				color: #669DBB;
				padding: 5px 0;
			}
		}
	}

	.no-data-inline-wide-box {
		padding-top: 25px;

		> div {
			text-align: center;

			&:last-child {
				font-size: 12px;
				letter-spacing: 1px;
				margin-top: 20px;
				color: #7CB3D1;
				padding: 0 10px;

				@media (min-width: $screen-xs) {
					padding: 0 20px;
				}

				> h5 {
					text-transform: uppercase;
					letter-spacing: 2px;
					font-family: "Roboto Condensed", sans-serif;
					font-weight: 600;
					color: #669DBB;
					padding: 0 0 5px 0;
					margin-top: -5px;
				}
			}
		}

		@media (min-width: $screen-lg) {
			> div {
				&:first-child {
					text-align: right;
				}

				&:last-child {
					text-align: left;
				}
			}
		}
	}

	@media (min-width: $screen-lg) {
		.no-data-inline-wide-box {
			padding-top: 30px;
		}
	}

	.success-background {
		background-color: $dashboard-success;

		> a {
			color: #fff;
		}
	}

	.primary-background {
		background-color: $dashboard-primary;

		> a {
			color: #fff;
		}
	}

	.warning-background {
		background-color: $dashboard-warning;

		> a {
			color: #fff;
		}
	}

	.danger-background {
		background-color: $dashboard-danger;

		> a {
			color: #fff;
		}
	}

	.default-background {
		background-color: $text-color;

		> a {
			color: #fff;
		}
	}

	svg > g > g:last-child {
		pointer-events: none;
	}


	.donut-chart-container {
		position: relative;

		.center-at-chart {
			position: absolute;
			top: 50%;
			z-index: 1;
			text-align: center;
			margin: auto;
			width: 100%;
			margin-left: -10px;

			@media (max-width: $screen-xs) {
				margin-left: -5px;
			}
		}

		.number {
			font-size: 35px;
			margin-top: -35px;
		}

		.description {
			font-size: $font-size-xsmall;
			opacity: 0.5;
			margin-top: 10px;
		}

		#divChartEmployeePerYear [dir=ltr],
		#divChartEmployeePerOffice [dir=ltr],
		#divChartEmployeePerDepartment [dir=ltr],
		#divChartContentProvider [dir=ltr] {

			@media (min-width: $screen-xs) {
				padding: 0;
				width: 100% !important;
			}

			svg {
				@media (min-width: $screen-xs) {
					margin: 0 auto;
					display: block;
				}
			}
		}

		#divChartFeedbackDetail,
		#divChartFeedbackYearAvg,
		#divEventsCompleted {
			[dir=ltr] {
				@media (min-width: $screen-xs) {
					padding: 0;
					width: 100% !important;
				}

				svg {

					@media (min-width: $screen-xs) {
						width: 100% !important;
						margin: 0 auto;
						display: block;
					}
				}
			}
		}
	}

	#divARDByRace,
	#divARDByAgeGroup {
		.donut-chart-container {
			.center-at-chart {
				@media (min-width: $screen-xs) {
					margin-left: -2px;
				}
			}
		}
	}

	.chart-item {
		@media (min-width: $screen-sm) {
			max-width: 300px;
		}
	}

	.bar-chart-item {
		@media (min-width: $screen-sm) {
			max-width: 750px;
		}
	}

	.half-donut-chart-container {
		position: relative;

		.half-donut-chart {
			margin-top: 25px;
			height: 300px;
			font-size: 8px;
			text-align: center;
		}

		.center-at-chart.legend {
			position: absolute;
			top: 50%;
			text-align: center;
			font-size: 16px;
			margin-top: -35px;
			font-weight: bold;
			width: 100%;
			border-bottom: 2px solid #e0e0e0;
			padding-bottom: 10px;
		}

		.half-donut-chart-legend-container {
			position: absolute;
			top: 50%;
			width: 100%;
			display: flex;
			padding: 15px 20px;

			> div {
				flex-basis: 50%;

				&:first-child {
					border-right: 2px dashed #d5d5d5;
					margin-left: 2px;
				}

				.number {
					font-size: 25px;
					font-weight: bold;
				}

				.description {
					font-size: 12px;
					color: #232425;
					opacity: 0.5;

					@media (min-width: $screen-xs) {
						margin-top: -6px;
					}
				}

				.percentage {
					font-size: 15px;
					color: #232425;
				}

				.right {
					text-align: right;
				}
			}
		}

		div[dir=ltr] {
			@media (min-width: $screen-xs) {
				padding: 0;
				width: 100% !important;
			}
		}
	}

	.legend-chart-summary {
		background-color: #555555;
		flex-basis: 260px;
		flex-shrink: 0;
		position: relative;
		padding-left: 35px;
		padding-right: 35px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		border-radius: 0 6px 6px 0;

		@media (max-width: $screen-md) {
			margin-top: -20px;
		}


		.summary-title {
			display: inline-block;
			position: absolute;
			top: -1px;
			background-color: #ac2a2a;
			border-bottom-left-radius: 6px;
			border-bottom-right-radius: 6px;
			padding: 2px 15px;



			.summary-title-text {
				font-family: "roboto condensed";
				color: white;
				font-size: 18px;
				letter-spacing: 1px;
				font-weight: 900;

				@media (max-width: $screen-xs) {
					font-size: 16px;
				}
			}
		}


		.summary-container {
			display: flex;
			justify-content: space-between;
			align-items: center;
			border-bottom: 1px dashed #9c9c9c;
			height: 100px;

			&:last-child {
				border-bottom: none;
			}

			.summary-label {
				font-size: 14px;
				color: #9c9c9c;
			}

			.summary-number {
				display: flex;
				align-items: center;
				flex-direction: column;

				.number {
					font-size: 35px;
					text-align: right;
				}

				.percentage {
					font-size: 18px;
					color: white;
					margin-left: 5px;
					border-top: 1px solid #9c9c9c;
					margin-top: -5px;
					padding-top: 5px;
				}
			}
		}
	}

	.legend {
		.circle {
			&.success {
				color: $dashboard-success;
			}
		}
	}

	.item-group {
		list-style: none;
		margin-bottom: 0;
		padding: 0 8px;
		@include clearfix;

		@media (max-width: $screen-xs) {
			padding: 5px;
		}

		.menu-subtitle {
			display: none;
		}

		.dropdown-toggle {
			height: auto;
		}

		> li {
			padding-top: 5px;

			.vi::after, div::after {
				content: '';
			}

			&.item-calendar-month {
				color: #555;
				font-weight: 500;
				letter-spacing: 0.5px;
				text-transform: uppercase;
			}

			&.item-title {
				height: 87px;
				overflow: hidden;

				> a {
					> div.item-calendar-container {
						display: flex;

						> div.item-calendar-day {
							width: 74px;
							color: lighten($text-color, 50%);
							position: relative;

							> div.item-calendar-day-no {
								position: absolute;
								top: 50%;
								left: 50%;
								transform: translate(-50%, -50%);
								font-size: 0;
								text-align: center;

								.item-calendar-day-number {
									color: #FFFFFF;
									opacity: 1;
									font-size: 22px;
									font-weight: bold;
									font-style: italic;
								}

								.item-calendar-day-name {
									font-size: 8.5px;
									text-transform: uppercase;
									letter-spacing: 0.5px;
									font-weight: 100;
								}
							}
						}

						> div.item-calendar-info {
							flex-grow: 1;
							overflow: hidden;
							padding-left: 15px;
							padding-top: 7px;

							> div.item-calendar-info-primary {
								font-weight: bold;
								color: #555;
								font-size: 12px;
								letter-spacing: 0.5px;
								text-overflow: ellipsis;
								white-space: nowrap;
								overflow: hidden;

								> i.vi {
									font-weight: normal;
									opacity: .5;
								}
							}

							> div.item-calendar-info-secondary {
								white-space: nowrap;
								font-size: 14px;
								overflow: hidden;
								text-overflow: ellipsis;
							}

							> div.item-calendar-info-optional {
								white-space: nowrap;
								font-style: italic;
								opacity: .5;
								font-size: 12px;
								letter-spacing: 0.5px;
								overflow: hidden;
								text-overflow: ellipsis;
							}
						}
					}

					> div.item-container {
						display: flex;

						> div.item-info {
							flex-grow: 1;
							overflow: hidden;
							padding-left: 15px;
							padding-top: 7px;

							> div.item-info-status-container {
								background-color: transparent;
								margin-top: -12px;
								position: absolute;
								width: 100%;
								text-align: right;
								padding-right: 40px;
								font-size: 11px;

								> div.item-info-status {
									float: right;
									border: none;
									border-bottom-left-radius: 4px;
									border-bottom-right-radius: 4px;
									padding: 0px 4px 0px 4px;

									&.status-primary {
										background-color: #1d87e4;
										color: #f0f0f0;
									}

									&.status-success {
										background-color: #009380;
										color: #f0f0f0;
									}

									&.status-warning {
										background-color: #faa800;
										color: #f0f0f0;
									}

									&.status-danger {
										background-color: #ac2a2a;
										color: #f0f0f0;
									}

									&.status-info {
										background-color: #6450ac;
										color: #f0f0f0;
									}

									&.status-default {
										background-color: #111111;
										color: #f0f0f0;
									}
								}
							}

							> div.item-info-icon {
								float: left;
