$shift-transition-time: 0.2s;
$shift-transition-function: ease;
$shift-amount: 40px;

.request-item {
    border-radius: 6px;
    border: 1px solid #ebebeb;
    background-color: #f6f6f6;
    padding: 15px 15px 10px 20px;
    position: relative;
    cursor: pointer;
    overflow: hidden;

    .item-status-border-bar {
        height: 100%;
        width: 5px;
        position: absolute;
        top: 0;
        left: 0;
        border-top-left-radius: 2px;
        border-bottom-left-radius: 2px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition-property: width;
        transition-duration: $shift-transition-time;
        transition-timing-function: $shift-transition-function;

        .vi-long-arrow-right {
            display: none;
            color: white;
            font-size: 22px;
        }
    }

    .item-info-container {
        margin-left: 0px;
        transition-property: margin-left;
        transition-duration: $shift-transition-time;
        transition-timing-function: $shift-transition-function;
    }

    .item-info-status {
        font-family: 'roboto condensed';
        display: inline-block;
        padding: 0px 4px 0px 4px;
        color: white;
        position: absolute;
        font-size: 10px;
        letter-spacing: 1px;
        top: 0;
        right: 15px;
        padding: 2px 8px;
        border-radius: 0 0 3px 3px;
        transition-property: right;
        transition-duration: $shift-transition-time;
        transition-timing-function: $shift-transition-function;
    }


    .item-info-score {
        font-size: 20px;
        font-weight: bold;
        font-stretch: condensed;
        color: #ffffff;
        padding: 1px 9px;
        border-radius: 2px;
        display: inline-block;
        margin-right: 10px;
        float: left;

        &:empty {
            display: none;
        }
    }


    .item-info-from-to {
        color: #333;
        font-weight: 600;
        line-height: 18px;

        .primary {
            font-size: 14px;
            color: #333;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }

        .secondary {
            color: #aaa;
            font-size: 11px;
            letter-spacing: 0.5px;
            font-style: italic;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            margin-top: -2px;
            font-weight: normal;
        }

        .vi-long-arrow-return {
            color: #faa800;
            margin-right: 5px;
            vertical-align: text-top;
            margin-left: -2px;
        }
    }

    .item-info-secondary {
        white-space: nowrap;
        font-size: 13px;
        overflow: hidden;
        text-overflow: ellipsis;
        padding-top: 5px;
        font-weight: 400;
        color: #333;
    }

    .item-info-optional {
        white-space: nowrap;
        font-style: italic;
        font-size: 11px;
        letter-spacing: 0.5px;
        overflow: hidden;
        text-overflow: ellipsis;
        color: #aaa;
        padding-right: 30px;
        margin-top: -2px;
    }

    .item-info-icon {
        display: inline-block;
        position: absolute;
        right: 14px;
        margin-top: -18px;
        margin-right: 0;
        transition-property: right;
        transition-duration: $shift-transition-time;
        transition-timing-function: $shift-transition-function;
    }
    /* On Hover */
    &:hover {
        .item-status-border-bar {
            width: $shift-amount;

            .vi-long-arrow-right {
                display: block;
            }
        }

        .item-info-container {
            margin-left: $shift-amount;
        }
    }
}

