            margin-top: -7px;
            right: 50px;
            border-bottom: 10px solid #ffffff;
            margin-right: 0;
         }
    }

     &::after{
        content: '';
        position: absolute;
        top: 100%;
        width: 0; 
        height: 0; 
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;  
        border-top: 10px solid #636363;
        clear: both;
        right: 50%;
        margin-right: -10px;

         @media (max-width: $screen-sm) {
            top: 50%;
            margin-top: -7px;
