}


.timeline-inverted {
    @media (min-width: 768px) {
        .request-item {
            &:hover {
                .item-info-container {
                    //margin-left: -$shift-amount;
                    margin-left: 0;
                }

                .item-info-status {
                    right: 20px + $shift-amount;
                }

				.item-info-icon {
                    right: 20px + $shift-amount;
                }
            }
        }
        .item-status-border-bar {
            left: inherit;
            right: 0;
            border-top-left-radius: 0px;
            border-bottom-left-radius: 0px;
            border-top-right-radius: 2px;
            border-bottom-right-radius: 2px;
        }
    }
    
}

.dashboard-panel-container .panel.full-width-panel {
    @media (max-width: $screen-sm) {
        margin: 0 -20px;
    }

    @media (max-width: $screen-xs) {
        margin: 0px;
    }

    .panel-heading {

        @media (max-width: $screen-xs) {
            padding: 15px;
        }
    }
}

.three-buttons {
    border: 1px solid #ccc;
}

@media (max-width: $screen-sm) {
    .dashboard-panel-container {
        .btn-group-square ,  .btn-group-round {
            width: 100%;

            .btn {
                width: 48%;

                > label {
                    width: 100%;
                }
            }
        }

		.panel {
			margin-left: -5px;

			.panel-heading {
				&.with-action-btn {
					display: block;

					.btn-group {
						margin-top: 10px;
						
						&.three-buttons {
							.btn {
								width: 32%;
							}
						}
					}
				}
			}
		}
    }
}

@media (max-width: $screen-xs){

    .dashboard-panel-container {
        margin: 20px 0;
        overflow: hidden;

        .panel {
                margin-left: 0;
                padding: 15px;
                margin-bottom: 20px;
                border-radius: 0;
        }
    }

    .full-width-panel + .full-width-panel {
        margin: 20px 0 20px 0 !important;
    }
}

.team-row-container {
}

.team-row {
	display: flex;
	height: 70px;
	align-items: center;
	border-top: solid 1px #dddddd;

	&:last-child {
		border-bottom: solid 1px #ddd;
		margin-bottom: 20px;
	}

	&:first-child, &:only-of-type {
		margin-top: 20px;
	}


	.name {
		width: 20%;
		font-size: 14px;
		color: #333;
		min-width: 165px;
	}

	.chart {
		flex-grow: 1;
		min-width: 1%;
	}

	.info {
		width: 15%;
		display: flex;
		justify-content: center;
		align-items: center;
		min-width: 135px;

		> span.title {
			font-size: 11px;
			opacity: 0.5;
		}

		> span.count {
			margin-left: 10px;
			font-weight: bold;
			font-size: 24px;
		}

		> span.trend {
			margin-left: 10px;

			.vi-trending-up {
				color: #58b957;
			}

			.vi-trending-down {
				color: #ac2a2a;
			}
		}
	}

	.arrow {
		height: 100%;
		width: 30px;
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: #f6f6f6;
	}
}

.flex-fill-width {
    flex-grow: 1;
    min-width: 1%;
}

.flexwrap {
    flex-wrap:wrap;
}

.flex-space-between-items {
    justify-content: space-between;
}

@media (min-width: $screen-md+1px) {
    .flex-container.flex-container-with-margin.row {
        margin-bottom: 20px;

        .panel-no-margin-full-height {
            margin-bottom: 0;
            height: 100%;
        }
    }
}

.attrition-trends-chart-tooltip {
    width: 200px;
    border-radius: 3px;
    
    .tooltip-title {
        background-color: #333;
        display: flex;
        align-items: center;
        padding: 7px 10px;

        > span {
            color: white;
            font-size: 13px;
            text-transform: uppercase;
        }

        .year {
            margin-left: 5px;
            font-weight: bold;
        }
    }
    .total-row {
        background-color: #f0f4f4;
        color: #929292;
        padding: 5px 10px;
        display: flex;
        align-items: center;

        .number {
            margin-right: 5px;
        }
    }

    .number {
        font-size: 20px;
        font-weight: bold;
    }

    .percentage-row {
        display: flex;
        padding: 10px;
        justify-content: space-between;
        > div {
            flex-basis: 50%;
        }

        .percentage {
            color: #666;
            font-size: 14px;
        }

        .text {
            font-size: 11px;
        }
    }
}

.legend .description {
    @media (max-width: $screen-xs) {
        margin-top: -5px;
    }
}
.legends {
    @media (max-width: $screen-xs) {
        margin: 10px 0 20px 10px;
        white-space: normal;
    }
}
