
//
//info-card
//
.info-card-container {
    background-color: $brand-primary;
    margin: 20px;
    border-radius: 6px;
    padding: 15px;

    @media screen and (max-width: $screen-sm) {
        margin: 20px 5px;
        padding: 15px 10px;
    }
}

.info-card-heading {
    display: flex;
    height: 100%;

    @media screen and (max-width: $screen-sm) {
        display: inline-block;
    }

    .info-card-header-container {
        flex-grow: 1;
        display: flex;
        align-items: flex-start;
        flex-wrap: nowrap;
        height: 100%;
        color: #fff;

        .info-card-image {
            width: 95px;

            @media screen and (max-width: $screen-md) {
                display: none;
            }

            .vi {
                font: normal normal normal 60px/1 "viglobal";
            }

            .item-calendar-day {
                width: 90px;
                height: 85px;
                color: lighten($text-color, 50%);
                position: relative;

                i.vi-calendar-background {
                    position: absolute;
                    font-size: 100px;
                    margin: -8px 0 0 -12px;
                    color: #fff;
                }

                > div.item-calendar-day-no {
                    position: absolute;
                    top: 42px;
                    left: 37px;
                    transform: translate(-50%, -50%);
                    font-size: 0;
                    text-align: center;

                    .item-calendar-month {
                        display: block;
                        margin-top: 14px;
                        padding: 1px 0 0 0;
                        width: 76px;
                        color: #fff;
                        font-size: 9px;
                        text-transform: uppercase;
                        letter-spacing: 2px;
                        font-weight: 600;
                    }

                    .item-calendar-day-number {
                        display: block;
                        margin-left: -5px;
                        color: $brand-primary;
                        opacity: 1;
                        font-size: 22px;
                        font-weight: bold;
                        font-style: italic;
                    }

                    .item-calendar-day-name {
                        color: $brand-primary;
                        font-size: 8.5px;
                        text-transform: uppercase;
                        letter-spacing: 0.5px;
                        font-weight: 100;
                    }
                }
            }
        }

        .info-card-header {
            width: 100%;
            align-self: center;

            .info-card-header-title {
                font-size: 20px;
                line-height: 22px;
                margin-bottom: 15px;
                margin-top: 3px;
            }

            .info-card-header-details {
                padding-top: 0px;
                padding-bottom: 9px;
                font-size: 12px;
                line-height: 15px;
                color: #222;
                display: -webkit-box;
                display: -moz-box;
                display: -ms-flexbox;
                display: -webkit-flex;
                display: flex;
                align-items: flex-start;
                flex-wrap: wrap;

                > .info-card-header-details-item {
                    padding: 5px 18px 5px 22px;
                    position: relative;
                    color: #fff;
                    font-size: 12px;
                    font-weight: 100;
                    letter-spacing: 1px;

                    @media (max-width: $screen-xs) {
                        padding: 5px 15px 0 20px;
                    }

                    &:before {
                        float: left;
                        color: #fff;
                        font-family: "viglobal";
                        font-size: 15px;
                        position: absolute;
                        left: 0;
                        top: 4px;
                        opacity: 0.3;

                        @media (max-width: $screen-xs) {
                            top: 6px;
                        }
                    }

                    &.info-date {
                        @media (max-width: $screen-xs) {
                            display: inline-block;
                            width: 100%;
                        }

                        &:before {
                            content: "s";
                        }
                    }

                    &.info-time {
                        @media (max-width: $screen-xs) {
                            display: inline-block;
                            width: 100%;
                        }

                        &:before {
                            content: ">";
                        }
                    }

                    &.info-chevron {
                        @media (max-width: $screen-xs) {
                            display: inline-block;
                            width: 100%;
                        }

                        &:before {
                            content: "y";
                        }

                        a {
                            color: #fff;
                            cursor: pointer;

                            &:hover {
                                text-decoration: underline;
                            }
                        }
                    }

                    &.info-location {
                        @media (max-width: $screen-xs) {
                            display: inline-block;
                            width: 100%;
                        }

                        &:before {
                            content: "?";
                        }
                    }

                    &.info-type {
                        @media (max-width: $screen-xs) {
                            display: inline-block;
                            width: 100%;
                        }

                        &:before {
                            content: "\e017";
                        }
                    }

                    &.event-type-1 {
                        @media (max-width: $screen-xs) {
                            display: inline-block;
                            width: 100%;
                        }

                        &:before {
                            content: "\e017";
                        }
                    }

                    &.event-type-2 {
                        @media (max-width: $screen-xs) {
                            display: inline-block;
                            width: 100%;
                        }

                        &:before {
                            content: "\e059";
                        }
                    }

                    &.event-type-3 {
                        @media (max-width: $screen-xs) {
                            display: inline-block;
                            width: 100%;
                        }

                        &:before {
                            content: "\e030";
                        }
                    }

                    &.event-type-4 {
                        @media (max-width: $screen-xs) {
                            display: inline-block;
                            width: 100%;
                        }

                        &:before {
                            content: "]";
                        }
                    }
                }
            }

            .info-card-header-extra {
                padding-top: 0px;
                font-size: 12px;
                font-weight: 100;
                letter-spacing: 1px;
                line-height: 15px;
                color: #222;
                display: -webkit-box;
                display: -moz-box;
                display: -ms-flexbox;
                display: -webkit-flex;
                display: flex;
                align-items: flex-start;
                flex-wrap: wrap;

                > .info-card-header-extra-item {
                    padding: 5px 15px 5px 0px;
                    position: relative;
                    color: #fff;

                    > .badge-circle {
                        border-radius: 12px;
                        font-size: 13px;
                        font-weight: 500;
                        padding: 2px 5px 2px 6px;
                        vertical-align: baseline;
                        border: none;
                        -webkit-box-shadow: inset 0px 0px 0px 1px rgba(255,255,255,0.3);
                        -moz-box-shadow: inset 0px 0px 0px 1px rgba(255,255,255,0.3);
                        box-shadow: inset 0px 0px 0px 1px rgba(255,255,255,0.3);

                        &.badge-warning {
                            border: none;
                            box-shadow: none;
                        }
                    }

                    a {
                        color: #ffffff;
                        font-weight: 500;
                        padding: 0 2px 0 4px;

                        &:hover {
                            transform: translateY(-1px);
                            display: inline-block;
                            text-decoration: none;
                            transition: all 0.2s;
                        }
                    }
                }
            }
        }
    }

    .info-card-action-container {
        min-width: 150px;

        @media screen and (max-width: $screen-sm) {
            min-width: unset;
            margin-top: 10px;
        }

        .info-card-action {
            float: right;

            @media screen and (max-width: $screen-sm) {
                float: left;
            }
        }
    }
}

.info-card-detail-panel {
    background-color: #fff;
    margin: 15px 1px 2px 1px;
    border-radius: 4px;
    padding: 6px 10px 0;
}



//
//status-card
//
.status-card-container {
	display: table;
	width: 100%;
	background-color: #f7f7f7;
	margin-bottom: 4px;
	border-radius: 3px;
	border: 1px solid #ececec;
}

.status-card-info {
    display: table-cell;

    .status-success {
        background-color: $brand-success;
        color: #fff;
    }

    .status-danger {
        background-color: $brand-danger;
        color: #fff;
    }

    .status-warning {
        background-color: $brand-warning;
        color: #fff;
    }

    .status-default {
        background-color: $brand-info;
        color: #fff;
    }

    .badge {
        letter-spacing: 0.5px;
        font-weight: 400;
        padding: 3px 8px;
        font-size: 11px;
        vertical-align: text-top;
    }
}

.status-card-status {
    float: right;
    padding-top: 2px;
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
    font-size: 11px;
    line-height: 18px;
    text-transform: uppercase;
    min-width: 90px;
    text-align: center;
    margin-right: 15px;
    width: 100px;
    padding: 2px 0 0;
    letter-spacing: 0.5px;
}

.status-card-title {
    font-size: 15px;
    font-weight: 500;
    color: #444;
    padding: 8px 0 6px 15px;
}

.status-card-subtitle {
    margin-top: -10px;
    padding-left: 15px;
}

.status-card-message {
    font-size: 11px;
    color: #333;
    opacity: .5;
    font-style: italic;
    padding: 2px 0 8px 0;
}

.status-card-link {
    font-size: 11px;
    padding-bottom: 4px;

    .badge {
        padding: 3px 6px;
        line-height: 1;
        vertical-align: baseline;
        font-size: 12px;
    }

    .btn-default {
        color: $brand-primary;
    }
}

.status-card-action {
	display: table-cell;
	width: 40px;
	text-align: center;
	vertical-align: middle;
	background-color: #f0f0f0;
}

.status-card-details {
    padding: 10px 10px 5px 10px;
    background-color: #fff;
    border: #eee 1px solid;
    box-shadow: 0 1px 8px 0 rgba(0,0,0,.05);
    border-radius: 5px;
    display: flex;
    height: 100%;
    margin: 0 15px 1px 15px;

    &:last-child {
        margin-bottom: 20px;
    }

    &:hover {
        box-shadow: 0 5px 25px 0 rgba(0,0,0,.1);
        border: #ddd 1px solid;
        transform: translateY(-1px);
        transition: all .3s;
    }

    .label {
        display: inline-block;
        border: 1px solid;
        font-size: 80%;
        margin-right: 20px;
        line-height: 18px;
        min-width: 80px;
        padding: 1px 0 0;
        margin-top: -1px;
    }

    .label-success::before {
        content: "" !important;
    }

    .label-warning::before {
        content: "" !important;
    }

    .label-primary::before {
        content: "" !important;
    }

    .label:not(:empty)::before {
        min-width: 0px;
    }

    .status-card-details-date {
        display: inline-block;
        min-width: 100px;
        font-weight: 500;
        color: #444;
    }

    .status-card-details-type {
        display: inline;
        min-width: 100px;
    }

    .status-card-details-type {
        &::before {
            content: "\e00d";
            color: $brand-primary;
            font-family: "viglobal";
            font-size: 14px;
            display: table-cell;
            vertical-align: middle;
            line-height: 10px;
            max-width: 18px;
            min-width: 18px;
            text-align: left;
        }
    }

    .badge-success {
        background-color: $brand-success;
        color: #fff;
    }
}

.status-card-details-info {
	flex-grow: 1;
}

.status-card-details-action {
	margin-top: -7px;
}

.panel .status-card-container:first-child, 
.panel-heading + .status-card-container {
    margin-top: 5px;
}