/* search-bar */
.search-bar, .search-container, .search-container > a, .browse-calendar-container, .browse-calendar-container > a {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
}

.search-bar {
    margin: 20px 23px 8px 20px;
}

.search-container {
    flex: 1 1;
    padding-left: 2px;
    padding-right: 15px;

    @media (max-width: $screen-sm) {
        padding-right: 0;
    }

    > input {
        opacity: 1;
        font-size: 16px;
        font-style: normal;
        color: #222;
        -webkit-flex: 1 1 100%;
        -webkit-box-flex: 1 1 100%;
        -moz-flex: 1 1 100%;
        -moz-box-flex: 1 1 100%;
        flex: 1 1 100%;
        border: 1px #ccc solid;
        border-radius: 37px;
        padding: 0 0 0 50px;

        &:focus {
            box-shadow: 0 5px 10px 0 rgba(0,0,0,0.15);
            outline: none;
            transition: all 0.3s;
            border-color: #007dc6;
        }
    }

    &.with-search-button {
        > input {
            border-right: 0;
            border-radius: 25px 0 0 25px;

            &:focus {
                box-shadow: 0 5px 10px 0 rgba(0,0,0,0.15);
                outline: none;
                transition: all 0.3s;
                border-color: #007dc6;
            }
        }
    }

    .icon {
        font-size: 26px;
        color: #00649e;
        transform: rotate(90deg);
        margin: 0 -36px 0 6px;
    }

    > a {
        width: 103px;
        height: 50px;
        color: #fff;
        background-color: #00649e;
        text-transform: uppercase;
        text-decoration: none;
        flex: 0 0 103px;
        font-family: "roboto condensed";
        letter-spacing: 1px;
        border-radius: 0 25px 25px 0;
    }
}
.browse-calendar-container, .search-container > a {
    align-items: center;
    justify-content: center;
}

.browse-calendar-container {
    border-radius: 25px;
    margin-left: 0px;
    height: 50px;
    background-color: #444;
    flex: 0 0 205px;

    span {
        color: #fff;
        opacity: 0.5;
        font-size: 25px;
        margin: 5px 12px 0 0;
    }

    > a {
        font-size: 14px;
        color: #fff;
        text-transform: uppercase;
        text-decoration: none;
        align-items: center;
        padding: 5px 18px;
        font-family: "roboto condensed";
        letter-spacing: 1px;
    }
}

.browse-calendar-container a:hover, .search-container a:hover {
    color: #ddd;
}

.search-options-container {
	height: 50px;
	display: -webkit-box;
	display: -moz-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	height: 100%;
	margin-top: -4px;

	@media screen and (min-width: 0px) {
		/* FireFox fix */
		@-moz-document url-prefix() {
			* {
				min-width: unset;
				min-height: unset;
			}
		}
	}

	.form-group {
		padding-bottom: 0;
		margin-bottom: 0;

		&:not(:first-child) {
			padding-left: 8px;
		}

		&.match-words {
			max-width: 110px;
		}

		&.sort-by {
			width: 100%;
		}

		&.sort-order {
			max-width: 95px;
		}
	}

	.control-label {
		text-transform: uppercase;
		text-align: left;
		font-size: 12px;
		font-weight: bold;
		letter-spacing: 1px;
		padding-top: 0;
		font-family: "Roboto Condensed",sans-serif;
		white-space: nowrap;
		margin-bottom: 0;
		color: #000;
		opacity: .9;
	}

	.form-control {
		display: block;
		width: 100%;
	}

	.btn-group-square {
		margin-bottom: 0;
	}

	.btn-group {
		display: flex;
		align-items: flex-start;
		flex-wrap: nowrap;
	}

	.btn {
		width: 50%;
	}
}

@media (max-width: $screen-sm) {
    .search-bar {
        flex-direction: column;
        margin: 10px 15px 20px 13px;
    }
    .browse-calendar-container {
        height: 50px;
        flex: 1 1;
        justify-content: start;
        margin: 10px 0 0 0;
    }

	.search-options-container {
		margin: 10px 15px 5px 5px;

		@media (max-width: $screen-sm) {
			margin: 10px 2px 5px 2px;
		}
	}
}

@media (max-width: $screen-note) {
    .search-bar {
        flex-direction: column;
    }
    .search-input {
        font-size: 14px;
        padding: 0 0 0 35px;
    }
    .search-container {
        > input {
            padding: 0 0 0 35px;
        }
        > a {
            width: 74px;
            height: 40px;
            flex: 0 0 74px;
            font-size: 12px;
        }
        .icon {
            font-size: 25px;
            margin: 0 -25px 0 0;
        }
    }

    .browse-calendar-container {
        height: 40px;
        flex: 1 1;
        justify-content: start;
        margin: 10px 0 0 0;
        > a {
            padding: 5px 10px;
            font-size: 12px;
        }
        span {
            font-size: 17px;
        }
    }
}

@media (max-width: $screen-xtn) {
    .search-container > input {
        padding: 0 0 0 31px;
    }
}