
.paging_simple_numbers {
	display: inline-block;
	margin-top: 0px;
}
.dataTables_info {
	display: inline-block;
}
.paginate_button {
	display: inline-block;
	padding: 5px 12px;
	margin-right: 2px;
	text-decoration: none;
	color: #555555;
	cursor: pointer;
}
.paginate_button:hover, paginate_button:focus {
	font-weight: bold;
	color: black;
	border-top: 4px solid #1d87e4;
}
.current {
	font-weight: bold;
	color: black;
	background-color: white; 
	border-top: 4px solid #1d87e4;
}
.previous {
	display: inline-block;
	visibility: hidden;
	margin-left: -50px;
	margin-right: 0px;
	padding-bottom: 4px;
	-webkit-transform: rotate(180deg) !important; -moz-transform: rotate(180deg) !important; -o-transform: rotate(180deg) !important; -ms-transform: rotate(180deg) !important; transform: rotate(180deg) !important;
}
.previous:hover {
	font-weight: normal;
	padding-top: 1px;
}
.previous:before {
	font: normal normal normal 16px/1 'viglobal';
	content: '\31';
	visibility: visible;
}

.next {
	display: inline-block;
	visibility: hidden;
	margin-left: 0px;
}
.next:hover {
	font-weight: normal;
}
.next:before {
	font: normal normal normal 16px/1 'viglobal';
	content: '\31';
	visibility: visible;
}

.top {
	display: flex;
	align-items: stretch;
}

.dataTables_info {
	flex-grow: 4;
	opacity: .9;
}
.dataTables_paginate {
	float: left;
	margin-top: -10px;
}
.dataTables_length {
    padding-top: 5px;
    font-weight: normal;
    text-align: right;

    select {
        @media (max-width: $screen-xs) {
            border: #ccc solid 1px;
            border-radius: 3px;
            padding: 4px 2px;
        }
    }
}

.dataTables_length > label {
    font-weight: normal;
    position: relative;
    top: -6px;

    @media (max-width: $screen-xs) {
        font-size: $font-size-xsmall;
        top: 8px;
        float: left;
    }
}

.dataTables_search_wrapper > .vi {
	position: absolute;
	left: 8px;
	top: calc(50% - 0.5em);
	color: #007dc6;
	font-size: 18px;
}
.dataTables_search_wrapper > input {
    opacity: 1;
    font-size: 16px;
    font-style: normal;
    color: #222;
    -webkit-flex: 1 1 100%;
    -webkit-box-flex: 1 1 100%;
    -moz-flex: 1 1 100%;
    -moz-box-flex: 1 1 100%;
    flex: 1 1 100%;
    border: 1px #ccc solid;
    border-radius: 37px;
    padding: 0 0 0 50px;

    &:focus {
        box-shadow: 0 5px 10px 0 rgba(0,0,0,0.15);
        outline: none;
        transition: all 0.3s;
        border-color: #007dc6;
    }


    & + i.vi {
        margin: 0 -36px 0 6px;
        font-size: 26px;
        color: #00649e;
        transform: rotate(90deg);
    }
}
.dataTables_search_wrapper {
    position: relative;

    .form-control {
        height: 47px;
        box-shadow: none;
    }
}

.dataTables_search {
    margin-bottom: 16px;
    padding-left: 30px;
}

.dataTables_paginate {
    @media (max-width: $screen-xs) {
        border-top: 2px solid #444 !important;
        width: 100%;
    }
}