@import 'mixins/alerts';

.alert-container {
	padding: 0 20px 5px 20px !important;

	@media (max-width: $screen-sm) {
		padding-left: 15px !important;
		padding-right: 15px !important;
	}
}

.alert:empty {
	display: none;
}

.alert {
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 4px;
    border-radius: 4px;
    padding: 5px 0;
    display: table;
    width: 100%;
    line-height: 24px;
    overflow: hidden;
    font-size: $font-size-small; // 13px
    color: #333 !important;

    @media (max-width: $screen-xs) {
        line-height: 16px;
    }

    &:not(:empty) {
        &::before {
            font-family: "viglobal";
            font-size: ceil(($font-size-base * 1.5));
            display: table-cell;
            vertical-align: middle;
            padding-left: 15px;
            width: 50px;
        }
    }

    .alert-link {
        display: table-cell;
        text-align: right;
        vertical-align: middle;
        padding: 0 20px;
        font-size: ceil(($font-size-base * 1.4));
    }
}

.alert-icon-jumbo {
    &:not(:empty) {
	    &::before {
            font-family: "viglobal";
            font-size: ceil(($font-size-base * 2.5));
            display: table-cell;
            vertical-align: middle;
            padding-left: 15px;
            width: 60px;
        }
    }
}

.alert-success {
  @include alert-variant($alert-success-bg, $alert-success-border, $alert-success-text);

    &:not(:empty) {
	    &::before {
            content: "\26"; 
            color: $alert-success-border;
        }
    }

    .alert-link > a {
        color: $alert-success-border;
    }

    .alert-link > a:hover {
        color: darken($alert-success-border, 5%);
    }

    > strong {
        color: $alert-success-border;
    }
}

