.user-registration-container {
    display: flex;
    height: 100%;
    border-radius: 6px;
    background-color: #fff;
    box-shadow: 0 5px 15px 0 rgba(0,0,0,.1);
    background: #fff;
    margin: 20px;

    @media (max-width: $screen-sm) {
        display: block;
        margin: 20px 5px;
    }

    h1 {
        line-height: 60px;
        color: #222222;
        font-weight: bold;

        @media (max-width: $screen-sm) {
            font-size: 25px;
            line-height: 30px;
            color: #222222;
            font-weight: 400;
        }
    }

    .event-info {
        border-radius: 6px 0px 0px 6px;
        width: 20%;
        min-width: 245px;
        background-color: #007dc6;
        display: flex;
        flex-wrap: wrap;
        align-content: space-between;

        @media (max-width: $screen-sm) {
            width: 100%;
            border-radius: 6px 6px 0px 0px;
            padding: 0px 20px;
        }

        > .info-card-container {
            padding: 0;

            @media (max-width: $screen-sm) {
                min-height: 80px;
            }

            .info-card-header-details-item {
                width: 100%;

                @media (max-width: $screen-sm) {
                    width: unset;
                }

                a {
                    display: inline-block;
                    border: 1px solid rgba(255,255,255,0.3);
                    padding: 5px 12px 4px;
                    border-radius: 50px;
                    margin-left: -22px;
                    color: #fff;
                    cursor: pointer;
                    margin-top: 10px;
                    text-transform: uppercase;
                    font-family: "roboto condensed";
                    letter-spacing: 1px;

                    &:hover {
                        border: 1px solid rgba(255,255,255,1);
                        background: #fff;
                        color: #007dc6 !important;
                        box-shadow: 0 2px 5px 0 rgba(0,0,0,0.15);
                        transform: translateY(-1px);
                        transition: all 0.2s;
                    }
                }
            }
        }

        .footer-container {
            padding: 20px 20px 0 20px;
            width: 100%;
            display: none;

            @media (max-width: $screen-sm) {
                padding: 0px 5px 0 5px;
            }

            > .event-info-footer {
                border-top: 1px solid rgba(255,255,255,.2);

                .event-location {
                    padding-top: 8px;
                    color: #fff;
                    font-size: 12px;
                    font-weight: 100;
                    letter-spacing: 1px;
                    line-height: 20px;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    width: 100%;
                    display: none;

                    &:before {
                        float: left;
                        color: #fff;
                        font-family: "viglobal";
                        font-size: 15px;
                        left: 0;
                        content: "?";
                        padding-right: 5px;
                    }
                }

                .event-footer-stats {
                    display: flex;

                    .event-info-no {
                        height: 100%;
                        width: 50%;
                        text-align: center;
                        padding: 15px 5px 0 5px;

                        .badge-info {
                            background-color: #111;
                        }

                        > .badge-circle {
                            padding: 10px 5px;
                            border-radius: 50px;
                            min-width: 36px;
                            display: inline-block;
                        }

                        > span {
                            display: block;
                            font-size: 11px;
                            color: #ffffff;
                            line-height: 30px;
                            padding-bottom: 10px;
                        }

                        .infinity {
                            line-height: 12px;
                            vertical-align: middle;
                            font-size: 24px;
                            max-height: 14px;
                            display: inline-block;
                            margin-top: -10px;
                        }
                    }
                }
            }
        }
    }

    .arrow-right {
        width: 30px;
        height: 40px;
        background-color: #007dc6;
        margin-left: -25px;
        margin-top: 30px;
        transform: scaleY(1.6) rotate(45deg);

        @media (max-width: $screen-sm) {
            margin-top: -25px;
            margin-left: 15px;
        }
    }

    .user-registration {
        width: 70%;
        flex-grow: 1;
        padding: 10px 25px 12px 25px;

        @media (max-width: $screen-md) {
            width: 100%;
        }

        .label {
            padding: .3em .6em .2em;
            font-weight: 400;
            letter-spacing: 0.5px;
        }
    }
}
