
section.calendar-panel-container {
    > div, > ul {
        * {
            box-sizing: border-box;
        }

        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        -webkit-flex-flow: wrap;
        -ms-flex-flow: wrap;
        flex-flow: wrap;
        font-size: 0;
        margin-bottom: 0;

        &.calendar-nav {
            display: flex;
            justify-content: space-between;
            align-content: center;

            .calendar-nav-panel {
                display: flex;
            }

            div.panel-option:first-child {
                float: left;
                width: 272px;
            }

            div.panel-option:last-child {
                float: right;
                width: 272px;
                text-align: right;
            }

            a {
                font-size: 24px;
                margin: 0 15px;

                .icon {
                    vertical-align: text-top;
                }
            }
        }

        .calendar-title-month, .calendar-title-year {
            font-size: 24px;
            font-style: normal;
            font-stretch: normal;
            letter-spacing: normal;
            color: #222222;
        }

        .calendar-title-month {
            font-weight: 900;
            text-transform: uppercase;
            margin-right: 7px;
        }

        .calendar-title-year {
            font-weight: 300;
        }

        time, li {
            -webkit-box-flex: 1;
            -webkit-flex: 1;
            -ms-flex: 1;
            flex: 1;
            position: relative;
            font-size: 1.8vw;
            line-height: 0;
            border-bottom: 1px solid #dddddd;
            -webkit-transition: .35s opacity;
            transition: .35s opacity;

            &.blank {
                min-height: 7vw;
            }

            .event-date-mmm-d, .event-date-dddd {
                display: none;
            }

            .event-date-minimum {
                display: inline;
                font-family: "Times";
                font-style: italic;
                font-size: 18px;
                margin-left: 2px;
            }

            &.time5 {
                -webkit-flex: 1 1 20.0000%;
                min-width: 20.0000%;
                max-width: 20.0000vw;
            }

            &.time6 {
                -webkit-flex: 1 1 16.6667%;
                min-width: 16.6667%;
                max-width: 16.6667vw;
            }

            &.time7 {
                -webkit-flex: 1 1 14.2857%;
                min-width: 14.2857%;
                max-width: 14.2857vw;
            }

            &.lastMonth, &.nextMonth, &.weekend {
                a {
                    opacity: 0.4;
                }
            }
            
            &.weekend {
                background-color: #eee;

                .registered, .in-progress, .not-open-registered, .open-registered {
                    border: #fff 1px solid;
                }

                .open-registered {
                    border-left: #57b756 3px solid;
                }

                .not-open-registered {
                    &:hover {
                        border: #fff 1px solid;
                    }
                }
            }

            a.month-year {
                display: inline-block;
                position: inherit;
                border-bottom: none;
                outline: none;
                width: 7px;
                height: 21px;
                font-family: Roboto;
                font-size: 12px;
                font-weight: bold;
                font-style: normal;
                font-stretch: normal;
                line-height: 1.75;
                letter-spacing: normal;
                color: #333333;
                padding-left: 8px;
                padding-top: 5px;
            }

            .registered, .in-progress, .open-registered, .not-open-registered, .calendar-holiday {
                font-family: Roboto;
                font-size: 12.5px;
                font-weight: bold;
                font-style: normal;
                font-stretch: normal;
                letter-spacing: normal;
                padding: 8px 10px;
                line-height: 16px;
                border: #f0f0f0 1px solid;
                margin: 4px 10px 6px;
                border-radius: 5px;
                font-weight: 500;

                .register {
                    display: none;
                }
            }

            .registered, .in-progress {
                color: #ffffff;

                &:hover {
                    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .05);
                    transform: translateY(-1px);
                    transition: all .3s;
                }

                a {
                    color: #ffffff;
                }

                .duration {
                    color: #ffffff;
                }
            }

            .registered {
                background-color: #57b756;
            }

            .in-progress {
                background-color: #007dc6;
            }

            .not-open-registered {
                color: #333333;
                background: repeating-linear-gradient(135deg, #fff, #fff 4px, #f6f6f6 4px, #f6f6f6 8px);

                &:hover {
                    background: repeating-linear-gradient(135deg, #fff, #fff 4px, #fff 4px, #fff 8px);
                    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .05);
                    transform: translateY(-1px);
                    transition: all .3s;
                    border: #ddd 1px solid;
                }

                .duration {
                    color: #333333;
                }
            }

            .open-registered {
                color: #333333;
                border-left: #57b756 3px solid;
                background: #fff;

                &:hover {
                    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .05);
                    transform: translateY(-1px);
                    transition: all .3s;
                    background: #fff;
                }

                .duration {
                    color: #333333;
                }
            }

            .calendar-holiday {
                color: #333333;
                font-size: 14px;
                padding: 8px 10px;
                line-height: 16px;
                border: none;
                margin: -10px 10px 6px -1px;
                font-weight: 400;
                font-family: "Times";
                font-style: italic;

                & + .group-name {
                    margin-top: 0 !important;
                }
            }

            .calendar-holiday::before {
                font-family: "viglobal";
                content: "O";
                font-size: 15px;
                vertical-align: sub;
                color: #00aa00;
                font-style: normal;
                vertical-align: bottom;
            }

            .watchlist {
                position: absolute;
                margin: -2px 7px 0 0;
                right: 0;
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 0 12px 12px 0;
                border-color: transparent #ee9c19 transparent transparent;
            }

            .duration {
                font-size: 14px;
                opacity: 0.7;
                font-weight: normal;
                display: none;
            }

            .group-name {
                font-weight: 400;
                font-family: "roboto condensed";
                font-size: 10px;
                font-style: normal;
                font-stretch: normal;
                line-height: 1.4;
                letter-spacing: 2px;
                color: #aaaaaa;
                text-transform: uppercase;
                margin: 0 0 0 10px;
            }

            .group-name-2nd {
                display: block;
                margin: 20px 0 0 0px;
                font-weight: 400;
                font-family: "roboto condensed";
            }
        }

        [data-vi-filter="eventDetail"] {
            padding: 8px 0 1px;
            color: #666;
            font-family: "roboto condensed";
            letter-spacing: 0.2px
        }

        [data-vi-filter="eventLocation"] {
            padding: 0;
            color: #666;
            font-family: "roboto condensed";
            letter-spacing: 0.2px
        }

        .registered, .in-progress {
            [data-vi-filter="eventDetail"], [data-vi-filter="eventLocation"] {
                color: #fff;
                opacity: 0.85;
            }
        }
    }

    > div {
        time {
            padding-bottom: 20px;

            .event-date-minimum, a.month-year {
                position: sticky;
                top: 35px;
                display: block !important;
                height: auto;
                width: 100%;
                z-index: 1;
                padding-bottom: 6px;
                background: -moz-linear-gradient(top, rgba(255,255,255,1) 60%, rgba(255,255,255,0) 100%);
                background: -webkit-linear-gradient(top, rgba(255,255,255,1) 60%,rgba(255,255,255,0) 100%);
                background: linear-gradient(to bottom, rgba(255,255,255,1) 60%,rgba(255,255,255,0) 100%);
                filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#ffffff',GradientType=0 );
            }

            &.weekend {
                .event-date-minimum, a.month-year {
                    position: sticky;
                    top: 35px;
                    display: block !important;
                    height: auto;
                    width: auto;
                    background: -moz-linear-gradient(top, rgba(238,238,238,1) 60%, rgba(238,238,238,0) 100%);
                    background: -webkit-linear-gradient(top, rgba(238,238,238,1) 60%,rgba(238,238,238,0) 100%);
                    background: linear-gradient(to bottom, rgba(238,238,238,1) 60%,rgba(238,238,238,0) 100%);
                    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#eeeeee', endColorstr='#eeeeee',GradientType=0 );
                }
            }
        }
    }

    > ul {
        border-top: 2px solid #999;
        border-bottom: 2px solid #999;
        list-style-type: none;
        padding: 0;
        position: sticky;
        top: 0;
        height: auto;
        z-index: 1;
        background: #fff;

        li {
            width: 25px;
            height: 31px;
            opacity: 0.4;
            font-family: Roboto;
            font-size: 12px;
            font-weight: bold;
            font-style: normal;
            font-stretch: condensed;
            line-height: normal;
            letter-spacing: 1.2px;
            text-transform: uppercase;
            color: #333333;
            display: flex;
            flex-direction: row;
            align-items: center;
            padding-left: 7px;
        }
    }
}

.popup-month-year {
    margin-top: 10px;

    ul {
        list-style-type: none;
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        -webkit-flex-flow: wrap;
        -ms-flex-flow: wrap;
        flex-flow: wrap;
        padding: 0 0 10px 0;
        border-bottom: dotted 2px #eeeeee;

        li {
            -webkit-flex: 1 1 25.0000%;
            min-width: 25.0000%;
            max-width: 25.0000vw;
            line-height: 25px;
            border: #fff 3px solid;
            font-family: "roboto condensed";
            font-size: 14px;
            text-transform: uppercase;
            color: #333333;
            opacity: 0.4;
            text-align: center;
            padding: 0;
            margin: 0;
            cursor: pointer;


            &.currentMonth, &.selectedMonth {
                color: #ffffff;
                border-radius: 16px;
                opacity: 1;
                border: #fff 3px solid;
                font-weight: bold;
            }

            &.currentMonth {
                background-color: #007dc6;
            }

            &.selectedMonth {
                background-color: #68c100;
                font-weight: bold;
            }
        }
    }

    .calendar-title-year {
        font-size: 20px;
        font-weight: bold;
        color: #000000;
    }

    .calendar-nav {
        display: flex;
        justify-content: center;
        align-content: center;
        padding-bottom: 10px;
        padding-top: 5px;

        > a {
            font-size: 20px;
            margin: 0 15px;
            color: #1d87e4;
            text-decoration: none;

            .icon {
                vertical-align: text-top;
            }
        }
    }

    .form-footer {
        padding: 10px 0 0 0;

        a {
            color: #fff;

            &.btn {
                line-height: 25px;
                height: 28px;
                min-width: 66px;
                border: none;
                text-transform: uppercase;
            }

            &.btn-default {
                color: #777777;
                font-size: 14px;
                font-weight: 500;
            }
        }
    }
}


.popup-event-detail {
    max-width: 500px;
    color: #222;
    padding: 0 10px 0 10px;

    div {
        font-family: Roboto;
        font-weight: normal;
        font-size: 13px;
        width: 100%;
        display: inline-block;
        line-height: 18px;
        padding-bottom: 3px;
    }

    h2 {
        font-weight: bold;
        color: #222;
        margin: 0;
        font-family: Roboto;
        padding: 30px 0 8px;
        line-height: 20px;
        font-size: 16px;
        letter-spacing: 0;

        > span:first-child {
            margin-left: 5px;
        }

        .vi-star {
            color: #ee9c19;
            vertical-align: sub;
            margin-left: 10px;
        }
    }

    .badge-info {
        position: absolute;
        top: 9px;
        left: 25px;
        border-radius: 2px;
        font-family: Roboto;
        font-size: 12px;
        font-weight: normal;

        .label {
            border-radius: 0 0 3px 3px;
            padding: 6px 10px;
            font-weight: 500;
            font-size: 10px;
            letter-spacing: 0.5px;
        }
    }

    .badge-type {
        position: absolute;
        bottom: 50px;
        right: 20px;
        color: #cccccc;
        font-size: 24px;

        .vi {
            display: inline;
        }

        span {
            display: none;
        }
    }

    .icon-panel {
        display: inline-block;
        white-space: normal;

        .vi {
            font-size: 16px;
            padding: 0 4px;
            margin-bottom: 2px;
        }
    }

    .icon-panel, .icon-credits {
        margin: 0 1px;
        font-size: 11px;
        text-shadow: none;
        height: auto;
        padding: 0 0 8px 0;
        color: #888;

        > span {
            margin: 0;
            border-radius: 2px;
            font-weight: normal;
            background-color: #ffffff;
            padding: 2px 15px 3px 0;
            margin-left: -2px;
            font-family: "Roboto Condensed";
            font-size: 13px;
            letter-spacing: 0.5px;
            display: inline-block;
        }

        .vi {
            font-size: 15px;
            color: #007dc6;
            vertical-align: middle;
            padding: 0 3px 0 0;
        }

        .icon-location {
            white-space: normal;
        }
    }

    .icon-credits {
        margin: 10px 0 0 0;
        font-size: 10px;
        letter-spacing: 1px;
        font-family: "Roboto condensed";
        padding: 13px 0 4px 0;
        color: #aaa;
        border-top: #e5e5e5 1px dashed;

        .vi {
            padding: 0 3px 0 0;
            display: none;
        }
    }

    .register-footer {
        font-family: "roboto condensed";
        letter-spacing: 1px;
        margin: 20px -5px 0 0;
        float: right;

        > span {
            > a:first-child {
                float: left;
                padding-left: 0;
            }
        }

        hr {
            margin: 0px -18px 0 -28px;
        }

        .link-button {
            padding: 10px 0 10px 25px;
            display: inline-block;
            font-size: 11px;
            margin: 0;
            float: right;
        }
    }
}

.tooltipster-sidetip.tooltipster-shadow.tooltipster-shadow-customized .tooltipster-box {
    margin-top: 6px;
    margin-bottom: 0px;
    border: solid 1px #dedede;
}

.tooltipster-sidetip.tooltipster-shadow.tooltipster-shadow-customized .tooltipster-content {
	padding-bottom: 0px;
	margin-bottom: 0px;
}


.tooltipster-sidetip.tooltipster-shadow .tooltipster-box {
    box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.15);
}

.tooltipster-sidetip.tooltipster-top .tooltipster-arrow {
    bottom: -5px;
}

.tooltipster-sidetip.tooltipster-top {
    transform: translateY(-8px);
}
.tooltipster-sidetip.tooltipster-bottom {
    transform: translateY(4px);
}

.tooltipster-sidetip.tooltipster-bottom .tooltipster-arrow {
    top: 1px;
}

.tooltipster-sidetip.tooltipster-bottom.tooltipster-shadow .tooltipster-box {
    box-shadow: 0 -2px 25px 0 rgba(0, 0, 0, 0.15);
}

.tooltip_templates {
	display: none;
}

.event-mobile-tooltip {
    display: none;
    cursor: pointer;
}
.icon-location {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    }
.event-desktop-tooltip {
    display: block;
    cursor: pointer;

    div {
        font-weight: normal;
    }

}

	@media all and (max-width: $screen-sm) {
        section.calendar-panel-container {
            padding-top: 10px;

            > div {
                &.calendar-nav {
                    border-bottom: solid 1px #000000;
                    border-top: dashed 1px #eee;
                    flex-direction: column;
                    position: relative;
                    margin-top: 30px;

                    div.panel-option:first-child {
                        position: absolute;
                        bottom: 75px;
                    }

                    div.panel-option:last-child {
                        display: none;
                    }

                    .calendar-nav-panel {
                        margin-top: 20px;
                    }
                }

                time {
                    min-width: 20%;
                    max-width: 20vw;
                    font-size: 3vw;
                    border-bottom: dashed 2px #eeeeee;
                    padding: 0 0 20px 0;

                    &:nth-last-child(2) {
                        border-bottom: none !important;
                        padding-bottom: 0;
                    }

                    &.blank {
                        display: none;
                    }

                    .event-date-mmm-d, .event-date-dddd {
                        display: inline;
                        font-size: 16px;
                        font-family: Roboto;
                        text-transform: uppercase;
                    }

                    .event-date-mmm-d {
                        color: #222222;
                        font-weight: bold;
                    }

                    .event-date-dddd {
                        color: #aaaaaa;
                        font-weight: 300;
                        letter-spacing: 1px;
                        margin-left: 6px;
                    }

                    .event-date-minimum {
                        display: none !important;
                    }

                    &.time5, &.time6, &.time7 {
                        -webkit-flex: 1 1 100.0000%;
                        min-width: 100.0000%;
                        max-width: 100.0000vw;
                    }

                    &.lastMonth, &.nextMonth, &.weekend {
                        a {
                            opacity: 1;
                        }
                    }

                    &.weekend {
                        background-color: #fff;
                    }

                    a {
                        margin: 16px 0 7px 0;
                        padding-left: 0;

                        &.month-year {
                            top: 0px;
                            height: 40px;
                            padding-top: 7px;
                            padding-left: 0;
                            background: -moz-linear-gradient(top, white 80%, rgba(255, 255, 255, 0) 100%);
                            background: -webkit-linear-gradient(top, white 80%, rgba(255, 255, 255, 0) 100%);
                            background: linear-gradient(to bottom, white 80%, rgba(255, 255, 255, 0) 100%);
                            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#ffffff", endColorstr="#ffffff",GradientType=0 );
                        }
                    }

                    .registered, .in-progress, .open-registered, .not-open-registered, .calendar-holiday {
                        font-size: 14px;
                        padding: 8px 12px;
                        line-height: 18px;
                        margin: 5px 0px 8px;
                        border-radius: 5px;
                        font-weight: 500;
                    }

                    .registered, .in-progress {
                        .event-mobile-tooltip div {
                            color: #fff;
                            opacity: 0.85;
                        }
                    }

                    .open-registered {
                        .register {
                            display: block;
                            margin-top: 5px;

                            .btn {
                                font-family: "roboto condensed";
                                letter-spacing: 1.4px;
                                text-transform: uppercase;
                            }
                        }
                    }

                    .calendar-holiday {
                        padding: 8px 0;
                        border: none;
                        margin: -10px 10px 6px -1px;
                        font-weight: 400;
                        font-family: "Times";
                        font-style: italic;
                    }

                    .calendar-holiday::before {
                        font-size: 20px;
                    }

                    .watchlist {
                        margin: -12px 0 0 0;
                        border-width: 0 24px 24px 0;
                    }

                    .duration {
                        display: block;
                    }

                    .group-name {
                        margin: 0;
                    }
                }

                .event-not-found {
                    color: #aaaaaa;
                    font-weight: 300;
                    font-size: 15px;
                    letter-spacing: 1px;
                    margin-left: 6px;
                }
            }

            > ul {
                display: none;
            }
        }

        .event-mobile-tooltip {
            display: block;

            span, div {
                font-weight: normal;
                padding: 0;
                color: #666;
                font-family: "roboto condensed";
                letter-spacing: 0.2px;

                &:first-child {
                    padding-top: 8px;
                }
            }

            .vi {
                font-size: 14px;
                padding: 0 3px 0 0;
            }
        }

        .event-desktop-tooltip {
            display: none;
        }

		.popup-event-detail {
			max-width: 100%;
			margin-left: -10px;

			.badge-type-icon {
				padding-left: 20px;
				margin: 20px 0 20px -18px;
				border-style: solid;
				border-width: 0 0 0 3px;
				border-color: #007dc6;
			}

			h2 {
				margin: 1px 10px 1px 0px;
				line-height: 1;
			}

			.badge-info, .badge-type {
				position: static;
			}

			.badge-type {
				.vi {
					display: none;
				}

				span {
					font-size: 10px;
					font-weight: normal;
					line-height: 0.9;
					letter-spacing: 0.91px;
					display: block;
					text-transform: uppercase;
				}
			}

			.icon-panel {
				height: auto;
				display: flex;
				flex-direction: column;

				> span {
					padding: 3px 5px 5px 0;
				}
			}

			.register-footer {

				> span {
					display: flex;
					justify-content: space-between;

					> a {
						color: #007dc6;
					}

					> a:nth-child(2) {
						order: 3;
					}

					> a:first-child {
						float: none;
						padding-left: 0;
					}

					hr {
						margin: 0 -12px;
					}

					.link-button {
						padding: 5px 0 5px 25px;
						display: inline-block;
						font-size: 11px;
						margin: 0;
						float: none;
						text-decoration: none;
					}
				}

				.dialog-container {
					padding: 0 20px;
				}
			}
		}

		.popup-month-year {
			margin-top: 40px;

			.form-footer {
				display: flex;
				justify-content: space-between;
			}
		}
	}

	@media (max-width: $screen-xs) {
		.popup-event-detail {
			padding: 0 30px;
		}
	}
