[vi-filter|="main"] {
    overflow: hidden;
}

[vi-filter|="main"] .form-horizontal .add-remove-container .form-section {
    border-left: none;
}

[vi-filter|="main"] .add-remove-container {
    padding-left: 15px;
}

[vi-filter|="main"] .add-remove-container #inputSearch {
    margin-bottom: 5px;
}

[vi-filter|="main"] .col.col-md-8 {
    -webkit-transition: all .3s ease;
}

[vi-filter|="main"] [vi-filter|="slide-block"] {
    position: relative;
    left: -400px;
    -webkit-transition: all .3s ease;
}

[vi-filter|="main"] label.filter-toggle {
    cursor: pointer;
    margin-left: 5px;
    margin-top: 5px;
}

[vi-filter|="additional-filters"] {
    padding-left: 0;
    background: white;
    height: auto;
    display: block;
    box-shadow: 0 -5px #fff, 0 5px #fff, 14px 0 22px -22px rgba(0, 0, 0, 0.5);
}

[vi-filter|="additional-filters"] {
    width: 400px;
    margin-left: -30px;
    opacity: 0;
    -webkit-transition: all .3s ease;
}

[vi-filter|="additional-filters"] label.filter-toggle {
    cursor: pointer;
    margin-left: 5px;
    margin-top: 15px;
}

[vi-filter|="additional-filters"] .form-horizontal .form-section {
    border-left: none;
    padding-left: 0;
}

[vi-filter|="additional-filters"] .form-horizontal .form-edit {
    overflow: hidden;
}

[vi-filter|="grid"] .flex {
    display: flex;
    width: 100%;
}

[vi-filter|="grid"] .flex div:nth-child(1) {
    flex: 0.7;
}

[vi-filter|="grid"] .flex div:nth-child(2) {
	flex: 0.3;
}

[vi-filter|="grid"] .flex div:nth-child(3) {
	flex: 0.5;
}

[vi-filter|="grid"] .flex div:nth-child(4) {
	flex: 0 0 85px;
	text-align: right;
}

[vi-filter|="grid"] .flex .row .col {
    display: inline-block;
}

[vi-filter|="grid"] .form-section .form-group .row .col {
    padding-right: 5px;
}

[vi-filter|="grid"] .form-section .form-group .row .col .row .col {
    padding: 0;
}

[vi-filter|="grid"] .form-section .form-group .row .col .row .col a {
    margin-top: -4px;
    margin-bottom: 4px;
}

[vi-filter|="main"] [vi-filter|="toggle"] {
    display: none;
}

[vi-filter|="main"] [vi-filter|="toggle"]:checked + [vi-filter|="slide-block"] {
    left: 30px !important;
}

[vi-filter|="main"] [vi-filter|="toggle"]:checked + [vi-filter|="slide-block"] [vi-filter|="additional-filters"] {
    opacity: 1;
}

[vi-filter|="main"] .search-filter-toggle {
    display: flex;
    width: 100%;
}

[vi-filter|="main"] .search-flex {
    flex: 1;
}


@media screen and (max-width:$screen-xlg) {
    [vi-filter|="main"] [vi-filter|="slide-block"] {
        position: relative;
        left: 0px;
        -webkit-transition: all .3s ease;
    }

    [vi-filter|="main"] .col.col-md-8 {
        width: 100%;
        padding-left: 0;
        max-width: 100% !important;
        -webkit-transition: all .3s ease;
    }

    [vi-filter|="main"] [vi-filter|="toggle"]:checked + [vi-filter|="slide-block"] {
        left: 0px !important;
    }

    [vi-filter|="additional-filters"] {
        margin-left: -400px;
        box-shadow: none;
        height: 200px;
        display: none !important;
    }

    [vi-filter|="main"] .add-remove-container {
        padding-left: 0;
    }

    [vi-filter|="main"] .form-horizontal .add-remove-container .form-section {
        padding-left: 0;
    }

	[vi-filter|="main"] [vi-filter|="toggle"]:checked + [vi-filter|="slide-block"] [vi-filter|="additional-filters"] {
		opacity: 1;
		width: 100%;
		height: 100%;
		margin-left: 0px;
		border-bottom: #ccc dashed 1px;
		margin-bottom: 20px;
		display: block !important;
	}

    [vi-filter|="additional-filters"] .form-horizontal.form-edit > div {
        display: inline-block;
        vertical-align: top;
        padding-right: 15px;
        height: auto;
    }

    [vi-filter|="additional-filters"] .form-horizontal.form-edit > div:nth-child(1) {
        width: 32%;
    }

	[vi-filter|="additional-filters"] .form-horizontal.form-edit > div:nth-child(2) {
		width: 25%;
		font-size: 13px;
		
		&[vi-filter|="grid"] {
			width: 60%;
		}
	}

    [vi-filter|="additional-filters"] .form-horizontal.form-edit > div:nth-child(3) {
        width: 40%;
        padding-right: 0;
    }

    [vi-filter|="additional-filters"] .form-horizontal.form-edit div.form-section {
        border-left: solid 4px #007dc6;
        padding-left: 10px;
    }

    [vi-filter|="additional-filters"] label.filter-toggle {
        display: block;
        position: absolute;
        right: 0;
        top: -10px;
    }
}

@media screen and (max-width:$screen-lg) {
    [vi-filter|="additional-filters"] {
        margin-left: -400px;
        box-shadow: none;
        height: auto;
    }

    [vi-filter|="additional-filters"] .form-horizontal.form-edit > div {
        width: 100% !important;
        padding-right: 0;
        height: auto;
    }

    [vi-filter|="additional-filters"] .form-horizontal .form-section {
        margin-bottom: 15px;
    }

    [vi-filter|="additional-filters"] .form-horizontal.form-edit > div:nth-child(3) {
        margin-bottom: 15px;
    }

    [vi-filter|="additional-filters"] label.filter-toggle {
        display: block;
        position: absolute;
        right: 0;
        top: 5px;
    }
}


@media screen and (max-width: $screen-md) {
    [vi-filter|="grid"] .form-section .row > .col:not(:first-child) {
        margin-top: 0px !important;
    }
}
