}


.radio-box {
	display: block;
	padding-left: 0px;

	&.required {
		padding-left: 0px !important;
		border-left: hidden !important;

		input + label {
			//border-left: 3px solid red !important;
		}
	}

	input + label {
		font-size: 12px;
		font-weight: normal;
		color: #333333;
		border: 1px solid #e0e0e0;
		border-radius: 2px;
		min-width: 150px;
		margin-right: 8px;
	}

	input[type="radio"] {
		display: none;

		+ label::before {
			content: "A";
			font-family: viglobal;
			display: inline-block;
			padding: 8px 4px;
			border-radius: 2px 0px 0px 2px;
			background-color: #e0e0e0;
			margin: -1px 8px -1px -1px;
			color: #fff;
		}

		&:checked {
			+ label {
				font-weight: bold;
				border: 2px solid #58b957;
				color: #58b957;

				&::before {
					padding: 7px 3px 7px 4px;
				    background-color: #58b957;
					color: #fff;
				}
			}
		}
	}
}




td {
    .toggle {
        margin-top: 5px;
    }
}


.toggle {
    position: relative;
    display: inline-block;
    width: 32px;
    height: 18px;

    > .toggle-label {
        white-space: nowrap; 
        margin-left: 40px; 
        pointer-events : none;
    }
}

.toggle input {
    display: none;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 10px;
    width: 10px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

input:checked + .slider {
    background-color: $brand-success;
}

input:checked + .slider-primary {
    background-color: $brand-primary;
}

input:checked + .slider:before {
    -webkit-transform: translateX(14px);
    -ms-transform: translateX(14px);
    transform: translateX(14px);
}

/* Rounded sliders */
.slider.round {
    border-radius: 9px;
}

.slider.round:before {
    border-radius: 50%;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.5);
}

