@media screen and (min-width: 0px) {
    /* FireFox fix for slick-carousel */
    @-moz-document url-prefix() {
        * {
            min-width: 0;
            min-height: 0;
        }
    }
}
html, body {
  height: 100%;
}

body {
    color: #666;
}

a {
    color: #666;

    &:hover {
        color: #444;
        text-decoration: none;
    }
}

mark {
	background: rgba(187, 220, 239, 0.80);
	color: #000;
	padding: 0;
}

.site-content {
    padding: 0px;
    transition: margin-left 0s;

    .site-title-wrapper {
        background-color: #007dc6;
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
        margin: 20px 20px -20px 20px;
        padding: 13px 25px 15px 25px;

        + .panel-container {
            border-top-left-radius: 0;
            border-top-right-radius: 0;
        }

        @media (max-width: $screen-sm) {
            margin: 20px 5px -20px 5px;
            padding: 15px 25px 25px 25px;
        }

        .site-title-container {
            border-right: none;
            padding-right: 0;

            .status {
                margin: 0;
                display: inline-block;
                box-shadow: 0 2px 6px rgba(0,0,0,0.15);
            }

            .site-title-name {
                margin: 0;
                font-size: 20px;
                font-weight: 500;
                color: #fff;
                padding: 5px 0 0 0
            }
        }

        .site-title-extra-container {
            display: inline-block;
            padding: 0 0 0 15px;

            div {
                display: inline-block;
            }

            .due-date-label {
                line-height: 15px;
                padding-right: 5px;
                letter-spacing: .8px;
                font-size: 10px;
                color: #fff;
                opacity: .3;
                text-transform: uppercase;
                font-family: "Roboto";
            }

            .due-date {
                font-family: roboto;
                font-weight: 500;
                display: inline-block;
                line-height: 15px;
                letter-spacing: 1px;
                font-size: 12px;
                color: #fff;
            }
        }

        .site-title-notes {
            padding: 0;
            margin: 0 0 -10px 0;
            clear: both;
            font-size: 14px;
            line-height: 16px;
            max-width: 800px;
            color: rgba(255,255,255,0.6);
            display: inline-block;

            &:empty {
                display: none;
            }
        }
    }

    .site-subtitle {
        margin: 0 20px -15px 20px;
        font-size: 20px;
        letter-spacing: 1px;
        color: $brand-primary;
        font-family: Roboto;

        @media (max-width: $screen-sm) {
            margin: 0 4px -15px 4px;
        }
    }

    .horizontal-divider + .site-subtitle {
        margin-top: -20px;
    }

    .site-title-status {
        display: inline-block;
        padding-bottom: 3px;
        margin-right: 5px;
        margin-left: -10px;
    }

    .site-title-status-ex {
        color: #222222;
        line-height: 16px;
        font-size: 14px;
        float: right;
        padding-top: 15px;
    }

    .site-title-notes {
        margin-right: 5px;
        color: #222222;
        padding-left: 40px;
        line-height: 16px;
        font-size: 14px;
    }
}

.select2-container {
    z-index: 9999;
}

.bootstrap-datetimepicker-widget {
  z-index: 9999999;
}

.no-left-nav {
	.site-content {
		margin-left: 0;
		margin-right: 0;
	}
}

.hidden-on-load {
  display: none !important;
}

.text-muted {
	font-weight: 300;
}

.text-center { 
    text-align: center !important; 
}
.text-left { 
    text-align: left; 
}
.text-right { 
    text-align: right; 
}

.text-success {
	color: $brand-success;
}

.text-warning {
	color: $brand-warning;
}

.text-danger {
	color: $brand-danger;
}

.text-primary {
	color: $brand-primary;
}

.text-default {
	color: $text-color;
}

.inline-block { 
    display:inline-block; 
}

.img-circle {
    border: 2px solid rgba(255,255,255,0.5);
}

.chart-feedback {
    line-height: 10px;
    padding: 0;
    margin: 0;
}

td > .chart-feedback:not(:last-child) {
    margin-bottom: 2px;
}

.help-block {
    margin-top: 0;
    margin-bottom: 0;

	> .vi-hc-lg {
		vertical-align: -34%;
	}
}

input[type=text]::-ms-clear { 
	display: none; 
}

.dropdown-menu {
	min-width: 120px;

	> li > a { 
		padding: 5px 5px 5px 8px;
	}
} 

.RadEditor {
	background-color: #fff !important;
}

//
//page top
//
.page-top-container {
	margin: 0;
	padding: 0 20px 5px 20px;
	background-color: #fff;


	@media (max-width: $screen-sm) {
		height: auto;
		padding: 0px;
	}

	.page-top {
		@include clearfix;
		height: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-top: 10px;
		min-height: 80px;

		@media (max-width: $screen-sm) {
			display: inherit;
			padding: 0;
		}

		@media (max-width: $screen-xs) {
			min-height: 60px;
		}
	}

	.page-top-title {
		padding-right: 50px;
		flex-grow: 1;

		@media (max-width: $screen-sm) {
			width: 100%;
			padding: 15px 17px 0 17px;

			&:last-child {
				border-bottom-color: transparent;
			}
		}

		.page-top-status {

			.label {
				font-size: 10px !important;
				text-transform: uppercase;
				background-color: #fff;
				padding: 1px 4px 0 4px;
				border-radius: 0;
				letter-spacing: 1px;
			}

			.label-primary {
				color: $label-primary-bg !important;
				border: 1px solid $label-primary-bg !important;
				border-left: 4px solid $label-primary-bg !important;
			}

			.label-success {
				color: $label-success-bg !important;
				border: 1px solid $label-success-bg !important;
				border-left: 4px solid $label-success-bg !important;
			}

			.label-info {
				color: $label-info-bg !important;
				border: 1px solid $label-info-bg !important;
				border-left: 4px solid $label-info-bg !important;
			}

			.label-default {
				color: $label-info-bg !important;
				border: 1px solid $label-info-bg !important;
				border-left: 4px solid $label-info-bg !important;
			}

			.label-warning {
				color: $label-warning-bg !important;
				border: 1px solid $label-warning-bg !important;
				border-left: 4px solid $label-warning-bg !important;
			}

			.label-danger {
				color: $label-danger-bg !important;
				border: 1px solid $label-danger-bg !important;
				border-left: 4px solid $label-danger-bg !important;
			}
		}

		.page-top-row2 {
			position: relative;
			top: -2px;
		}

		.page-top-label1 {
			font-family: Times;
			font-size: $font-size-h3;
			font-weight: bold;
			width: 100%;
			margin-bottom: 0px;
			line-height: normal;
			color: #000;

			@media (max-width: $screen-sm) {
				font-size: 22px;
			}

			> .vi {
				vertical-align: -5px;
				padding: 0 8px;
			}
		}

		.page-top-label2 {
			font-size: $font-size-xsmall;
			padding-left: 0px;
			font-weight: 400;
			color: #444;
		}

		.page-top-label3, .page-top-label4 {
			font-size: $font-size-xsmall;
			font-weight: normal;
			color: #444444;

			&::before {
				font-family: 'viglobal';
				content: '\79';
				font-size: $font-size-large;
				color: $brand-primary;
				position: relative;
				top: 3px;
				padding: 0 3px;
			}
		}
	}

	.page-top-buttons {
		visibility: hidden;
		border-radius: 20px 0 0 20px;
		background-color: $body-bg;
		border: 1px solid darken($body-bg, 10%);
		border-right-color: $body-bg;
		margin: 0 -20px 0 0;
		padding: 0 25px 0 10px;

		@media (max-width: $screen-sm) {
			float: left !important;
			width: 100%;
			background-color: #fff;
			border: 0;
			border-radius: 0;
			margin: 0 0 0 0;
			padding: 0 7px 10px 7px;
		}

		&:has(> a) {
			background-color: red;
		}
	}

	.page-info-container {
		width: 100%;
		border-top: 1px dashed #979797;

		@media (max-width: $screen-sm) {
			padding-left: 18px;
		}

		.page-info-element {
			color: #444444;
			font-family: "Roboto";
			font-weight: bold;
			font-size: 16px;
			display: inline-block;
			padding: 15px 75px 0 0;

			.element-title {
				opacity: 0.5;
				font-size: 12px;
				display: block;
			}
		}
	}

	.alert-container {
		padding: 3px 0 !important;

		@media (max-width: $screen-xs) {
			padding: 0 15px 10px !important;
		}
	}
}


.theme-dark {
	overflow: hidden;
	padding: 0 0 100px 0;

	.panel-container {
		@media (max-width: $screen-xs) {
			padding: 0 20px 0 20px !important;
		}
	}

	> .page-top-container {
		background-color: #415562;
		padding-top: 15px;
		padding-left: 30px;
		padding-bottom: 0;

		@media (max-width: $screen-sm) {
			padding-left: 15px;
			margin-bottom: 10px;
		}

		@media (max-width: $screen-xs) {
			padding-left: 0;
			margin-bottom: 10px;
			padding-top: 3px;
		}

		&:after {
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 18px 30px 0 30px;
			border-color: #415562 transparent transparent transparent;
			content: "";
			background-color: transparent;
			position: relative;
			top: 36px;

			@media (max-width: $screen-xs) {
				top: 30px;
				margin-left: 4px;
			}
		}


		.page-top-title {
			.page-top-label1 {
				color: #fff;
			}

			.page-top-label2 {
				color: #fafafa;
			}

			.page-top-label3, .page-top-label4 {
				color: #fafafa;
			}

			.page-top-row1 {
				padding-right: 36px;
			}
		}

		.page-top-buttons {
			background-color: darken(#415562, 10%);
			border: 0px solid darken(#415562, 10%);
			padding: 0 10px 0 10px;
			margin: 35px -20px 0 0;

			@media (max-width: $screen-sm) {
				margin: unset;
			}

			> .btn-default {
				color: #fff;

				&:hover {
					color: #415562;

					> i {
						color: #415562;
					}
				}
			}

			@media (max-width: $screen-sm) {
				border: 1px solid transparent;
				background-color: transparent;

				> .btn-default {
					color: #fff;

					&:hover {
						color: #415562;

						> i {
							color: #415562;
						}
					}
				}
			}
		}
	}

	.panel-body {
		padding: 30px;

		@media (max-width: $screen-xs) {
			padding: 10px 0 0 0 !important;
		}
	}

	.form-horizontal {
		max-width: 100%;
	}

	.request-item .item-info-status {
		font-size: 14px;
		padding: 5px 12px;
		border-radius: 0 0 4px 4px;

		@media (max-width: $screen-xs) {
			font-size: $font-size-xsmall;
		}
	}
}


//
//page filter
//
.page-filter {
    list-style-type: none;
    border-radius: 6px;
    background-color: $brand-info;
    color: white;
    height: 36px;
    overflow: hidden;
    margin-bottom: 20px;

    > li {
        float: left;
        outline: none;

        &.filter-icon {
            background-color: darken($brand-info, 12%);
            margin-left: -40px;
            padding: 10px 14px;
            border-top-left-radius: 3px;
            border-bottom-left-radius: 3px;
            float: left;

            i {
                opacity: 0.7;
            }

            &.filtered {
                background-color: #007dc6;

                i {
                    opacity: 1;
                }
            }
        }

        &.filter-menu {
            cursor: pointer;
            border-top: 1px solid transparent;
            border-right: 1px solid #363636;
            padding-top: 9px;
            padding-right: 10px;
            padding-bottom: 4px;
            padding-left: 10px;

            @media (max-width: $screen-xs) {
                padding-top: 7px;
                border-right: none;
                text-overflow: ellipsis;
                width: 93%;
                white-space: nowrap;
                overflow: hidden;
            }

            &:after {
                content: "\e008";
                font-family: viglobal;
                padding-left: 7px;
                opacity: 0.5;
                padding-top: 0;
                margin-top: -2px !important;
                display: inline-block;
                vertical-align: top;
                padding-right: 2px;

                @media (max-width: $screen-xs) {
                    margin-top: 0 !important;
                }
            }

            &.open {
                background-color: darken($brand-info, 12%);
                box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.25);

                &:after {
                    content: "\7a";
                    font-family: viglobal;
                    padding-left: 7px;
                    opacity: 1;
                }
            }
        }

        > div.filter-text {
            float: left;
            text-transform: uppercase;
            line-height: 17px;
            opacity: 0.5;
            font-size: 11px;
            font-weight: 400;
            letter-spacing: 1px;
            padding-right: 20px;
        }

        > div.filter-value {
            display: inline;
            font-size: 13px;
            vertical-align: top;
            line-height: 16px;
        }

        &.filter-mobile {
            cursor: pointer;
            padding-top: 6px;
            padding-right: 10px;
            padding-bottom: 4px;
            padding-left: 10px;
            overflow: hidden;
        }

        &.mobile {
            display: none;
        }

        > div.menu-content {
            position: absolute;
            z-index: 1;
            padding-left: 0;
            opacity: 0;
            visibility: hidden;
            transition: visibility 0.5s;
        }

        > div.menu-dropdown {
            background-color: darken($brand-info, 12%);
            margin-top: 1px;
            margin-left: -10px;
            padding: 5px 10px;
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.25);
            border-bottom-left-radius: 3px;
            border-bottom-right-radius: 3px;
            z-index: 2;
            max-height: 60vh;
            overflow-y: auto;
            border-radius: 0 6px 6px 6px;

            > a {
                display: block;
                color: darken(#ffffff, 15%);
                white-space: nowrap;
                text-decoration: none;
                margin: 2px 0;
                padding: 2px 5px;
                border-radius: 4px;
                transition: background-color 0.5s, color 0.5s;

                &:hover {
                    color: #ffffff;
                    background-color: $brand-info;
                }

                &.selected {
                    color: #ffffff;
                    background: #1d87e4;
                }
            }
        }

        > div.fullscreen {
            position: fixed;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            width: 100%;
            min-height: 100%;
            overflow: auto;
            padding: 0;
            z-index: 9999;
            background-color: #ffffff;

            > div.menu-dropdown {
                border-radius: 0;
                margin-top: 1px;
                padding: 20px 20px 120px 20px;
                background-color: $brand-info;

                .radio label {
                    margin: 5px 0 5px 2px;
                    font-size: 16px;

                    &::before {
                        margin: 2px 0 0 -22px;
                        opacity: 0.3;
                        border: none;
                    }
                }


                h6 {
                    font-size: 32px;
                    font-weight: bold;
                    font-family: "Times";
                    letter-spacing: 0.5px;
                    position: sticky;
                    top: 55px;
                    padding: 12px 0px;
                    background: #555;
                    width: 100%;
                    position: -webkit-sticky;
                    z-index: 1;
                    border-bottom: #999 1px dashed;
                }

                .form-horizontal .radio {
                    padding-bottom: 20px;
                    padding-top: 10px;
                    padding-left: 22px;
                }

                .radio-success input[type=radio]:checked + label {
                    &::after {
                        background-color: #68c100;
                        margin: 2px 0 0 -22px;
                    }

                    &::before {
                        border: #68c100 1px solid;
                        background: transparent;
                        opacity: 1;
                    }
                }

                .form-footer {
                    position: fixed;
                    bottom: 20px;
                    display: block;
                    width: 100vw;
                    background: #333;
                    margin: -20px;
                    padding: 20px;
                    text-align: center;
                    box-shadow: 0 -5px 60px 0 rgba(0,0,0,0.3);
                    z-index: 2;

                    .btn {
                        width: 35%;
                    }

                    input {
                        display: inline-block;
                        margin-right: 0;

                        &[value="Apply"] {
                            float: left;
                        }

                        &[value="Cancel"] {
                            float: right;
                        }
                    }
                }
            }
        }
    }

    @media (max-width: $screen-sm) {
        margin-bottom: 0;
    }

    @media (max-width: $screen-xs) {
        font-size: 13px;
        text-overflow: ellipsis;
    }
}

.panel-separator {
    margin: 20px -25px 25px -25px;
    padding: 0;

    &:after {
        content: "";
        display: inline-block;
        border-bottom: #f0f0f0 4px solid;
        width: 100%;
    }
}

.panel-separator + .panel {
	margin-top: -15px;
}


.panel-default {
	.row {
		margin-left: 0px;
		margin-right: 0px;
	}

	.note {
		line-height: 28px;
	}

    .page-filter {
        border-radius: 0;
        background-color: $timeline-default-color;
        box-shadow: 
            inset 0px 11px 5px -10px rgba(0,0,0,0.15),
            inset 0px -11px 5px -10px rgba(0,0,0,0.15); 
        color: black;
		border-radius: 3px;

        .filter-icon {
            background-color: $timeline-darker-color;
            box-shadow: 
                inset 0px 11px 5px -10px rgba(0,0,0,0.15),
                inset 0px -11px 5px -10px rgba(0,0,0,0.15); 
            color: $timeline-default-color;
        }

        li {
            &.filter-menu {
                border-right: solid 1.5px rgba(102, 102, 102, 0.3);
                border-top: 1px solid #ddd;

                &.open {
                    color: #333;
                    background-color: #fff;
                }
            }

            > div.fullscreen > div.menu-dropdown {
                background-color: #fff;
            }
        }

        div.menu-dropdown {
            background-color: #fff;

            a {
                color: #333;
            }
        }
    }
}


//
//status
//
.status
{
    height: 8px; 
    width: 30px; 
    margin: 0 0 0 10px; 
    border-radius: 8px;

    &.status-success {
        background-color:  $brand-success;
    }
    &.status-warning {
        background-color:  $brand-warning
    }
    &.status-danger {
        background-color:  $brand-danger;
    }
    &.status-primary {
        background-color: $brand-primary;
    }
    &.status-default {
        background-color: $text-color;
    }

	> .status-text {
		@media screen {
			display: none;
		}
	}
}

.status-numeric-container {
    width: 100%;
    padding-top: 5px;

    .status-numeric
    {
        width: 40px; 
        float: left; 
        background-color: #e5eff6; 
        line-height: 30px; 
        text-align: center; 
        font-weight: bold;

        &:first-child {
            background-color: #0162a9; 
            color: #ffffff;
        }

        &.status-success {
            color:  $brand-success;
        }
        &.status-warning {
            color:  $brand-warning
        }
        &.status-danger {
            color:  $brand-danger;
        }
        &.status-primary {
            color: $brand-primary;
        }
        &.status-default {
            color: $text-color;
        }
    }
}

.blur {
    -webkit-filter: url(#blur-filter);
    filter: url(#blur-filter);
    -webkit-filter: blur(3px);
    filter: blur(3px);
    -ms-filter: blur(3px);
    filter:progid:DXImageTransform.Microsoft.Blur(PixelRadius='3');
}


//
//Real-Time Feedback
//
.rtf-header-container {
    display: flex;
    padding: 10px 0 0 0;

    @media only screen and (max-width: $screen-sm-min) {
        display: block;
    }

    > .rtf-from-container {
        display: flex;
        width: fit-content;
        overflow: hidden;
        background-color: lighten(#415562, 8%);
        margin: 5px 5px 5px 0px;
        border-radius: 6px;
        padding: 5px 20px 8px 15px;

        @media only screen and (max-width: $screen-sm) {
            display: block;
            margin-bottom: 25px;
            margin-top: 15px;
            width: 100%;
        }
    }

    > .rtf-to-container {
        display: flex;
        width: fit-content;
        overflow: hidden;
        background-color: lighten(#415562, 8%);
        border-radius: 6px;
        padding: 5px 30px 5px 30px;
        margin: 5px 5px 5px 0px;

        @media only screen and (max-width: $screen-sm) {
            display: block;
            width: 100%;
            margin: unset;
        }
    }

    > .rtf-from-container + .rtf-to-container {
        padding: 5px 20px 5px 30px;
        margin: 5px 5px 5px 15px;

        &:before {
            content: 'b';
            font-family: "viglobal";
            color: $brand-success;
            position: absolute;
            top: 5px;
            font-size: 40px;
            margin-left: -58px;

            @media (max-width: $screen-xs) {
                margin-top: 66px;
                top: 11px;
            }
        }

        @media only screen and (max-width: $screen-sm) {
            margin: unset;

            &:before {
                margin-left: 0;
                width: 90%;
                text-align: center;
                margin-top: 60px;
                -ms-transform: rotate(90deg);
                -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
            }
        }

        @media (max-width: $screen-xs) {
            padding: 5px 15px 15px 15px;
        }
    }

    > .rtf-from-container + .rtf-to-container.incomplete {
        &:before {
            content: 'c';
            color: $brand-warning;
        }
    }

    .rtf-label {
        padding-top: 5px;
        color: #ffffff;
        opacity: .4;
        text-transform: uppercase;
        font-size: $font-size-small;
        font-weight: 100;
        letter-spacing: 1.3px;

        @media only screen and (max-width: $screen-sm) {
            width: 100%;
        }
    }

    .rtf-field-icon {
        height: 22px;
        padding-top: 3px;
        display: table-cell;
        vertical-align: middle;
        padding-left: 30px;

        @media only screen and (max-width: $screen-sm) {
            padding-left: 0px;
            width: 50px;
        }

        &:first-child {
            padding-left: 0px;
        }

        i {
            color: #ffffff;
            opacity: .4;
            font-size: 22px;
            margin-right: 5px;
        }
    }

    .rtf-field-group {
        display: flex;
        padding-left: 20px;
        white-space: nowrap;

        @media only screen and (max-width: $screen-sm) {
            padding-left: 0px;
        }
    }

    .rtf-field {
        padding-top: 5px;
        vertical-align: middle;
        color: #ffffff;
        font-weight: bold;
    }

    .incomplete {
        > .rtf-field, > .rtf-field-icon > i {
            color: $brand-warning;
            opacity: unset;
        }
    }

    .rtf-label:empty + .rtf-field-icon {
        padding-left: 0px;
    }
}


.form-view {
	.request-item {
		&:hover {
			cursor: default;

			.item-status-border-bar {
				width: 0;    

				.vi-long-arrow-right {
					display: unset;
				}
			}

			.item-info-container {
				margin-left: 0;
			}
		}

		.form-section {
			border-left: solid 0px #ffffff;
			padding-left: 2px;
		}
	}
}

