.ui-widget-overlay {
	background-color: rgba(0,0,0,0.6);
    opacity: 0.6;
}

.ui-widget.ui-widget-content {
    border: 0px solid #ffffff !important;
    background-color: #ffffff;
    padding-top: 5px;
    border-radius: 5px;

    @media (max-width: $screen-xs) {
        border-radius: 0;
    }
}

.ui-dialog {
	z-index: 2000 !important;
	background-color: #ffffff;
	border-color: #ffffff;
	overflow-x: hidden;

	iframe {
		height: 10000px;
	}
}

.ui-dialog-full-height {
	overflow-y: scroll;
    min-height: 100vh;

	.ui-dialog-title {
		display: none;
	}

	button.ui-button.ui-corner-all.ui-widget.ui-button-icon-only.ui-dialog-titlebar-close {
		margin-top: -5px;
	}
}

.ui-dialog-title {
	font-size: 20px;
    font-family: "Roboto Condensed";
    font-weight: 600;
    letter-spacing: 0.5px;
    margin-bottom: 6px;
    width: 100%;
    margin-bottom: 0px;
	color: #000;
	white-space: normal !important;
}

.ui-dialog-titlebar {
	background: transparent;
    position: absolute !important;
    z-index: 99;
    width: 100%;
	border: 0;
	margin-top: 10px;
}

.ui-dialog .ui-dialog-titlebar-close {
	margin-right: 35px;
    margin-top: 5px;
}

.ui-dialog .ui-dialog-buttonpane {
	margin: -10px 0px 20px 20px;

	.btn {
		text-transform: uppercase;
		min-width: 100px;
		height: 35px;
		line-height: 30px;
		border-radius: 3px;
		border-style: solid;
		border-width: 1px;
		margin-right: 5px;
	}
}

.ui-corner-all {
	border-radius: 0;
}

.ui-dialog-content {
	padding: 0 !important;
	margin-top: -10px;
	margin-left: -5px;
	margin-right: -5px;
    overflow: hidden !important;
	
	.dashboard-panel-container {
		.panel-body {
			height: auto !important;
			overflow-y: auto;
		}

		.panel-footer {
			display: none;
		}
	}

	.panel {
		box-shadow: none;
	}

	.panel-default {
		border: 0px;
	}

	.panel-container {
		box-shadow: none;
	}

	.legends {
		margin-top: 20px;
		margin-bottom: 20px;
	}
}

.print-mode {
	background-color: #fff;
}

.dialog-container {
    width: 100%;
    background-color: #fff;
    padding: 15px 20px 30px 30px;

    @media (max-width: $screen-xs) {
        height: 100vh;
        padding: 15px 0 0 0;
    }

    .button-print {
        margin-left: -10px;
        margin-bottom: 5px;
    }

    &.theme-dark {
        padding: 0;
    }

    .panel-heading {
        padding: 0;

        > .panel-title {
            margin-top: 10px;
            height: 30px !important;
            overflow: hidden;
            white-space: nowrap;
        }

        > .panel-status {
            .label {
                font-size: 10px !important;
                text-transform: uppercase;
                background-color: #fff;
                padding: 1px 4px 0 4px;
                border-radius: 0;
                letter-spacing: 1px;
            }

            .label-primary {
                color: $label-primary-bg !important;
                border: 1px solid $label-primary-bg !important;
                border-left: 4px solid $label-primary-bg !important;
            }

            .label-success {
                color: $label-success-bg !important;
                border: 1px solid $label-success-bg !important;
                border-left: 4px solid $label-success-bg !important;
            }

            .label-info {
                color: $label-info-bg !important;
                border: 1px solid $label-info-bg !important;
                border-left: 4px solid $label-info-bg !important;
            }

            .label-default {
                color: $label-info-bg !important;
                border: 1px solid $label-info-bg !important;
                border-left: 4px solid $label-info-bg !important;
            }

            .label-warning {
                color: $label-warning-bg !important;
                border: 1px solid $label-warning-bg !important;
                border-left: 4px solid $label-warning-bg !important;
            }

            .label-danger {
                color: $label-danger-bg !important;
                border: 1px solid $label-danger-bg !important;
                border-left: 4px solid $label-danger-bg !important;
            }
        }
    }

    .panel {
        margin-bottom: 20px;
    }

    .dialog-title {
        color: #007dc6;
        font-size: 17px;
        text-transform: uppercase;
        font-weight: 100;
        letter-spacing: 1px;
    }

    > .dialog-title {
        padding-left: 20px;
        padding-top: 5px;

        @media screen and (max-width: $screen-sm) {
            padding-left: 5px;
            font-size: 15px;
        }
    }

    > .dialog-content {
        padding: 15px 5px 65px 20px;

        @media screen and (max-width: $screen-sm) {
            padding: 5px;
        }
    }

    > .form-edit {
        padding-left: 20px;
        padding-top: 10px;

        .control-label {
            opacity: 1;
        }
    }

    > .form-footer {
        padding-left: 20px;
        padding-bottom: 50px;
    }

    .dashboard-panel-container {
        .panel {
            border: none;
            box-shadow: none;
            padding: 10px 0 0 0;

            @media (max-width: $screen-xs) {
                padding: 15px;
            }

            .panel-heading {
                @media (max-width: $screen-xs) {
                    padding: 8px 8px 8px 4px;
                }
            }

            .legends {
                padding-bottom: 20px;

                @media (max-width: $screen-xs) {
                    padding: 0;
                    margin-left: 5px;
                }
            }
        }
    }

    .panel-container {
        box-shadow: none;
        margin: 0;
        padding: 0;

        @media (max-width: $screen-xs) {
            padding: 0 5px 0 20px !important;
        }

        .panel-container {
            @media (max-width: $screen-xs) {
                padding: 0 !important;
            }
        }
    }

    .row.chart-and-activities-container .activities-container .activities-body {
        max-height: none
    }
}

@media (max-width: $screen-sm-min) {
    .dialog-container {
        width: 100%;

        .panel-container {
            box-shadow: none;
        }

        .dashboard-panel-container {
            margin: 20px 0;

            .panel {
                .legends {
                    padding: 20px 0 20px 5px;
                }
            }
        }

        .row.chart-and-activities-container .activities-container .activities-body {
            max-height: none
        }
    }
}

.popup-dialog {
    .hide-on-expand {
		display: none;
    }
}

.dialog-xs {
    width: $screen-xs !important;
    left: 50% !important;
    margin-left: - $screen-xs / 2;

    @media (max-width: $screen-xs) {
        width: 100% !important;
        left: 0 !important;
        margin-left: 0;
    }

    .popup-dialog {
        -webkit-overflow-scrolling: touch;
    }
}

.dialog-sm {
    width: 768px !important;
    left: 50% !important;
    margin-left: -384px;

    @media (max-width: 768px) {
        width: 100% !important;
        left: 0 !important;
        margin-left: 0;
    }

    .popup-dialog {
        -webkit-overflow-scrolling: touch;
    }
}

.dialog-md {
    width: 992px !important;
    left: 50% !important;
    margin-left: -496px;

    @media (max-width: 992px) {
        width: 100% !important;
        left: 0 !important;
        margin-left: 0;
    }

    .popup-dialog {
        -webkit-overflow-scrolling: touch;
    }
}

.dialog-lg {
    width: 1200px !important;
    left: 50% !important;
    margin-left: -600px;

    @media (max-width: 1200px) {
        width: 100% !important;
        left: 0 !important;
        margin-left: 0;
    }

    .popup-dialog {
        -webkit-overflow-scrolling: touch;
    }
}

/* UI Modal Override*/
button.ui-button.ui-corner-all.ui-widget.ui-button-icon-only.ui-dialog-titlebar-close {
margin-top: -20px;
    border: none;
    background: transparent;
    font-size:0;
    z-index:0;

    &:focus {
        outline:0;
    }
}
    
span.ui-button-icon.ui-icon.ui-icon-closethick {
    width: 30px;
    height: 30px;
    padding-top: 4px;
    display: inline-block;
    position: absolute;
    top: 20px;
    left: 0px;
    -webkit-transition:ease 0.25s all;
    transition: ease 0.25s all;
    -webkit-transform: translate(50%, -50%);
    transform: translate(50%, -50%);
    border-radius: 1000px;
    background-color: transparent;
    font-family: 'viglobal';
    font-size: 15px;
    text-align: center;
    line-height: 23px;
    color: #999;
    z-index:1;
    text-indent:0;

    &:hover {
		width: 30px;
		height: 30px;
		padding-top: 4px;
		top: 20px;
		left: 0px;
        -webkit-transform: translate(50%, -50%) rotate(180deg);
        transform: translate(50%, -50%) rotate(180deg);
        background: rgba(0,0,0,0.1);
        text-decoration: none;

        color: red;
    }

    &:after {
        content: "\43"
    }
}

.ui-dialog.ui-corner-all.ui-widget.ui-widget-content.ui-front {
	box-shadow: 0 0 250px rgba(0,0,0,0.3);
	border-radius: 5px;

	.confirm-dialog-container {
		padding: 30px 20px 10px 30px !important;

		.confirm-dialog-title {
			color: $brand-primary;
			padding-bottom: 10px;
			text-transform: uppercase;
			text-decoration: none;
			padding-bottom: 10px;
		}

		.confirm-dialog-content {
			padding-bottom: 10px;
		}
	}

	.ui-dialog-buttonpane {
		button {
			font-family: "roboto condensed";
			letter-spacing: 1px;
			text-transform: uppercase;
			min-width: 100px;
			height: 32px;
			line-height: 23px;
		}
	}
}

.ui-icon {
	background-image: none !important;
}

//
//Modal Popup
//
.modal-popup-background
{
	background-color: rgba(0, 0, 0, 0.7);
    opacity: 0.6;
}

.modal-popup {
	display: none;
	visibility: visible;
	background: #fff;
	overflow-x: hidden;
	overflow-y: hidden;
	padding: 20px 0;
	border-radius: 6px;

	@media (max-width: $screen-xs) {
		border-radius: 0;
	}

	.panel-heading {
		padding: 0;

		> .panel-title {
			line-height: 30px !important;
		}
	}
}
