
.overview-row {
	border-top: 2px solid #f5f5f5;
	padding: 0;
	margin: 0 -25px;
	display: flex;
	flex-wrap: wrap;

	@media screen and (max-width: $screen-sm) {
		margin: 0 -10px;

		> .form-group {
			width: 100% !important;

			&:not(:first-child) {
				border-top: 2px solid #f5f5f5;
			}
		}
	}

	.inline-repeater-item-container {
		height: unset;
	}

	> .form-group {
		padding: 10px 25px 18px 25px;
		border-left: 2px solid #f0f0f0;
		margin-bottom: 0;

		&:first-child {
			border-left: none;
		}
	}
}
