
.editable {
	padding-right: 0px !important;
	padding-bottom: 10px;
	transition: all 0.1s;
	background: #fff;
	border: 1px solid #fff;
	margin-right: 5px !important;
	overflow: hidden;
	border-radius: 5px;
}

.editable.inactive {
	background: repeating-linear-gradient(135deg, #fff, #fff 4px, #f6f6f6 4px, #f6f6f6 8px);
	border: 1px dashed #E4E4E4;
}

.editable:hover, .editable.inactive:hover {
	background: #fff;
	border: 2px solid #fff;
	box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
	border-radius: 5px;
	transition: all 0.1s;
}

.editable.inactive:hover {
	background: repeating-linear-gradient(135deg, #fff, #fff 4px, #f6f6f6 4px, #f6f6f6 8px);
}

.editable.inactive table {
	background-color: transparent;
}

.edit-controls {
	height: 0;
	opacity: 0;
	line-height: 40px;
	transition: all 0.1s;
	border-top: 2px solid #fff;
	text-align: center;
}

.editable:hover > .edit-controls {
	height: 40px;
	transition: all 0.1s;
	opacity: 1;
	background: #eee;
}

.edit-controls .btn-group-round {
	border: none;
	background-color: transparent;
}

.editable-form-container {
	transition: all 0.1s;
}

.editable.inactive .editable-form-container {
	padding: 5px;
	transition: all 0.1s;
}

.editable:hover > .editable-form-container {
	padding: 5px;
	transition: all 0.1s;
}

.form-group.readonly .control-label:after {
	content: '\r';
	font-family: "viglobal";
	font-style: normal;
	font-weight: normal;
	text-decoration: inherit;
	margin-left: 5px;
	color: #b00;
	font-size: 15px;
	vertical-align: bottom;
	line-height: 20px;
}

.form-section .spacer {
	display: block;
	height: 11px;
	width: 100%;
}

.editable .edit-controls {
	height: 34px;
	transition: all .1s;
	background: #eee;
	display: block;
	position: absolute;
	top: -28px;
	left: 0px;
	opacity: 0;
	border-radius: 5px 5px 0 0;
	padding: 2px;
	box-shadow: inset 0px -5px 8px -5px rgba(0,0,0,0.05);
}

.editable:hover, .editable.readonly:hover {
	background: #fff;
	border: 1px solid #eee;
	box-shadow: 0 2px 5px rgba(0,0,0,.1);
	border-radius: 0 5px 5px 5px;
	transition: all .1s;
	margin-bottom: -20px;
}

.editable.inactive:hover {
	border: 1px solid #eee;
	box-shadow: 0 2px 5px rgba(0,0,0,.1);
	border-radius: 0 5px 5px 5px;
	transition: all .1s;
	margin-bottom: 0px;
}


.editable:hover > .edit-controls {
	height: 34px;
	transition: all .1s;
	opacity: 1;
	top: -34px;
}

.edit-controls .btn-group-square {
	border: none;
	background-color: transparent;
}
