.dropdown-menu {
  box-shadow: $dropdown-box-shadow;

  > li > a {
    padding: $dropdown-link-padding-vertical $dropdown-link-padding-horizontal;
  }
}

// Custom dropdowns
.dropdown-header {
    background-color: $navbar-dropdown-header-bg;
    color: $navbar-dropdown-header-color;
    font-size: $navbar-dropdown-header-font-size;
    padding: 10px;

    span {
        font-weight: bold;
        font-family: Roboto;
        font-size: 20px;
        color: #222222;
        padding-left: 10px;
    }

    a {
        float: right;
    }
}

.dropdown-footer a {
  background-color: $navbar-dropdown-footer-bg;
  border-top: $navbar-dropdown-footer-border;
  color: $navbar-dropdown-footer-color;
  display: block;
  height: 100%;
  padding: 10px;
  text-align: center;
  text-decoration: none;
  transition: all $transition-duration;
  width: 100%;

  &:hover,
  &:focus {
    background-color: $navbar-dropdown-footer-hover-bg;
    color: $navbar-dropdown-footer-hover-color;
  }
}

.custom-dropdown {
  min-width: $navbar-dropdown-min-width;
  padding: 0;

  @media (min-width: $grid-float-breakpoint) {
    max-width: $navbar-dropdown-max-width;
  }
}

.dropdown-apps {
  .a-area {
    padding: 15px;
  }

  .a-item {
    text-align: center;
  }

  .a-item a {
    display: block;
    padding: 10px 0;
    color: $gray-light;
    border: 1px solid transparent;
    border-radius: $border-radius-base;
    text-decoration: none;
    transition: all $transition-duration;

    &:hover,
    &:focus {
      border-color: #eee;
      color: #333;
    }
  }

  .a-item .ai-icon {
    margin: 0 auto 10px auto;
    width: 32px;
    height: 32px;
  }

  .a-item .ai-title {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

.dropdown-messages {
    .m-item {
        border-bottom: 1px solid #e5e5e5;

        a {
            color: $dropdown-messages-color;
            display: block;
            height: 100%;
            padding: 0 10px 0 60px;
            position: relative;
            text-decoration: none;
            transition: all $transition-duration;
            border: solid 1px #ececec;
            border-radius: 0 3px 3px 0;
            margin: 0 20px 0 20px;

            &:hover,
            &:focus {
                background-color: $dropdown-messages-hover-bg;
                color: $dropdown-messages-hover-color;
            }
        }

        .toggleBgColor {
            background-color: #f7f7f7;
        }

        .mi-icon {
            border-radius: $border-radius-base+1 0 0 $border-radius-base+1;
            font-size: 19px;
            height: 40px;
            left: 10px;
            line-height: 40px;
            position: absolute;
            text-align: center;
            width: 40px;

            span {
                color: #ffffff;
                vertical-align: middle;
            }

            div {
                color: #ffffff;
                vertical-align: middle;
            }
        }

        .text-info {
            background-color: #1d87e4;
        }

        .text-warning {
            background-color: #f5a623;
        }

        .text-danger {
            background-color: #b82217;
        }

        .mi-title {
            font-weight: bold;
            font-family: Roboto;
            font-size: 14px;
            color: #333333;
        }

        .mi-text {
            font-size: 12px;
            color: $gray-light;
            padding-bottom: 7px;
        }

        .mi-time {
            font-size: 10px;
            color: $gray-light;
            padding-top: 7px;
        }
    }

    .m-items {
        height: $navbar-dropdown-height;
        overflow: hidden;

        .m-item:last-child {
            border-bottom: none;
            margin: 3px 0;
        }
    }
}

.dropdown-notifications {
  .n-item {
    border-bottom: 1px solid #e5e5e5;
    padding: 10px 10px 10px 60px;
    position: relative;

    .ni-img {
      left: 10px;
      position: absolute;
      text-align: center;
      top: 15px;
      width: 40px;
    }

    .ni-time {
      font-size: $font-size-small;
      color: $gray-light;
    }
  }

  .n-items {
    height: $navbar-dropdown-height;
    overflow: hidden;

    .n-item:last-child {
      border-bottom: none;
    }
  }
}
