@media print {
	.no-print {
		display: none;
	}

	.dataTables_search_wrapper {
		display: none;
	}

	td {
		div.status {
			padding: 0;
			margin: -10px 0 0 0;
			white-space: nowrap;
		}
	}

	#contentPlaceHolder_ctrlReport_divReportMenu {
		display: none;
	}

	#contentPlaceHolder_panelMessage {
		display: none;
	}

	#divSiteLeftSidebar {
		display: none;
	}

	#divSiteTopNav {
		display: none;
	}

	#divSiteMain {
		min-height: unset;
	}

	#filterTitle {
		display: none;
	}

	#menuAddFilter {
		display: none;
	}

	#panelBreadcrumbs {
		display: none;
	}

	.aspNetHidden {
		display: none;
	}

	.distinct-values td:nth-child(2) {
		border-left: #ccc 1px solid;
	}

	.distinct-values tr[type='D'][data="1"] td {
		border-top: black 2px solid;
		box-shadow: none !important;
	}

	.distinct-values tr[type='D'][data="1"] td.rptDistinctFull {
		border-left: none;
		box-shadow: none;
		padding-left: 0;
		padding-top: 8px;
		vertical-align: top;
	}

	.layout {
		padding-top: 0;
	}

	.page-top-container {
		min-height: 30px;
		margin: 0 -18px 0 -20px;
		padding: 0;

		.page-top-buttons {
			border: none;
			display: none !important;
		}

		.page-top-title {
			border-bottom: none;

			.page-top-label1 {
				font-size: 180%;
			}

			.page-top-label2, .page-top-label3, .page-top-label4 {
				font-size: 100%;
				font-weight: normal;
				padding-left: 0;
			}

			.page-top-label3::before, .page-top-label4::before {
				font-size: 100%;
			}

			.page-top-row1 {
				top: 0;
			}

			.page-top-row2 {
				top: 5px;
			}
		}
	}

	.theme-dark {
		> .page-top-container {
			margin: 0 0 -35px 0;
			padding: 0;

			&:after {
				display: none;
			}

			.page-top-title {
				padding: 0;

				.rtf-header-container {
					padding: 0;

					> .rtf-from-container {
						padding-left: 0;
						margin-left: 0;

						+ .rtf-to-container:before {
							display: none;
						}
					}
				}

				.page-top-row2 {
					top: -5px;
				}

				.page-top-row1 {
					padding: 0;
					margin: 0;
				}
			}
		}
	}

	.button-print {
		display: none;
	}

	.form-footer {
		display: none;
	}

  .panel-body {
    padding: 0px !important;
  }

  .panel-container {
    background: white;
    border-radius: 3px;
    box-shadow: none;
    margin: 0;
    margin-left: 0 !important;
    margin-right: 0 !important;
    padding-bottom: 30px;
  }

  .progress {
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 10px;
    box-shadow: none;
    height: 5px;
  }

  .progress-bar.progress-bar-danger {
    border: 5px solid #ac2a2a;
  }

  .progress-bar.progress-bar-primary {
    border: 5px solid #007dc6;
  }

  .progress-bar.progress-bar-warning {
    border: 5px solid #fea633;
  }

  .progress-bar-success {
    border: 5px solid #68c100;
  }

  .btn-group {
	display: none;
  }

  .radio input[type="radio"]:checked {
	margin: 3px -16px;
	opacity: 0;
  }

  .radio label::before {
    height: 10px;
    margin: 4px -9px;
    width: 10px;
  }

  .radio-success input[type="radio"]:checked+label::after {
    display: none;
  }

  .radio-success input[type="radio"]:checked+label::before {
    border: 5px solid #68c100;
    height: 10px;
    margin: 4px -9px;
    width: 10px;
  }

  .report-filter a {
    display: none;
  }

  .report-filter.clear-all {
    display: none;
  }

  .report-filter-and-menu-container {
    background-image: none;
    border: none;
    margin: 0;
  }

  .report-filter-container {
    border: none;
    margin-top: 2px;
    padding: 0 0 10px;
  }

  .report-filter-container .filter-logic .radio {
    margin-top: -1px;
    padding: 0 0 0 14px;
  }

  .report-filter-container .filter-logic .radio>label {
    font-size: 90%;
    margin-right: 24px;
  }

  .report-filter-container .report-filter {
    font-size: 90%;
    margin-right: 6px;
    padding: 3px 6px;
  }

  .report-filter-container:before {
    color: #999;
    content: "FILTERS";
    float: left;
    font-size: 80%;
    line-height: 20px;
    padding: 0 8px 0 0;
  }

  .report-menu-container {
    border: none;
  }

  .site-content {
    margin-left: 0;
  }

  .vi {
    display: none;
  }

  body {
    background-color: #fff;
  }

  body {
    font-size: 100%;
  }

  div#contentPlaceHolder_ctrlReport_panelBtmNav {
    display: none;
  }

  div#contentPlaceHolder_ctrlReport_panelNoOfRecBtm {
    color: #666;
    font-size: 100% !important;
    opacity: 1 !important;
    padding: 5px 0 !important;
  }

  table {
    font-size: 100%;
  }

  table.report>tbody>tr>td:first-child {
    padding-left: 0;
  }

  table.report td.rptDistinctLast {
    border-bottom: #ccc 1px solid !important;
  }

  table.report>tbody>tr>td:last-child {
    padding-right: 0;
  }

  table.report>tbody>tr>td>div.rptSecondary {
    font-size: 100%;
  }

  table.report>thead {
    background: #fff;
    border-bottom: #000 2px solid;
    color: #000;
  }

  table.report>thead>tr.rptHeader>th:first-child {
    padding-left: 0 !important;
  }

  table.report>thead>tr.rptHeader>th:last-child {
    padding-right: 0;
  }

  table.report>thead>tr>th {
    border-right: #000 1px solid;
    font-size: 80%;
    padding: 0 7px 5px 7px !important;
  }

  table.report>thead>tr>th, table.report>thead>tr>td, table.report>tbody>tr>th, table.report>tbody>tr>td, table.report>tfoot>tr>th, table.report>tfoot>tr>td {
    border-top: #ccc 1px solid;
    padding: 5px 8px 5px 8px;
  }

  .rptAction {
	  display: none;
  }

  .panel-options-container {}

  .panel-option.legends {
    display: inline-block;
  }

  .panel-container {
	  padding-left: 0;
	  padding-right: 0;
  }

  .panel-container .panel-options-container .panel-option:last-child {
    float: right !important;
  }

  .panel-option {
    display: none;
  }

  .legend {
    font-size: 90%;
  }

  .legend .legend-text {
    margin: 0 0 0 3px;
    display: inline-block;
    vertical-align: top;
  }

  .chart-feedback {
    transform: scale(0.6);
    line-height: 0px;
  }

  .horizontal-divider {
	  display: none;
  }

.site-content {
	.site-subtitle {
		font-size: 17px;
		font-weight: 500;
		margin-bottom: -25px;
	}
}

  td > .chart-feedback:not(:last-child) {
    margin: 0;
  }
  
  .status.status-success {
	text-transform: uppercase;
	background-color: transparent;
	margin: 0;
	height: 100%;
	font-size: 75%;
  }

	.form-design {
		.form-row {
			padding-bottom: 35px !important;

			&:last-child {
				padding-bottom: 0px !important;
			}
		}
	}
}
