
table.plain {
	> thead th,
	> tbody td {
        border: none !important;
	}
}

table {
    border: none;
    border-collapse: collapse;
}

th, td {
    border: none;
}

td .label {
    padding: 2px 10px 2px !important;
    border-radius: 2px;

    @media (max-width: $screen-sm) {
        font-size: 85% !important;
        padding: 1px 6px 1px !important;
    }
}

table.report {
	margin-bottom: 10px;

	@media (max-width: $screen-sm) {
		tr > th {
			height: 30px !important;
		}

		> thead > tr > th {
			padding: 0px 8px !important;
		}

		> tbody > tr > th {
			padding: 0px 8px !important;
		}

		> thead > tr > td {
			padding: 8px !important;
			font-size: $font-size-xsmall !important;
		}

		> tbody > tr > td {
			padding: 8px !important;
			font-size: $font-size-small !important;
		}
	}
}

table.report > tbody > tr.rptFilter { 
    background-color: #ECF4FB !important; 
} 

table.report > tbody > tr.rptHeader > td { 
    border-top-color: #FFFFFF !important; 
    background-color: #FFFFFF; 
}

table.report > tbody > tr > td.rptGroupEmpty { 
    border-top-color: #FFFFFF !important; 
    background-color: #FFFFFF !important; 
    outline: none;

    + td {
        border-left: 1px solid $table-border-color;
    }

    ~ td {
        border-top: 1px dashed #979797;
    }
}
table.report > tbody > tr > td.rptAction { 
	background-color: #f0f0f0;
	padding: 6px 4px 2px 4px;
    text-align: center;
}
table.report > tbody > tr > td {
	&.rptMain {
		color: #222222;
		font-weight: 500;
	}

	div.rptSecondary {
		opacity: .8;
		font-size: $font-size-small;
		font-weight: 300;
		text-transform: uppercase;

		@media (max-width: $screen-xs) {
			font-size: $font-size-xxsmall;
		}
	}

	div.rptInfo {
		color: #222;
		opacity: .7;
		font-size: $font-size-xsmall;
		padding-right: 10px;
		display: inline-block;

		:last-child {
			padding-right: 0;
		}

		@media (max-width: $screen-xs) {
			font-size: $font-size-xxsmall;
		}
	}

	div.rptSubtitle {
		opacity: .8;
		font-size: $font-size-xxsmall;
		font-weight: 300;
		text-transform: uppercase;

		@media (max-width: $screen-xs) {
			font-size: $font-size-xxsmall;
		}
	}
}
table.report > tbody > tr > td.rptGroupLast { 
    background-color: #FFFFFF !important; 
}

table.report {
    > tbody > tr.rptHeader > td:first-child,
    > tbody > tr.rptFilter > td:first-child,
    > tbody > tr.rptGroup1 > td:first-child,
    > tbody > tr.rptGroup2 > td:first-child,
    > tbody > tr.rptGroup3 > td:first-child,
    > tbody > tr.rptGroup4 > td:first-child {
        cursor: default !important;
        outline: none;
    }

    > tbody > tr.rptHeader + tr.child,
    > tbody > tr.rptFilter + tr.child,
    > tbody > tr.rptGroup1 + tr.child,
    > tbody > tr.rptGroup2 + tr.child,
    > tbody > tr.rptGroup3 + tr.child,
    > tbody > tr.rptGroup4 + tr.child {
        display: none !important;
    }

    > tbody > tr.rptHeader > td:first-child::before,
    > tbody > tr.rptFilter > td:first-child::before,
    > tbody > tr.rptGroup1 > td:first-child::before,
    > tbody > tr.rptGroup2 > td:first-child::before,
    > tbody > tr.rptGroup3 > td:first-child::before,
    > tbody > tr.rptGroup4 > td:first-child::before {
        display: none !important;
    }

    > tbody > tr.rptGroup2 > td,
    > tbody > tr.rptGroup3 > td,
    > tbody > tr.rptGroup4 > td {
        border-top-color: #ffffff;
    }

    > tbody > tr > td.child {
        border-top-color: #ffffff;
    }

    thead > tr.rptHeader > th > .vi,
    tbody > tr.rptHeader > td > .vi {
        font-size: $font-size-small;
        margin-right: 0;
        padding-right: 0;
        padding-left: 5px;
        color: $brand-success;
    }

    tr > th .vi {
        margin-top: 0;
        margin-right: 0;
        vertical-align: text-bottom;
    }
}

.grouped {
	&.group-1 {
		td.rptGroupEmpty + td {
			border-left: 1px solid $table-border-color;
		}
	}

	&.group-2 {
		td.rptGroupEmpty:nth-child(2) + td {
			border-left: 1px solid $table-border-color;
		}

		.rptGroup2 > td:nth-child(2) {
			background-color: #e7ebf1;
			border-bottom: 1px solid $table-border-color;
			-webkit-box-shadow: inset 3px 0px 0px $table-thead-bgcolor, 0px 1px 0px $table-border-color;
			-moz-box-shadow: inset 3px 0px 0px $table-thead-bgcolor, 0px 1px 0px $table-border-color;
			box-shadow: inset 3px 0px 0px $table-thead-bgcolor, 0px 1px 0px $table-border-color;
			padding: 6px 10px;

			> h6 {
				font-size: 14px;
				color: #000000;
				display: inline-flex;
				margin-left: 18px;
			}

			div.rptSecondary {
				margin-left: 18px;
				opacity: .6;
				font-size: $font-size-small;
				font-weight: 300;
				text-transform: uppercase;
			}
		}
	}

	.rptGroup1 > td {
		background-color: #e7ebf1;
		border-bottom: 1px solid $table-border-color;
		-webkit-box-shadow: inset 3px 3px 0px $table-thead-bgcolor, 0px 1px 0px $table-border-color;
		-moz-box-shadow: inset 3px 3px 0px $table-thead-bgcolor, 0px 1px 0px $table-border-color;
		box-shadow: inset 3px 3px 0px $table-thead-bgcolor, 0px 1px 0px $table-border-color;
		
		> h6 {
			color: #000000;

			> .rptSecondary {
				padding-top: 5px;
			}
		}

		div.rptSecondary {
			opacity: .6;
			font-size: $font-size-small;
			font-weight: 300;
			text-transform: uppercase;
		}
	}

	&.group-2 .rptGroup1 > td {
		border-bottom: none;
	}

	tr.rptGroup1:first-child td {
		-webkit-box-shadow: inset 3px 0px 0px $table-thead-bgcolor, 0px 1px 0px $table-border-color;
		-moz-box-shadow: inset 3px 0px 0px $table-thead-bgcolor, 0px 1px 0px $table-border-color;
		box-shadow: inset 3px 0px 0px $table-thead-bgcolor, 0px 1px 0px $table-border-color;
	}

	tr.rptGroup1 ~ .even {
		// background-color: #d8d8d832;
	}
}






