//
.distinct-values {
    tr[type='D'][data="1"] {
        td {
            -webkit-box-shadow: 3px 3px 0px $table-thead-bgcolor inset;
            -moz-box-shadow: 3px 3px 0px $table-thead-bgcolor inset;
            box-shadow: 3px 3px 0px $table-thead-bgcolor inset;


            & ~ td {
                -webkit-box-shadow: 0px 3px 0px $table-thead-bgcolor inset;
                -moz-box-shadow: 0px 3px 0px $table-thead-bgcolor inset;
                box-shadow: 0px 3px 0px $table-thead-bgcolor inset;
            }
        }

        &:first-child {
            td {
                -webkit-box-shadow: 3px 0px 0px $table-thead-bgcolor inset;
                -moz-box-shadow: 3px 0px 0px $table-thead-bgcolor inset;
                box-shadow: 3px 0px 0px $table-thead-bgcolor inset;

                & ~ td {
                    -webkit-box-shadow: none;
                    -moz-box-shadow: none;
                    box-shadow: none;
                }
            }
        }
    }


    tr[type='D'] {
        td.rptDistinctFull {
            border-bottom: 1px solid #FFFFFF !important;
        }
    }

    &.collapsed tr[type=D] {
        &.parent td.rptDistinctFull {
            border-bottom: 1px solid #ddd !important;
        }

        td.rptDistinctFull {
            border-left: 1px solid #ddd !important;
        }
    }
}


.distinct-level-2 {
    td:nth-child(1) {
        border-right: 1px solid $table-border-color;
    }

    &.collapsed td:nth-child(1) {
        border-right: none;
    }
}

.distinct-level-3 {
    td:nth-child(2) {
        border-right: 1px solid $table-border-color;
    }

    td:nth-child(1) {
        border-right: 1px solid $table-border-color;
    }

    &.collapsed td:nth-child(1) {
        border-right: none;
    }
}


tr[type='H'] {
    td {
        &:first-child {
            -webkit-box-shadow: 3px 0px 0px $table-thead-bgcolor inset;
            -moz-box-shadow: 3px 0px 0px $table-thead-bgcolor inset;
            box-shadow: 3px 0px 0px $table-thead-bgcolor inset;
            border-top: none;
        }

        padding: 8px 10px !important;
        background-color: #c8cfdc !important;
        color:  #424242;
        text-transform: uppercase;
        font-size: 10px;

        a {
            font-size: 12px;
            color: white;
            margin-left: 5px;
        }
    }  
}

table.report td.rptDistinctEmpty { 
    border-bottom: 1px solid #FFFFFF !important; 
    background-color: #FFFFFF !important; 
}
table.report td.rptDistinctLast { 
    border-bottom: 1px dotted $table-border-color !important; 
    background-color: #FFFFFF !important; 
} 

table.report tr:last-child {
    td {
        border-bottom: 2px solid #444 !important; 
    }
}

table.report tr.rptFooter { background-color: #FFFFFF !important; } 
table.report tr.rptFooter td { font-weight: bold; }

table.report {
	border-collapse: collapse;
	border-bottom: 1px solid $table-border-color !important;

	> thead {
		background-color: transparent;
		color: $table-thead-color;
		text-transform: uppercase;
	}

	tr {
		&.tr-selected {
			background-color: #e5f2f9;

			> td.rptAction {
				background-color: #e5f2f9;
			}
		}

		> th {
			background-color: transparent;
			color: $table-thead-color;
			text-transform: uppercase;
			height: 40px;
			letter-spacing: 1px;
			font-size: $font-size-xsmall - 2px;
			font-weight: 500;
			border: none;
			background: #444;

			.vi {
				margin-top: 0px;
				margin-right: 3px;
			}

			a {
				color: $table-thead-color;

				:hover {
					color: lighten($table-thead-color, 10%);
				}
			}
		}

		&.rptHeader {
			> th {
				padding: 8px 16px;
			}
		}

		> th:first-child {
			//border-top-left-radius: 3px !important;
			@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
				border-radius: 0;
			}

			@supports (-ms-ime-align: auto) {
				/* Microsoft EdgeV13&14 CSS styles go here */
				border-radius: 0;
			}
		}

		> th:last-child {
			//border-top-right-radius: 3px !important;
			@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
				border-radius: 0;
			}

			@supports (-ms-ime-align: auto) {
				/* Microsoft EdgeV13&14 CSS styles go here */
				border-radius: 0;
			}
		}
	}

	> tbody {
		> tr {
			transition: background-color 0.3s;

			&:hover {
				background-color: #f8f8f8;

				> td.rptAction {
					background-color: darken(#f8f8f8, 5%);
				}
			}
		}

		> tr.rptHeader {
			> td {
				letter-spacing: 1px;
				font-weight: bold;
			}
		}

		> tr.rptFilter {
			> td {
				background-color: lighten(rgba(0, 125, 198, 0.1), 30%);
				border-top: 0;
				border-bottom: 0;
				margin-top: -1px;
				margin-bottom: -1px;

				&:nth-child(1) {
					border-radius: 3px 0px 0px 0px;
					-webkit-border-radius: 3px 0px 0px 0px;
					-moz-border-radius: 3px 0px 0px 0px;
				}

				&:nth-last-child(1) {
					border-radius: 0px 3px 0px 0px;
					-webkit-border-radius: 0px 3px 0px 0px;
					-moz-border-radius: 0px 3px 0px 0px;
				}
			}
		}

		> tr > .dataTables_empty {
			//display: none;
		}
	}
	// Cells
	> thead,
	> tbody,
	> tfoot {
		> tr {
			> th {
				padding-top: $table-cell-padding + 4px;
				padding-bottom: $table-cell-padding + 4px;
				vertical-align: middle !important;

				&:nth-child(1) {
				}

				&:nth-last-child(1) {
					padding-right: 22px;
					border-right: solid 0px #ffffff;
				}

				a > .vi {
					font-size: $font-size-small;
					padding-left: 5px;
					color: #ffffff;
				}

				a:hover > .vi {
					font-size: $font-size-small;
					padding-left: 5px;
					color: #cccccc;
				}

				a:focus {
					outline: none;
				}
			}

			> th, > td {
				padding: 10px 5px 10px 15px;
				line-height: $line-height-base;
				vertical-align: middle;

				> .form-control {
					height: 25px;
				}

				> label.toggle {
					margin-bottom: -5px;
				}
			}

			> td {
				border-top: 1px dashed $table-border-color;
			}

			&:first-child td {
				border-top: 0px solid $table-border-color;
			}
		}
	}
	// Bottom align for column headings
	> thead > tr > th {
		vertical-align: top;
		border-bottom-width: 0px;
	}

	&.collapsed {
		tr[type=D].parent {
			border-bottom: #ddd solid 1px !important;

			td.rptDistinctEmpty {
				border-bottom: none !important;
				background-color: transparent !important;
			}
		}

		> tbody > tr.child ul.dtr-details {
			width: 90%;
		}
	}

	&.dtr-inline.collapsed {
		> tbody > tr[role="row"] > {
			td:first-child:before,
			th:first-child:before {
				display: inline-block;
				float: left;
				margin-left: -5px;
				padding-right: 3px;
				margin-top: -1px;
			}
		}

		.status {
			@media (max-width: $screen-xs) {
				width: 25px;
				margin: 0;
				border-radius: 8px;
				display: inline-block;
			}
		}
	}
}

table.report > tbody > tr.child {
    ul {
        border: 1px solid $table-border-color;
        border-radius: 3px 3px 3px 3px;
        -webkit-border-radius: 3px 3px 3px 3px;
        -moz-border-radius: 3px 3px 3px 3px;
    }

    ul.dtr-details {
        width: 100%;
        margin: 14px 0 15px 20px;
        padding: 0 0 0 10px;
        border: none;
        border-left: #1d87e4 4px solid;
        border-radius: 0;

        > li {
            border: none;
            padding: 15px 0 0 0;

            &:first-child {
                border: none;
                padding: 0;
            }
        }
    }

    span.dtr-title {
        display: block;
        width: 100%;
        text-transform: uppercase;
        font-family: "roboto condensed";
        font-weight: 100;
        letter-spacing: 1px;
        font-size: 11px;
        color: #bbb;

        > a {
            padding-left: 10px;
        }
    }

    span.dtr-data {
        display: block;
        width: 100%;
        color: #666;
    }
}

tr[type='G'] + tr > td {
	border-top-width: 0px !important;
}


table.report {
    > thead > tr > th {
        position: -webkit-sticky !important;
        position: sticky !important;
        -webkit-transform: translateZ(0) !important;
        top: 0px;
        z-index: 1;

        @media (max-width: $screen-xs) {
            padding: 5px 8px !important;
        }
    }

    tr > th {
        @media (max-width: $screen-xs) {
            height: auto !important;
        }
    }
}

.table.report > thead > tr > th:first-child {
    border-radius: 4px 0 0 0
}

.table.report > thead > tr > th:last-child {
    border-radius: 0 4px 0 0
}

table.report.header-top {
	> thead > tr > th {
		z-index: 3;
	}

	> tbody > tr.rptGroup1 > td {
		position: -webkit-sticky !important;
		position: sticky !important;
		-webkit-transform: translateZ(0) !important;
		z-index: 2;
		padding-top: 8px;
		padding-bottom: 8px;
	}

	> tbody > tr.rptGroup2 > td {
		position: -webkit-sticky !important;
		position: sticky !important;
		-webkit-transform: translateZ(0) !important;
		z-index: 1;
	}
}

table.report.header-group {
	> tbody > tr.rptGroup1 > td {
		position: -webkit-sticky !important;
		position: sticky !important;
		-webkit-transform: translateZ(0) !important;
		top: 0px;
		z-index: 1;
	}

	> tbody > tr.rptGroup2 > td {
		position: -webkit-sticky !important;
		position: sticky !important;
		-webkit-transform: translateZ(0) !important;
		z-index: 2;
	}

	> tbody > tr.rptHeader > td {
		position: -webkit-sticky !important;
		position: sticky !important;
		-webkit-transform: translateZ(0) !important;
		z-index: 3;
	}
}

.dtr-details .dtr-title a, .dtr-details .dtr-title i {
    display: none;
}



