.filter-dropdown-wrapper {
    display: inline-block;
    height: 27px;
    position: relative;
    top: -27px;
    margin-right: 6px;
}

.report-filter-container {
	background: #f0f0f0;
	padding: 5px 6px;
	flex: 1;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	-moz-border-radius: 2px;
	-webkit-border-radius: 2px;
	-moz-border-radius: 2px;

	.filter-dropdown-wrapper {
		select {
			background-color: #fff;
			color: #333;
			padding: 3px 10px;
			width: 250px;
			line-height: 16px;
			height: 27px;
			border: #ccc 1px solid;
			font-size: 14px;
			-webkit-appearance: none;
			appearance: none;
			outline: none;
			display: inline-block;
			box-shadow: none;
			-webkit-box-shadow: none;
			border-top-left-radius: 3px;
			border-bottom-left-radius: 3px;

			&:focus {
				box-shadow: none;
				-webkit-box-shadow: none;
				border: #ccc 1px solid;
			}
		}

		&:before {
			content: "\e00b";
			font-family: "viglobal" !important;
			position: relative;
			top: 27px;
			right: -224px;
			width: 26px;
			height: 27px;
			text-align: center;
			font-size: 14px;
			color: #fff;
			background-color: #007dc6;
			border: none;
			pointer-events: none;
			display: block;
			border-radius: 0 3px 3px 0;
			padding-top: 3px;
		}
	}

	.report-filters {
		display: flex;
		flex-wrap: wrap;

		.form-control {
			width: unset;
		}
	}

	.report-filter {
		line-height: normal;
		white-space: nowrap;
		font-size: 12px;
		padding: 2px 5px 3px 10px;
		margin: 0 5px 0 0;
		background-color: #ffffff;
		border: 1px solid #d9d9d9;
		border-radius: 100px;
		-moz-border-radius: 100px;
		-webkit-border-radius: 100px;
		-moz-border-radius: 100px;

		.filter-value {
			max-width: 350px;
			overflow: hidden;
			text-overflow: ellipsis;
			color: $text-color;
			line-height: 16px;

			.filter-value-name {
				font-family: 'roboto condensed';
				text-transform: uppercase;
				color: #c0c0c0;
				font-weight: bold;
				margin: 0 5px 0 0;
				letter-spacing: 1px;
				vertical-align: text-bottom;
				font-size: $font-size-xxsmall;
				padding-top: 3px;
				display: inline-block;
				height: 16px;
			}

			.filter-value-value {
				color: #333;
				margin: 0 10px 0 0;
				padding-top: 2px;
				vertical-align: middle;
				display: inline-block;
			}
		}

		> a {
			outline: none;
			text-decoration: none;

			&:hover {
				text-decoration: none;
				color: $link-color;
			}

			> i {
				padding: 0 3px;
				vertical-align: text-top;
				margin-top: -1px;
			}

			.change-filter {
				color: $link-color;
				font-size: 16px;
				padding-top: 1px;
				padding-right: 0px;

				&:hover,
				&:focus {
					color: $link-hover-color;
					text-decoration: none;
				}
			}

			.remove-filter {
				color: $brand-danger;
				font-size: 16px;
				padding-top: 2px;

				&:hover,
				&:focus {
					color: darken($brand-danger, 10%);
					text-decoration: none;
				}
			}
		}
	}

	.clear-all {
		background-color: $brand-danger;
		padding: 4px 12px 1px 12px;
		letter-spacing: 0.5px;
		border-color: #f0f0f0;

		> a {
			color: white;
			line-height: 18px;

			&:hover {
				color: darken(#ffffff, 10%);
			}
		}
	}

	.filter-logic {
		margin-left: auto;

		.radio {
			display: inline-block;
			margin: 0;

			> label {
				line-height: 20px;
			}
		}

		.form-control {
			display: inline-block;
			height: 25px;
			min-width: 150px;
		}

		.btn {
			display: inline-block;
			height: 24px;
			line-height: 15px;
			margin-top: -2px;
		}
	}
}

.pagination {
    margin: 0;
    border-radius: 0px;

    > li {
        > a,
        > span {
            border-top: 4px solid #fff;
            border-left: 0px solid #fff;
            border-right: 0px solid #fff;
            border-radius: 0px;
            margin-right: 2px;
            font-size: $font-size-base;
        }
    }

    > li > a,
    > li > span {
        &:hover,
        &:focus {
            font-weight: bold;
            color: $pagination-active-color;
            background-color: $pagination-bg;
            border-top: 4px solid $pagination-active-bg;
        }
    }

    > .active > a,
    > .active > span {
        &,
        &:hover,
        &:focus {
            font-weight: bold;
            color: $pagination-active-color;
            background-color: $pagination-bg;
            border-top: 4px solid $pagination-active-bg;
        }
    }
}

/*Bootstrap Override*/
.table-condensed.report>tbody>tr>td {
    padding: 5px 10px;
}



.panel-collapse {
	table.report {
		border-bottom: 1px dashed #dddddd !important;
		border-top: 3px solid;

		tr {
			> th {
				background-color: #f0f0f0;
				color: #333333;
				font-size: $font-size-base;
				font-family: "Roboto Condensed";
				font-weight: bold;
				border-bottom: 2px solid #444444 !important;
			}
		}

		tr:last-child {
			> td {
				border-bottom: 1px dashed #dddddd !important;
			}
		}
	}
}

.hidden-onload {
	opacity: .3;
}

