
.panel-table {
	margin: 0;

	> div.panel-slick-table {
		display: none;
	}

	> table {
		display: table;
		margin: 25px 0 8px 0;
		width: 100%;

		> thead {
			> tr {
				border-radius: 6px;
				height: 40px;

				> th {
					color: #fff;
					font-size: 10px;
					letter-spacing: 1px;
					font-weight: 500;
					text-transform: uppercase;
					padding: 0 8px;
				}

				> th:last-child {
					width: 46px;
				}
			}

			> tr:first-child {
				background-color: #fff;

				th:first-child, th:last-child {
					background-color: #fff;
				}
			}

			> tr:last-child {
				background-color: #444;

				th:first-child {
					border-radius: 6px 0 0 0;
				}

				th:last-child {
					border-radius: 0 6px 0 0;
				}
			}
		}

		> tbody {
			> tr {
				border-bottom: dashed 1px rgba(151,151,151,0.3);

				td:last-child {
					background-color: rgba(216,216,216,0.4);

					.btn-ico-table.btn-default {
						margin: 0 auto;
						display: block;
					}
				}

				td:last-child:hover {
					background-color: rgba(216,216,216,0.7);
				}

				> td {
					padding: 10px 8px;
				}
			}

			h5 {
				font-size: 11px;
				color: #777777;
				margin: 0 0 3px 0;

				span {
					opacity: 0.3;
					padding: 0 3px;
				}
			}

			h4 {
				font-size: 14px;
				color: #222222;
			}
		}

		.panel-table-status {
			width: 70px;
			text-align: center;
		}

		.icon {
			text-align: center;
			font-size: 20px;
			margin: 7px 0 0 0;
		}
	}

	table > tbody > tr > td {
		color: #333;
		line-height: 1.25;
	}

	.danger-front {
		color: $brand-danger;
	}

	.head-credits {
		color: #fff;
		text-transform: uppercase;
		text-align: center;
		border-radius: 6px 6px 0 0;
	}

	.head-credit {
		text-align: center;

		&.needed {
			background-color: #f69900;
			color: #fff;
			min-width: 120px;
		}

		&.completed {
			background-color: $brand-success;
			color: #fff;
			min-width: 120px;
		}

		&.credits-not-required {
			background-color: #000;
			color: transparent !important;
		}
	}

	.body-credit, .footer-credit {
		text-align: center;
		width: 90px;
		height: 50px;
		color: #fff;
		font-size: 15px;
		font-weight: 600;
		padding: 5px 5px 5px 10px;
		border-bottom: solid 1px rgba(255,255,255,0.2);

		h6, span {
			font-size: 11px;
			margin: 0 0 3px 2px;
			display: inline-block;
			color: rgba(255,255,255,.65);
			font-weight: 400;
			padding: 3px;
			letter-spacing: 0.02em;
		}

		span {
			margin: 0 0 3px 5px;
		}

		span.notes {
			display: block;
			margin: 0;
		}
	}

	.body-credit {
		&.needed {
			background-color: #c77b00;
			color: #fff;
		}

		&.completed {
			background-color: darken($brand-success, 10%);
			color: #fff;
		}

		&.credits-not-required {
			background-color: #fff;
			color: transparent !important;
			border-top: 1px dashed #ddd;
			border-bottom: none;
		}
	}

	.footer-credit {
		&.needed {
			background-color: #f69900;
			color: #fff;
		}

		&.completed {
			background-color: $brand-success;
			color: #fff;
		}

		&.credits-not-required {
			background-color: #fff !important;
			color: transparent !important;
			border-top: 1px dashed #ddd;
			border-bottom: none;
		}
	}

	.credit-info {
		line-height: 0px;
		max-width: 50px;

		&::after {
			white-space: nowrap;
			position: relative;
			color: #888;
			font-size: $font-size-xxsmall;
			right: -5px;
			bottom: -40px;
			float: right;
			font-weight: 400;
			letter-spacing: 0.02em;
		}
	}

	.footer-heading {
		color: $brand-primary;
		font-size: 14px;
		text-transform: uppercase;
		font-weight: 600;

		> span {
			color: #222222;
			opacity: .7;
			font-size: 12px;
			text-transform: initial;
			display: block;
			font-weight: initial;
		}
	}

	.head-date {
		width: 130px;
		text-align: left;
	}

	.body-date {
		text-align: left;
	}

	.panel-table-button {
		min-width: 155px;

		a {
			color: #00649e;
			border: 1px solid rgba(151,151,151,0.4);
			font-size: 11px;
			text-transform: uppercase;
			text-decoration: none;
			padding: 6px 10px 5px 11px;
			border-radius: 15px;
			letter-spacing: 1px;
			font-family: "roboto condensed";

			span {
				font-size: 13px;
				color: rgba(0,0,0,0.3);
				padding: 0;
			}
		}

		a:hover {
			border: 1px solid rgba(151,151,151,0.8);

			span {
				color: rgba(0,0,0,0.5);
				padding: 0;
			}
		}
	}
}

.panel-table-primary {
	.head-credits {
		background-color: #00649e;
	}

	.head-credit {
		background-color: $brand-primary;
	}

	.body-credit {
		background-color: #00649e;
	}
}

.panel-table-default {
	.head-credits {
		background-color: #ddd;
		color: #333;
		letter-spacing: 3px;
	}

	.head-credit {
		background-color: #666;
		color: #fff;
		font-weight: 600;
	}

	.body-credit {
		background-color: #ddd;
		color: #333;
	}

	.footer-credit {
		background-color: #444444;
	}
}



@media (max-width: $screen-note) {
  .panel-table {
    > div.panel-slick-table {
        display: block;
        margin: 0px -23px;
    }
    > table {
        display: none;
    }
  }
}

@media print {
	.panel-table {
		> table {
			margin: 2px 0 0 0;

			> tbody {
				> tr {
					> td {
						border: none !important;
						padding: 5px 4px;
					}
				}

				h4 {
					font-size: 12px;
				}
			}

			.icon {
				text-align: center;
				font-size: 16px;
				margin: 7px 0 0 0;
			}
		}

		table > tbody > tr > td {
			padding: 0;
			line-height: 1;
			border: none !important;
			font-size: 13px;
		}

		table > tbody > tr > th {
			line-height: 1;
			border: 2px solid #000 !important;
		}

		.head-credits {
			display: none;
		}

		.head-blank {
			display: none;
		}

		.body-credit, .footer-credit {
			height: 25px;
			font-size: 14px;
			font-weight: 500;
			padding: 2px 2px 2px 5px;
		}

		.footer-heading {
			font-size: 14px;
			font-weight: 500;
		}

		.head-date {
			width: 100px;
		}

		.panel-table-button {
			min-width: 155px;

			a {
				display: none;
			}
		}
	}
}