
.timeline-legends {
    .legend {
        display: inline-block;
    }
}

.timeline-left {
    position: relative;
    overflow: hidden;
    margin-left: -30px;
    margin-right: -20px;
    padding: 20px 0 30px 0;

    .timeline-row {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        -webkit-flex-wrap: nowrap;
        flex-wrap: nowrap;
        min-height: 87px;
        margin-top:55px;
        padding-right: 5px;
        
        &.no-margin {
            margin-top:0px;
        }
    }

    .timeline-dates, .timeline-contents {
        display: flex;
        flex-direction: column;
    }

    .timeline-dates {
        position: relative;
        flex: 0 0 100px;
        text-align: center;
        padding-top: 17px;
        left: 10px;

        @media screen and (max-width: $screen-sm) {
            padding-left: 10px;
        }

        .timeline-month {
            font-size: $font-size-xsmall;
            text-transform: uppercase;
            color: #007dc6;
            font-weight: bold;
            width: 80px;
        }
        
        .timeline-date {
            font-size: 24px;
            font-weight: bold;
            line-height: 24px;
            position: absolute;
            top: 50%;
            width: 100%;
            margin: 0 auto;
            margin-top: -12px;
            width: 80px;
        }

        .timeline-day {
            font-size: $font-size-xsmall;
            text-transform: uppercase;
            position: absolute;
            top: 50%;
            width: 100%;
            margin: 0 auto;
            margin-top: 12px;
            width: 80px;
        }
    }

    .timeline-contents {
        padding: 15px;
		margin-right: 20px;
        position: relative;
        left: 2px;
        flex: 1 1 auto;

        .timeline-edit {
            position: absolute;
            right: 10px;
            top: 50%;
            margin-top: -22.5px;
        }

        .timeline-date-name {
            position: absolute;
            top: -40px;
            display: inline-block;
            background-color: #666;
            color: white;
            left: -13px;
            padding: 4px 30px;
            border-top-right-radius: 3px;
            border-bottom-right-radius: 3px;
            font-size: $font-size-small;
            text-transform: uppercase;
            overflow:hidden;
			min-width: 300px;

            @media screen and (max-width: $screen-sm) {
                width: 100%;
            }
        }

        .timeline-triangle {
            width: 0px;
            height: 0px;
            border-top: 13px solid transparent;
            border-bottom: 13px solid transparent;
            border-right: 13px solid #2f2f2f;
            position: absolute;
            top: -25.5px;
            left: -13px;
        }

        .timeline-status-circle {
            position: absolute;
            width: 14px;
            height: 14px;
            border-radius: 7px;
            top: 50%;
            margin-top: -7px;
            left: -8px;

            
            &.completed {
                background-color:  $brand-success;
            }
            &.overdue {
                background-color:  $brand-warning
            }
            &.startrequired {
                background-color:  $brand-danger;
            }
            &.inprogress {
                background-color: $brand-primary;
            }
            &.upcoming  {
                background-color: $text-color;
            }
        }

        .timeline-action-type {
        }

        .timeline-action-title {
            font-weight: bold;
        }

        .timeline-action-user {
            font-style: italic;
        }
        
    }

    .line {
        position: absolute;
        width: 30px;
        display: block;
        background: white;
        top: 10px;
        bottom: 10px;
        left: 100px;
        opacity: 0.3;
        border-left: 2px solid #777;
        box-shadow: -10px 0 20px -10px #000;

        @media screen and (max-width: $screen-sm) {
            display: none;
        }
    }

    .line::before { top: -4px; }
    .line::after { bottom: -4px; }
    .line::before,
    .line::after {
        content: '';
        position: absolute;
        left: -4px;
        width: 6px;
        height: 6px;
        display: block;
        border-radius: 50%;
        background: currentColor;
    }
}

.timeline-configuration {
    .line {
        left: 50px;
    }

    .timeline-dates {
        flex: 0 0 50px;
    }

    .timeline-contents {
        margin-right: 15px;
    }

    .timeline-status-circle.active {
        background-color:  $brand-success;
    }

    .timeline-status-circle.inactive {
        background-color:   $brand-warning;
    }

    .timeline-status-circle.error {
        background-color:   $brand-danger;
    }
}


/////////////////////////////////////////////////////////////

.timeline-centered {
	list-style: none;
	padding: 20px 0 20px;
	position: relative;

	&:before {
		top: 0;
		bottom: 0;
		position: absolute;
		content: " ";
		width: 3px;
		background-color: #eeeeee;
		left: 50%;
		margin-left: -1.5px;
	}

	> li {
		margin-bottom: 25px;
		position: relative;

		> .timeline-panel {
			width: 46%;
			float: left;
			border: 1px solid #d4d4d4;
			border-radius: 6px;
			padding: 0px;
			position: relative;
			-webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);
			box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);

			&:before {
				position: absolute;
				top: 26px;
				right: -15px;
				display: inline-block;
				border-top: 15px solid transparent;
				border-left: 15px solid #ccc;
				border-right: 0 solid #ccc;
				border-bottom: 15px solid transparent;
				content: " ";
			}

			&:after {
				position: absolute;
				top: 27px;
				right: -13px;
				display: inline-block;
				border-top: 14px solid transparent;
				border-left: 14px solid #fff;
				border-right: 0 solid #fff;
				border-bottom: 14px solid transparent;
				content: " ";
			}

			.request-item {
				border: 0px;
			}
		}

		> .timeline-badge {
			color: #fff;
			width: 50px;
			height: 50px;
			font-size: 1.4em;
			text-align: center;
			position: absolute;
			top: 16px;
			left: 50%;
			margin-left: -25px;
			background-color: #999999;
			z-index: 100;
			border-top-right-radius: 50%;
			border-top-left-radius: 50%;
			border-bottom-right-radius: 50%;
			border-bottom-left-radius: 50%;

			> .info {
				font-size: 10px;
				margin-top: 6px;
				margin-bottom: -6px;
			}
		}
	}

	> li.timeline-inverted > .timeline-panel {
		float: right;

		&:before {
			border-left-width: 0;
			border-right-width: 15px;
			left: -15px;
			right: auto;
		}

		&:after {
			border-left-width: 0;
			border-right-width: 14px;
			left: -13px;
			right: auto;
		}
	}

	> li:before, > li:after {
		content: " ";
		display: table;
	}

	> li:after {
		clear: both;
	}

	> li:before, > li:after {
		content: " ";
		display: table;
	}

	> li:after {
		clear: both;
	}

	.timeline-badge.primary {
		background-color: #1d87e4 !important;
	}

	.timeline-badge.success {
		background-color: #009380 !important;
	}

	.timeline-badge.warning {
		background-color: #faa800 !important;
	}

	.timeline-badge.danger {
		background-color: #ac2a2a !important;
	}

	.timeline-badge.info {
		background-color: #6450ac !important;
	}

	.timeline-badge.default {
		background-color: #111111 !important;
	}

	.timeline-title {
		margin-top: 0;
		color: inherit;
		padding: 10px 10px 0 10px;
	}

	.timeline-body {
		padding: 0 10px 10px 10px;
	}

	.timeline-body > p,
	.timeline-body > ul {
		margin-bottom: 0;
	}

	.timeline-body > p + p {
		margin-top: 5px;
	}
}


@media (max-width: 767px) {
	ul.timeline-centered {
        &:before {
			left: 40px;
		}

		> li {
			> .timeline-panel {
				width: calc(100% - 90px);
				width: -moz-calc(100% - 90px);
				width: -webkit-calc(100% - 90px);
			}

			> .timeline-badge {
				left: 15px;
				margin-left: 0;
				top: 16px;
			}

			> .timeline-panel {
				float: right;

				&:before {
					border-left-width: 0;
					border-right-width: 15px;
					left: -15px;
					right: auto;
				}

				&:after {
					border-left-width: 0;
					border-right-width: 14px;
					left: -14px;
					right: auto;
				}
			}
		}
    }
}
