//
// Login
//
.full-page {
    width:100%;
    height: 100%;
    position: absolute;
    top:0;
    left:0;

    @media screen and (max-width: 480px) {
        height: inherit;
        min-height: 100%;
    }
}

.login-navbar {
    height: 55px;
    background-color: $brand-primary;
    box-shadow: 0 15px 25px 0 rgba(0, 0, 0, 0.25);
    color: white;
    position:absolute;
    top:0;
    left:0;
    width: 100%;
    padding: 0;

    .brand-text {
        line-height: 54px;
        font-size: 20px;
        font-weight: 300;
        color: white;
    }

    z-index: 1;
}

.no-margin {
    margin: 0 !important;
}

.login-wrapper {
    @media screen and (max-width: 414px) {
        font-size: 12px;
    }

    @media screen and (max-width: 480px) {
        overflow: auto;
    }

    .illustration-wrapper {
		background: url("../images/bg-login.jpg") no-repeat left center fixed;
		background-size: cover;
        
        .illustration {
            height: 100%;
            margin-right: 480px;
            margin-left: 0px;
            background-color: inherit;
            background-repeat: no-repeat;
            background-size: cover;
            -webkit-background-size: cover;
            -moz-background-size: cover;
            -o-background-size: cover;

            .page-title{
                color: white;
                font-size: 4vw;
                font-family: "Times";
                font-weight:bold;
                position:absolute;
                bottom: 180px;
                left: 50px;

                border-left: 5px solid $brand-primary;
                padding-left: 16px;

                span {
                    display: block;
                    line-height: 1.12;
                }
            }

            .login-footer {
                position: absolute;
                left: 50px;
                bottom: 80px;
                font-size: 14px;

                img {
                    height: 35px;
                    margin-top: -25px;
                }

                span {
                    display:inline-block;
                    color:white;
                    padding-left: 1em;
                }
            }
        }
    }
    
    .content-wrapper {
        position: absolute;
        top: 50px;
        right: 0;
        width: 480px;
        height: 100%;
        overflow: auto;
        background-color: #ffffff;
        margin-left: -480px;
        margin-right: 0px;

        background: linear-gradient(#2b2b2b, #191919), linear-gradient(156deg, rgba(75, 75, 75, 0.5), rgba(0, 0, 0, 0.5));
        color: #ffffff;
		box-shadow: 0 0 50px 0 rgba(0,0,0,0.90);
            
        display: -webkit-box;
        display: -webkit-flex;
        display: -moz-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -webkit-flex-direction: column;
        -moz-box-orient: vertical;
        -moz-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;

        @media screen and (max-width: 480px) {
            width: 100%;
            min-height: 100%;
            height: inherit;
        }

        margin-top: -50px;
        padding-top: 50px;
    }

    .content-header {
        display:none;

        @media screen and (max-width: 480px) {
            display:block;
        }

        .page-title {
            font-size: 38px;
            font-family: 'Times New Roman';
            font-weight: bold;
            padding: 50px 0;
            border-bottom: 2px solid black;

            div {
                padding-left: 16px;
                border-left: 5px solid $brand-primary;
                span {
                    display: block;
                    line-height: 1.12;
                }
            }
        }

        margin: 0 50px;
    }
        
    div.content {
        -webkit-box-flex: 1;
        -webkit-flex: 1;
        -moz-box-flex: 1;
        -ms-flex: 1;
        flex: 1;
        display: -webkit-box;
        display: -webkit-flex;
        display: -moz-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
        -webkit-justify-content: center;
        -moz-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -webkit-flex-direction: column;
        -moz-box-orient: vertical;
        -moz-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
    }

    .content {
        margin: 0 50px;           
        border-bottom: 1px dashed white;
        @media screen and (max-width: 480px) {
            border-bottom: 2px solid #000000;
            border-top: 2px solid rgba(255, 255, 255, 0.1);
        }
    }

    .greeting {
        margin-top: 1em;
        font-family: Times;
        font-size: 3em;
        font-weight: bold;
    }

    .title {
        font-family: 'Roboto Condensed', sans-serif;
        font-size: 3em;
        text-transform: uppercase;
    }

    .info {
        opacity: 0.5;
        font-family: Roboto;
        font-size: 1em;
        margin-bottom: 2em;
    }

	.alert-danger {
		color: $brand-danger !important;
	}

    .input-icon-wrap {
        border: 1px solid #444;    
        display: flex;
        flex-direction: row;
        border-radius: 3px;
        margin: 12px 0;
    }

    .input-with-icon {
        border: none;
        flex: 1;
        color: white;
    }

    .input-icon, .input-with-icon {
        padding: 4px;
        font-size: 16px;
        background: #444;
    }

    .remember-me-text {
        font-size: 0.8em;
        margin-top: 0.1em;
        margin-left: 10px;
        position: absolute;
    }

    ::-webkit-input-placeholder {
        font-style: italic;
    }
    :-moz-placeholder {
        font-style: italic;  
    }
    ::-moz-placeholder {
        font-style: italic;  
    }
    :-ms-input-placeholder {  
        font-style: italic; 
    }

    .buttons {
        display: flex;
        margin-top: 30px;
        margin-bottom: 1em;
        flex-wrap: wrap;
    }

    .button-container {
        padding-right: 20px;

        &:last-child {
            padding-right: 0px;
        }

        > a {
            white-space: nowrap;
        }
    }

    .flat-button {
        width: 100%;
        height: 40px;
        padding: 0;
        font-size: 1em;
        text-transform: uppercase;
        color: white;
        text-align: center;
        border-radius: 3px;
        border: 0;
        cursor: pointer;
    }

    .login-button {
        background: #007dc6;
        width: 120px;
        @media screen and (max-width: 480px) {
            width: 85px;
        }
           
    }

    .forgot-password-button {
        line-height: 40px;
    }

    .reset-password-button {
        background: #007dc6;
        width: 160px;
    }

    .cancel-button {
        background: none;

        &:hover {
            text-decoration: underline;
        }
    }

    .footer {
        border-top: 2px solid rgba(255, 255, 255, 0.1);
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        min-height: 150px; 
        height: 150px;
        -webkit-box-pack: center;
        -webkit-justify-content: center;
        -moz-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        -ms-flex-align:center;
        margin: 0 50px;

        @media screen and (max-width: 480px) {
           min-height: 90px;
           height: 90px;
        }

        @media (max-height: 667px){
            height: 150px;
            min-height: 150px;
        }

        @media (max-width: 332px) {
            height: 150px;
            min-height: 150px;
        }
    }

    .footer > div {
        flex: 1;       
        margin: auto;
    }

    .footer .widget-time {
        font-family: Roboto;
        font-size: 40px;
        font-weight: normal;
        font-style: normal;
        font-stretch: condensed;
        line-height: normal;
        letter-spacing: normal;
        color: #ffffff;
        text-align: right;

        @media screen and (max-width: 480px) {
            display: none;
        }
    }

    .weather {
        display:flex;

        @media screen and (max-width: 480px) {
            display: none;
        }

        .weather-icon {
            margin-top: 0px;
            width: 50px;
            height: 50px;
        }

        .widget-date {
            font-size:16px;
            margin-left: 10px;
        }
    }

    .footer .logo {
        display:none;
        font-size: 14px;
        @media screen and (max-width: 480px) {
            display: block;
            margin-top: 2em;
        }

        img {
            height: 20px;
            margin-top: -12px;
        }
    }

    .lighter-text {
        opacity: 0.6;
    }
}

.password-requirements {
    margin-top: 2em;
    font-size: 14px;
    opacity: 0.7;

    .success {
		list-style-type: none;
		margin-left: -14px;

        &:before {
            font-family: "viglobal";
            content: '\76';
            color: $brand-success;
            font-size: 12px;
            padding-right: 5px;
        }
    }
    .error {
		list-style-type: none;
		margin-left: -14px;

        &:before {
            font-family: "viglobal";
            content: '\43';
            color: $brand-danger;
            font-size: 12px;
            padding-right: 5px;
        }
    }
}
