.layout-right-sidebar-opened {
    .site-main {
        width: 100%;
    }
    /* Block scroll on dashboard panels */
    .sb-content {
        overflow: hidden;
    }

    .site-right-sidebar {
        display: flex;
        align-items: start;
        justify-content: center;

        .grid-container {
            margin: auto 0;
            display: -ms-grid;
            display: grid;
            -ms-grid-columns: 400px 400px;
            grid-template-columns: repeat(2, 400px);
            -ms-grid-rows: 70px 70px 70px 70px 70px 70px 70px;
            grid-template-rows: repeat(7, 70px);
            max-width: 1000px;

            @media screen and (max-width: 1000px) {
                -ms-grid-columns: 400px 400px;
                grid-template-columns: repeat(2, 400px);
                max-width: 800px;
            }

            @media screen and (max-width: 800px) {
                display: flex;
                flex-direction: column;
                padding-top: 55px;
            }

            @media (max-width: $screen-xs) {
                padding-top: 0px;
            }
        }
    }
}

.site-right-sidebar {
    display: none;
    background-color: rgba(34, 34, 34, 0.9);
    width: 100%;

    .close-btn {
        position: absolute;
        top: 27px;
        right: 27px;
        width: 30px;
        font-size: 20px;
        -webkit-transition: ease 0.25s all;
        transition: ease 0.25s all;
        -webkit-transform: translate(50%, -50%);
        transform: translate(50%, -50%);
        border-radius: 1000px;
        background: white;
        cursor: pointer;

        &:hover {
            -webkit-transform: translate(50%, -50%) rotate(180deg);
            transform: translate(50%, -50%) rotate(180deg);
            background: rgba(0, 0, 0, 0.1);
            text-decoration: none;
            color: red;
        }

        > i {
            margin-left: 5px;
            position: relative;
            top: 3px;
        }
    }

    .grid-container {
        margin: auto;
        padding-top: 20px;
    }

    overflow-y: scroll;
    z-index: 2060;
    //margin-top: 5px;
    //top: 50px;
    // Hide scrollbars for Firefox, IE/Edge & Chrome
    overflow: -moz-scrollbars-none;
    -ms-overflow-style: none;

    &::-webkit-scrollbar {
        display: none;
    }
    // Rightside-nav fade-scroll
    /*
    &::before {
        content: '';
        position: fixed;
        top: 55px;
        width: 330px;
        height: 25px;
        background: linear-gradient(#000000,rgba(0, 0, 0, 0));
        pointer-events: none;
    }

    &::after {
        content: '';
        position: fixed;
        bottom: 0;
        width: 330px;
        height: 25px;
        background: linear-gradient(rgba(0, 0, 0, 0), #000);
        pointer-events: none;
    }
    */
    .ai-icon {
        //padding: 25px 0 5px;
        padding-right: 15px;

        @media (max-width: $screen-xs) {
            padding-right: 15px;
            opacity: 0.3;
        }
    }

    .ai-title {
        font-family: "roboto condensed";
        letter-spacing: 1px;
        font-size: 18px;

        @media (max-width: $screen-xs) {
            font-size: 15px;
        }
    }

    h3 {
        font-size: 24px;
    }

    a {
        //background-color: #666;
        color: rgba(255, 255, 255, 0.7);
        margin: auto;
        transition: all 0.3s;
        border-radius: 35px;
        text-align: center;
        text-decoration: none;
        width: 360px;
        height: 61px;
        display: flex;
        align-items: center;
        padding-left: 45px;

        @media (max-width: $screen-xs) {
            height: 44px;
            padding-left: 25px;
        }

        > div {
            display: inline-block;
        }
        // Grid: Explicit positioning - For IE 10, IE 11 & Edge > 16
        &:nth-child(1) {
            -ms-grid-row: 1;
            -ms-grid-column: 1;
        }

        &:nth-child(2) {
            -ms-grid-row: 1;
            -ms-grid-column: 2;
        }

        &:nth-child(3) {
            -ms-grid-row: 2;
            -ms-grid-column: 1;
        }

        &:nth-child(4) {
            -ms-grid-row: 2;
            -ms-grid-column: 2;
        }

        &:nth-child(5) {
            -ms-grid-row: 3;
            -ms-grid-column: 1;
        }

        &:nth-child(6) {
            -ms-grid-row: 3;
            -ms-grid-column: 2;
        }

        &:nth-child(7) {
            -ms-grid-row: 4;
            -ms-grid-column: 1;
        }

        &:nth-child(8) {
            -ms-grid-row: 4;
            -ms-grid-column: 2;
        }

        &:nth-child(9) {
            -ms-grid-row: 5;
            -ms-grid-column: 1;
        }

        &:nth-child(10) {
            -ms-grid-row: 5;
            -ms-grid-column: 2;
        }

        &:nth-child(11) {
            -ms-grid-row: 6;
            -ms-grid-column: 1;
        }

        &:nth-child(12) {
            -ms-grid-row: 6;
            -ms-grid-column: 2;
        }

        &:nth-child(13) {
            -ms-grid-row: 7;
            -ms-grid-column: 1;
        }

        &:nth-child(14) {
            -ms-grid-row: 7;
            -ms-grid-column: 2;
        }

        &:hover {
            color: rgba(255, 255, 255, 1) !important;
            background-color: #007dc6;
            transform: translateY(-2px);
            box-shadow: 0 7px 14px rgba(0, 0, 0, 0.1), 0 3px 6px rgba(0, 0, 0, .08);
        }
    }
}

.blur {
  -webkit-filter: url(#blur-filter);
  filter: url(#blur-filter);
  -webkit-filter: blur(20px);
  filter: blur(20px);
  -ms-filter: blur(20px);
  filter: progid:DXImageTransform.Microsoft.Blur(PixelRadius='20');
}

.scrollbar-vertical-left::-webkit-scrollbar-track{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: transparent;
}

.scrollbar-vertical-left::-webkit-scrollbar{
	width: 26px;
  background-color: transparent;
 }

.scrollbar-vertical-left::-webkit-scrollbar-thumb{
  border-radius: 20px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #666;
  border:10px solid #000;
}