
.navbar-default {
    background-color: $navbar-default-bg;

    .navbar-nav {
        background-color: $navbar-default-bg;
        margin-top: 0;

        > li > a {
            color: #fff;
        }

        > li > a:hover,
        > li > a:focus {
            color: $navbar-top-link-hover-color !important;
            background-color: transparent !important;
        }

        > .open > a,
        > .open > a:hover,
        > .open > a:focus {

            width: 100%;

            .p-r-10 {
                width: 32px !important;
            }
        }

        > .profile-dropdown.open > a, 
        > .profile-dropdown.open > a:hover, 
        > .profile-dropdown.open > a:focus {
            color: #fff;
            background-color: #00456e;
        }
    }
}

.navbar-brand {
    color: $navbar-brand-color !important;
}

.navbar-collapsible {
    margin-left: 0;
    box-shadow: 0 3px 8px 0px rgba(0,0,0,0.15);
    z-index: 2;

    .navbar-toggler.right-sidebar-toggle {
        background: #000;
        border-radius: 0;
        border: none;
        color: rgba(255, 255, 255, 0.6);
        outline: none;
        padding-top:3px;

        &:hover {
            color: $navbar-invert-default-link-hover;
        }
    }
}

.logo-image {
	background-color: $navbar-default-bg;
	display: inline-block;
	height: 55px;
	width: 219px;
	background-size: contain;
	background-repeat: no-repeat;
	background-position: center;
	background-origin: content-box;
}

.logo-text {
	display: inline-block;
	width: 219px;
	white-space: nowrap;
}

#logo {
	width: 219px;
	height: 55px;
	margin: 0 auto;
	white-space: nowrap;
	padding: 0 15px;
	display: flex;
	align-items: center;
	justify-content: center;

	> .logo-text {
		text-align: left;
	}
}

#labelFunctionalArea {
	text-transform: uppercase;
}

#labelUserName {
	padding: 0 5px;
	text-overflow: ellipsis !important;
	overflow: hidden !important;
	white-space: nowrap !important;
	max-width: 120px;
	display: inline-block;
	line-height: 22px;
}

.navbar-header {
	display: none;
	height: $navbar-height;

	.navbar-brand {
		padding: $navbar-brand-padding 0 0;
		font-size: $navbar-brand-font-size;
		font-weight: $navbar-brand-font-weight;
		margin-left: 0;

		> img {
			margin-top: -5px;
			margin-right: 5px;

			@media (max-width: $screen-sm) {
				display: none;
			}
		}

		.brand-text {
			font-size: $font-size-larger;
			line-height: 18px;

			@media (max-width: $screen-sm) {
				> i {
					display: none;
				}
			}
		}

		.brand-abbr-text {
			display: none;
		}
	}

	.navbar-toggler:hover {
		color: $navbar-top-link-hover-color;
	}

	.pull-right {
		color: $navbar-default-link-active-color !important;
	}

	.collapsed {
		background-color: $navbar-default-bg !important;

		:hover {
			color: $navbar-top-link-hover-color !important;
		}
	}

	button {
		outline: none;
	}
}

.nav .open > a,
.nav .open > a:focus,
.nav .open > a:hover {
	border-color: transparent;
}

.sidebar-menu-toggle {
	a {
		:hover {
			color: $navbar-top-link-hover-color;
		}
	}

	font-size: $navbar-brand-font-size;
	font-weight: $navbar-brand-font-weight;
	padding-top: $navbar-brand-padding - 2px;
	padding-right: $navbar-brand-padding;
}

.navbar-company {
	padding-top: $navbar-company-padding;
	padding-left: 0;
	font-size: $navbar-company-font-size;
	font-weight: $navbar-company-font-weight;
	color: $navbar-company-color;
	font-style: normal;
	font-stretch: normal;
	line-height: normal;
	letter-spacing: normal;

	.insert {
		margin-left: 0;
		margin-right: 20px;
		border-left: 1px solid rgba(0,0,0,0.2);
		border-right: 1px solid rgba(255,255,255,0.2);
		height: 55px;
	}

	> span {
		float: left;
	}

	> .logo-text {
		padding-left: 15px;
		height: 55px;
		line-height: 55px;
	}
}

.navbar-functional-area {
	text-transform: uppercase;
	font-size: $navbar-company-font-size - 2px;
	font-weight: bold;
	font-style: normal;
	font-stretch: condensed;
	line-height: 55px;
	letter-spacing: 1.1px;
	text-align: left;
	color: $navbar-company-color;
}
#liNotifications {
    > a {
        padding: 0 10px;
        text-align: center;
        width: 55px;
        background-color: inherit;
        color: #fff;
    }
}
.navbar-right {
    &.nav {
        > li {
            font-size: $font-size-xsmall;
            min-width: 201px;
            border-left: 1px solid rgba(0,0,0,0.2);

            > a {
                padding: 5px 15px;
                width: 100%;
            }

            > .dropdown-toggle {
                border-left: 1px solid rgba(255,255,255,0.2);
            }

            &.profile-dropdown.open > .dropdown-toggle,
            > .dropdown-toggle:hover {
                filter: brightness(150%);
            }
        }

        .dropdown-notifications {
            min-width: 55px;

            .dropdown-messages {
                width: 380px;

                .m-items {
                    height: auto;
                    margin-bottom: 20px;

                    a {
                        padding-left: 40px;
                    }

                    .mi-icon {
                        left: 0px;
                        width: 32px;
                        height: 100%;
                        height: 103%;
                        margin: -1px;
                    }
                }
            }
        }
    }

    .nav-cell {
        &.p-r-10 {
            width: 32px !important;
        }
    }
}

.nav-table.dropdown.profile-dropdown.open,
.nav-table.dropdown.profile-dropdown.open:hover {
    background-color: #00456e;
}
.nav-table.dropdown.profile-dropdown:hover {
    background: darken($navbar-default-bg, 10%);
}
.layout-left-sidebar-opened {
	.navbar-header {
		.left-sidebar-toggle {
			color: $navbar-default-link-active-color;
		}
	}
}
// Menu collapsed
.layout-left-sidebar-collapsed {
	@media (min-width: $grid-float-breakpoint) {
		.navbar-collapsible {
			margin-left: 0;
		}

		.navbar-header {
			.navbar-brand {
				padding: $navbar-brand-padding + 5px 2px 0 2px;
				width: 100%;

				> img {
					display: none;
				}

				.brand-text {
					display: none;
				}

				.brand-abbr-text {
					display: inherit;
					text-align: center;
				}
			}
		}
	}
}

.layout-mobile {
    #liNotifications {
        > a {
            text-align: left;
            width: auto;
            padding: 8px 18px 16px 18px;
        }
    }

    #spanMsgCountContainer {
        display: none;
    }

    .navbar-collapsible {
        position: relative;
        top: -1px;
    }

    .layout-left-sidebar-collapsed .navbar-collapsible {
        margin-left: 220px;
        position: relative;
    }

    .navbar-header {
        display: block;
        background-color: transparent;
        height: $navbar-height;
        margin-left: 0;
        margin-bottom: 0;

        @media (max-width: $screen-sm) {
            padding: 0 5px;
        }

        .navbar-toggler {
            background-color: transparent !important;
            color: $navbar-invert-default-link-hover !important;

            @media (max-width: $screen-sm) {
                width: 44px;
                margin-top: 4px;
                height: 52px;
            }

            span {
                background-color: transparent;
                padding: 0;
                margin: 0;
                left: 38%;
                top: -4px;

                @media (max-width: $screen-sm) {
                    width: 44px;
                    left: 2px;
                    top: -5px;
                }
            }

            img {
                width: 23px;
                height: 23px;

                @media (max-width: $screen-sm) {
                    height: 25px;
                    width: 25px;
                }
            }

            &.left-sidebar-toggle {
                @media (max-width: $screen-sm) {
                    padding: 0;
                }
            }

            &.right-sidebar-toggle {
                @media (max-width: $screen-sm) {
                    padding: 0;
                }
            }
        }

        .navbar-toggler:hover {
            color: $navbar-top-link-hover-color;
        }

        .navbar-brand {
            position: relative;
            margin-left: -60px;
            left: 50%;
            padding: $navbar-brand-padding 0 0;

            @media (max-width: $screen-sm) {
                position: relative;
                margin-left: 0;
                left: auto;
                padding: 20px 5px;
                transform: none;
                width: 60%;
                max-width: 60%;
                margin-top: -1px;
            }
        }
    }

    .navbar-company {
        padding-top: 0;
    }

    .navbar-nav {
        margin: 0;
        padding-top: 10px;

        .open .dropdown-menu {
            background-color: #FFFFFF;
            border: none;
            margin: 0px;
            max-width: 100%;
            background: $brand-primary;

            > li > a {
                padding: 13px 12px;
                line-height: 16px;
                color: $navbar-default-link;
                font-size: $font-size-base;
                letter-spacing: 0.5px;
                color: #FFFFFF;
            }

            > .active > a,
            > .active > a:focus {
                color: $navbar-active;
                background-color: transparent;
                font-weight: 700;
            }

            > .active > a:hover {
                color: $navbar-default-link-hover;
                background-color: transparent;
            }
        }
    }

    .dropdown-apps {
        .a-item a {
            &:focus,
            &:hover {
                color: $navbar-default-link-active-color;
            }
        }

        .ai-icon i {
            &:focus,
            &:hover {
                color: $navbar-default-link-active-color;
            }
        }
    }

    .navbar-collapse {
        padding: 0px;
        width: 100%;
        border-top: 2px solid darken($navbar-default-bg, 5%);
        transition: all 0.3s;
    }

    .navbar-default {
        background-color: $navbar-default-bg;
        box-shadow: 0 0 15px 0 rgba(0,0,0,0.5);
    }

    .navbar-left {
        display: none;
    }

    .navbar-right.nav {
        > li {
            border: none;
        }

        .dropdown-notifications {
            .dropdown-menu {
                .m-items {
                    .mi-icon {
                        height: 100%;
                        margin: 0;
                    }

                    .m-item > a {
                        margin: 0;
                    }
                }
            }
        }
    }

    ul.dropdown-menu.profile-dropdown-menu > li > a > .vi {
        margin-top: -2px;
        float: left;
        opacity: 0.3;
        margin-left: 4px;
        margin-right: 15px;
        font-size: 18px;
    }

    .dropdown-notifications {
        z-index: 1;

        a {
            .nav-icon {
                top: 5px;
                padding-right: 12px;
            }

            .hidden-md-up {
                font-size: 14px;
                letter-spacing: 0.5px;
                color: #FFFFFF;
            }
        }

        .dropdown-toggle {
            border-left: 0px !important;
        }

        .dropdown-menu {
            background-color: #f0f0f0 !important;
            padding: 5px 5px 0px 8px;
            width: 100% !important;
            border-radius: 0px;

            .dropdown-header {
                display: none;
            }
        }
    }
}

    .navbar-nav > li.profile-dropdown.open > a:after, .navbar-nav > li.profile-dropdown.open > a:before {
        border: 0;
    }

ul.dropdown-menu.profile-dropdown-menu {
    background-color: #00456e;
    border: none;
    border-radius: 0px 0px 5px 5px;
    padding: 0 5px 5px 5px;
    margin-right: 0;
    z-index: 2;
    margin-top: -1px;
    left: 0;
    max-width: 201px;

    .tooltip.top {
        display: none !important;
    }

    > li {
        > a {
            color: white;
            font-size: $font-size-xsmall;
            background-color: inherit;

            > .vi {
                margin-left: 12px;
                margin-right: 9px;
            }

            &:hover {
                border-radius: 2px;
                background-color: #19577c;
                transition: all 0.3s;
            }
        }

        &.divider {
            height: 0;
            margin: 4px -2px;
            width: auto;
            border-bottom: 1px dashed rgba(255,255,255,0.2);
            background: none;
        }
    }
}
