 
.notify-container {
    position: absolute;
    background-color: #f69900;
    top: 0;
    height: 50px;
    z-index: 10;
    div span.notify-desc:not(:empty)::before {
        content: "\79";
        color: #000;
        font-family: "viglobal";
        font-size: 20px;
        vertical-align: middle;
        padding: 0 2px 0 0;
    }
    > div {
        margin: 9px 0 0 15px;
        font-size: 14px;
        color: #222;
        .icon {
            font-size: 22px;
        }
        &:not(:empty)::before {
            content: "\30";
            color: #c60000;
            font-family: "viglobal";
            font-size: 22px;
            vertical-align: middle;
            padding: 0 7px 0 0;
        }
    }
}

.notify-container-close {
    position: relative;
    background-color: #f69900;
    height: 50px;
    z-index: 9;
}

.notify-desc {
  color: #fff;
  vertical-align: middle;
}

.notify-close {
    display: block;
    text-align: right;
    margin: 0;
    padding: 12px 15px;
    font-size: 22px;
  .vi-close {
    font-size: 22px;
  }

  a{
      color:#222;
  }
}

.notify-hidden {
	display: none;
}

@media (max-width: $screen-lg) {
  .notify-container, .notify-container-close {
    display: none;
  }
}  