@mixin button-variant2($color, $background, $border) {
  color: $color;
  background-color: $background;
  border-color: $border;

  &:focus,
  &.focus {
    color: $color;
    background-color: darken($background, 10%);
        border-color: darken($border, 25%);
  }
  &:hover {
    color: $color;
    background-color: $background;
        border-color: $border;
  }
  &:active,
  &.active,
  .open > &.dropdown-toggle {
    color: $color;
    background-color: darken($background, 10%);
        border-color: darken($border, 12%);

    &:hover,
    &:focus,
    &.focus {
      color: $color;
      background-color: darken($background, 17%);
          border-color: darken($border, 25%);
    }
  }
  &:active,
  &.active,
  .open > &.dropdown-toggle {
    background-image: none;
  }
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &:hover,
    &:focus,
    &.focus {
      background-color: $background;
          border-color: $border;
    }
  }

  .badge {
    color: $background;
    background-color: $color;
  }
}

.form-update-button {
    border-radius: 13px;
    border: solid 1px lighten($text-color, 10%);
    font-size: $font-size-small;
    margin-right: 20px;
    padding: 5px 15px;
    color: $text-color;
	white-space: nowrap;
	line-height: 35px;

    &:hover, &:focus, &.focus, &:active {
        border: solid 1px darken($text-color, 20%);
        color: darken($text-color, 20%);
        text-decoration: none;
    }
}

.btn-success {
    font-weight: bold;
}

.btn {
    border-radius: 4px;
}

//
//Button Group
//
.btn-group > .btn:first-child:not(:last-child):not(.dropdown-toggle) {
    border-radius: 0;
}

.btn-group-round {
    border-radius: 15px;
    height: 30px;
    background-color: #fff;
    padding: 2px 0px;
    border: #e7e7e7 1px solid;

    .focus {
        outline: none !important;
    }

    .btn {
        background-color: transparent;
        font-weight: normal;
        font-family: "roboto condensed";
        letter-spacing: 0.5px;
        font-size: $font-size-small;
        font-weight: normal;
        padding: 1px;
        border: 0px solid transparent;
        border-style: none !important;
        box-shadow: none;
        background-color: transparent;
        margin-left: 5px;

        &:hover, &:active, &.active, &:focus, &.focus {
            background-color: transparent;
            font-weight: normal;
            font-weight: normal;
            font-stretch: condensed;
        }

        &:first-child {
            margin-left: 3px;
        }

        &:last-child {
            margin-right: 3px;
        }

        > input[type=checkbox], > input[type=radio] {
            position: absolute;
            display: none;
        }

        > input[type=checkbox] ~ label, input[type=radio] ~ label {
            border-radius: 15px !important;
            line-height: normal;
            color: $text-color;
            font-size: $font-size-xsmall;
            font-weight: normal;
            font-stretch: condensed;
            cursor: pointer;
            padding: 4px 12px;
            letter-spacing: 0.5px;

            &:hover {
                color: #000000;
            }
        }

        > input[type=checkbox]:checked ~ label, input[type=radio]:checked ~ label {
            font-weight: bold;
            background-color: $brand-danger;
        }

        &.btn-primary {
            > input[type=checkbox]:checked ~ label, > input[type=radio]:checked ~ label {
                @include button-variant2($btn-primary-color, $btn-primary-bg, $btn-primary-border);
            }
        }

        &.btn-danger {
            > input[type=checkbox]:checked ~ label, > input[type=radio]:checked ~ label {
                @include button-variant2($btn-danger-color, $btn-danger-bg, $btn-danger-border);
            }
        }

        &.btn-warning {
            > input[type=checkbox]:checked ~ label, > input[type=radio]:checked ~ label {
                @include button-variant2($btn-warning-color, $btn-warning-bg, $btn-warning-border);
            }
        }

        &.btn-success {
            > input[type=checkbox]:checked ~ label, > input[type=radio]:checked ~ label {
                @include button-variant2($btn-success-color, $btn-success-bg, $btn-success-border);
            }
        }

        &.btn-info {
            > input[type=checkbox]:checked ~ label, > input[type=radio]:checked ~ label {
                @include button-variant2($btn-info-color, $btn-info-bg, $btn-info-border);
            }
        }

        &.btn-default {
            > input[type=checkbox]:checked ~ label, > input[type=radio]:checked ~ label {
                @include button-variant2($btn-default-color, $btn-default-bg, $btn-default-border);
            }
        }
    }

    > input[type="submit"] {
        border-radius: 15px !important;
        padding: 1px 10px 2px 10px;
        margin-top: 1px;
        line-height: normal;
        font-weight: normal;
        font-size: $font-size-base;
        font-stretch: condensed;

        &.btn {
            border-style: none !important;
            box-shadow: none;
            background-color: transparent;

            &:hover, &:focus, &.focus {
                color: darken($btn-group-color, 25%);
            }

            &.btn-primary {
                @include button-variant2($btn-primary-color, $btn-primary-bg, $btn-primary-border);
            }

            &.btn-danger {
                @include button-variant2($btn-danger-color, $btn-danger-bg, $btn-danger-border);
            }

            &.btn-warning {
                @include button-variant2($btn-warning-color, $btn-warning-bg, $btn-warning-border);
            }

            &.btn-success {
                @include button-variant2($btn-success-color, $btn-success-bg, $btn-success-border);
            }

            &.btn-info {
                @include button-variant2($btn-info-color, #555555, #555555);
            }

            &.btn-default {
                @include button-variant2($btn-default-color, $btn-default-bg, $btn-default-border);
            }
        }
    }

    > span.aspNetDisabled {
        display: none;
    }
}

@media (max-width: $screen-sm) {
	.btn-group-round {
		display: flex;
		flex-wrap: wrap;
		align-content: flex-start;
		height: 100%;
		padding: 2px 0 0 4px;

		> .btn {
			padding: 2px 0 0 0;
		}
	}
}

