

.compliance-period-container {
    background-color: $brand-primary;
    margin-top: -1px;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    margin: -26px -25px 0 -25px;

    @media screen and (max-width: $screen-sm) {
        margin: -10px -10px 0 -10px;
    }

    .compliance-period-info {
        width: 100%;
        padding: 20px;
        border-bottom: 1px solid rgba(0,0,0,0.2);

        .compliance-period-heading {
            display: flex;
        }

        .compliance-period-name {
            width: 100%;

            @media screen and (max-width: $screen-sm) {
                margin-left: -10px;
            }
        }

        .compliance-period-credits {
            width: 150px;
            background: rgba(0,0,0,0.4);
            border-radius: 4px;
            padding: 3px 0 10px;
        }

        .regulator {
            display: inline-block;
            font-size: 18px;
            font-weight: 600;
            text-transform: uppercase;
            color: #fff;
            padding-right: 30px;

            @media screen and (max-width: $screen-sm) {
                display: block;
                padding-right: 0;
                padding-bottom: 5px;
            }
        }

        .element {
            display: inline-block;
            line-height: 15px;
            letter-spacing: 1px;
            font-size: 12px;
            color: #fff;
            padding-right: 30px;
        }

        .element-name {
            display: inline-block;
            line-height: 15px;
            padding-right: 5px;
            letter-spacing: 0.80px;
            font-size: 10px;
            color: #fff;
            opacity: .3;
            text-transform: uppercase;
            font-family: 'Roboto';
        }

        .compliance {
            padding-left: 0;
            display: block;
            font-size: 20px;
            font-family: 'Times New Roman';

            > div {
                display: block;
                padding-top: 10px;
                padding-bottom: 5px;
            }

            .experience-level {
                font-family: "roboto";
                font-weight: 100;
                padding-right: 2px;
                opacity: 0.8;
            }

            .complaince-period-detail {
                font-weight: 600;
                letter-spacing: 0;
                padding-right: 2px;
            }
        }

        .credits {
            text-align: center;

            > .value {
                font-size: 36px;
                font-weight: bold;
                color: #f69900;
                display: block;
                margin-bottom: -5px;

                i {
                    padding-top: 8px;
                    padding-bottom: 0;
                    font-size: 46px;
                }
            }

            > .text {
                display: block;
                line-height: 10px;
                letter-spacing: 0.80px;
                font-size: 11px;
                color: rgba(255,255,255,1);
            }
        }
    }


    .compliance-period-action {
        width: 100%;
        border-top: 1px solid rgba(255,255,255,0.2);

        .buttons {
            display: flex;

            > .button {
                border-right: 1px solid rgba(0,0,0,0.2);
                border-left: 1px solid rgba(255,255,255,0.2);
                padding: 8px 20px;
                color: #fff;
                opacity: .7;
                font-size: 11px;
                letter-spacing: 0.05em;

                @media screen and (max-width: $screen-sm) {
                    padding: 8px 10px;
                }

                &:hover {
                    background-color: darken($brand-primary, 5%);
                    opacity: .9;
                }

                &:first-child {
                    padding-left: 20px;
                    border-left: unset;

                    @media screen and (max-width: $screen-sm) {
                        padding-left: 10px;
                    }
                }
            }
        }

        ul {
            list-style-type: none;
            margin: 0;
            padding: 0;
            border-right: 1px solid rgba(0,0,0,0.2);
            border-left: 1px solid rgba(255,255,255,0.2);
            box-shadow: 1px 0 0 rgba(255,255,255,0.2);

            li a.credits-more {
                letter-spacing: 1px;
                color: #fff;
                opacity: 0.7;
                font-size: 20px;
                padding: 0px 12px 0px;
                line-height: 28px;
                display: inline-block;

                i {
                    padding-top: 4px;
                }

                &:hover {
                    background-color: darken($brand-primary, 5%);
                    opacity: .9;
                }
            }

            li:hover .credits-menu {
                display: block;
            }
        }

        .credits-menu {
            display: none;
            position: absolute;
            box-shadow: 0 5px 15px 0 rgba(0,0,0,.25);
            margin-top: -1px;
            padding: 8px 0;
            border-radius: 4px;
            background: #fff;
            z-index: 2;

            > a {
                display: block;
                color: #333333;
                opacity: .7;
                font-size: 11px;
                padding: 3px 20px;

                &:hover {
                    opacity: 1;
                    text-decoration: none;
                }
            }

            > hr {
                border-top: 1px solid rgba(0,0,0,.10);
            }
        }
    }
}

.credits-required {
    position: relative;
    z-index: 2 !important;
    font-size: 28px;
    font-weight: bold;
    color: #f69900;
    padding-left: 0;

    > .text {
        line-height: 10px;
        color: #444;
        opacity: .7;
        padding-left: 7px;
        padding-bottom: 7px;
        display: inline-block;
        vertical-align: text-bottom;
        font-size: 12px;
        font-weight: 400;
    }

    > .text-success {
        vertical-align: -4px;
        font-size: 38px;
        margin-left: -3px;

        + .text {
            vertical-align: middle;
        }
    }

    + div .panel-table {
        padding-bottom: 8px;

        .credit-info::after {
            bottom: -43px;
        }
    }
}

@media print {
	.compliance-period-container {
		.compliance-period-info {
			padding: 0 30px 0px 30px;

			.compliance-period-credits {
				width: 300px;
				padding: 3px 0 10px;
			}

			.regulator {
				font-size: 15px;
				font-weight: 500;
				padding-right: 15px;
			}

			.element {
				padding-right: 15px;
			}

			.element-name {
				line-height: 15px;
			}

			.compliance {
				font-size: 15px;

				> div {
					padding-top: 5px;
					padding-bottom: 2px;
				}

				.complaince-period-detail {
					font-weight: 500;
				}
			}

			.credits {
				text-align: right;
				padding-top: 35px;

				> .value {
					display: inline;
					font-size: 18px;

					i {
						font-size: 18px;
					}
				}

				> .text {
					display: inline;
				}
			}
		}

		.compliance-period-action {
			display: none
		}
	}

	.credits-required {
		float: right;
		margin-top: -15px;
		font-size: 18px;
		font-weight: bold;

		> .text {
			line-height: 5px;
			color: #444;
			opacity: .7;
			padding-left: 7px;
			padding-bottom: 7px;
			font-size: 12px;
			font-weight: 400;
		}

		> .text-success {
			vertical-align: -4px;
			font-size: 18px;
			margin-left: -3px;
		}

		+ div .panel-table {
			padding-bottom: 8px;

			.credit-info::after {
				bottom: -43px;
			}
		}
	}
}