.nowrap {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.solid {
  color: #ef9321;
}

.hide-success {
	.label-success {
		display: none;
	}
}
.hide-primary {
	.label-primary {
		display: none;
	}
}
.hide-default {
	.label-default {
		display: none;
	}
}
.hide-default {
    .label-dark {
        display: none;
    }
}
.link-button {
  color: #007dc6;
  font-size: 12px;
  font-weight: 400;
  text-transform: uppercase;
  text-decoration: none;
}

.event-panel-container {
	margin-left: -10px;
	margin-right: -10px;
	margin-bottom: 20px;

	@media (max-width: $screen-xs) {
		margin-left: 0;
		margin-right: 0;
	}

	> div {
		display: -webkit-box;
		display: -moz-box;
		display: -ms-flexbox;
		display: -webkit-flex;
		display: flex;

		> .event-panel {
			height: 290px;
			width: 200px;
		}
	}

	> hr {
		border: solid 1px rgba(151,151,151, 0.2);
		width: 40%;
		margin: 30px auto 15px auto;
		box-shadow: 0px 1px 0 0 rgba(255, 255, 255, 1);
	}

	> div {
		flex-flow: row wrap;
		align-items: flex-start;
		padding: 0;
	}
}

.event-panel {
    background-color: #fff;
    border-radius: 6px;
    margin: 10px 10px 0;
    flex: 1 1 253px;
    box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.1);

    > h2 {
        margin: 13px 10px 15px 20px;
        padding: 5px;
        font-size: 20px;
        font-family: "Roboto Condensed";
        font-weight: 600;
        color: #222;
        letter-spacing: 0.5px;

        > span:first-child {
            margin-left: 5px;
        }
    }

    .link-button {
        text-align: right;
        float: right;
        margin: 7px 0 0 0;
    }

    .no-data-stacked-small-box {
        text-align: center;
        padding-top: 25px;

        > div {
            font-size: 12px;
            letter-spacing: 1px;
            width: 75%;
            margin: 10px auto 0;
            color: #7CB3D1;
            padding: 0 10px;
            line-height: 16px;

            > h5 {
                text-transform: uppercase;
                letter-spacing: 2px;
                font-family: "Roboto Condensed", sans-serif;
                font-weight: 600;
                color: #669DBB;
                padding: 5px 0;
            }
        }
    }
}

.event-panel-banner {
	height: 35px;
	background-color: #f5f5f5;
	margin: 0 1px;
	padding: 9px 0 0 8px;
	font-size: 12px;
	color: #777;
	left: 0;
	right: 0;
	text-shadow: none;
	letter-spacing: 0.5px;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;

	h5 {
		font-size: 10px;
		display: inline;
		color: #837e7e;
		text-shadow: none;
	}

	.vi {
		font-size: 15px;
		color: #007dc6;
		vertical-align: sub;
		padding: 0 4px 0 15px;
	}

	div {
		display: inline-block;
	}

	.event-status {
		float: right;
		padding-right: 15px;
	}
}
.slick-single-item {
	height: 1px;
	overflow: hidden;
	visibility: hidden;
	transition: height 0.2s linear;
}

.slick-carousel-caption {
    text-align: left;
    color: #000;
    overflow: hidden;

    > h3 {
        font-size: 14px;
        font-weight: 800;
        color: #333;
        text-shadow: none;
        line-height: 1.5;
        margin: 0;
    }

    h5, p {
        color: #837e7e;
        text-shadow: none;
    }

    h5 {
        font-size: 10px;
    }

    p {
        font-size: 12px;
        margin: 3px 0 0 0;
    }

    .label {
        font-size: 10px;
        letter-spacing: 0.7px;
        font-weight: 500;
        text-transform: uppercase;
        padding: 0.3em 0.6em .3em;
    }

    .event-status {
        margin-bottom: 10px;
    }
}

.slick-carousel-control {
    position: absolute;
    z-index: 1;
    width: 20px;
    font-size: 10px;
    text-shadow: none;
    cursor: pointer;
    opacity: .07;
    color: #333;
    background-color: transparent;

    .glyphicon-chevron-left, .glyphicon-chevron-right {
        position: absolute;
        display: inline-block;
        width: 30px;
        height: 30px;
        font-size: 30px;
        margin-left: -5px;
    }

    &:hover {
        opacity: 1;
        color: #007dc6;
        background-color: transparent;
        transition: all 0.3s;
    }

    &.left {
        left: 0;
    }

    &.right {
        right: 0;
    }
}
.slick-carousel-register-footer {
    font-family: "roboto condensed";
    letter-spacing: 1px;
    margin: 37px 0 0 0;

    .link-button {
        padding: 5px 0 0 25px;
        display: inline-block;
        float: none;
        margin: 0;
    }

    .link-delete {
        font-size: 20px;
        float: right;
        padding: 4px 15px 0 0;

        .vi-delete {
            color: lighten(#007dc6, 5%);
        }
    }
}

.slick-dot {
    display: inline-block;
    border: 1px solid #ccc;
    width: 6px;
    height: 6px;
    margin: 0;
    text-indent: -999px;
    border-radius: 5px;
    cursor: pointer;
    background-color: #000 \9;
    background-color: rgba(0, 0, 0, 0);

    &:hover {
        width: 10px;
        height: 10px;
        transform: translateY(2px);
        transition: all 0.1s;
    }
}
.slick-dots li {
  width: 10px;
  height: 10px;
  margin: 0 0 15px 0;
}

.slick-active .slick-dot {
    margin: 0;
    width: 6px;
    height: 6px;
    background-color: #5288ce;
    border: #5288ce 1px solid;

    &:hover {
        width: 10px;
        height: 10px;
        transform: translateY(2px);
        transition: all .1s;
    }
}

.event-bulletin {
    .no-data-stacked-small-box {
        padding-top: 0;
    }
}
.event-registered {
    .slick-dots {
        bottom: 37px;
    }

    .slick-carousel-caption {
        padding: 10px 25px;
        height: 115px;
    }

    .slick-carousel-control {
        top: 35px;
        bottom: 36px;

        .glyphicon-chevron-left, .glyphicon-chevron-right {
            top: 50px;
        }
    }
}
.event-watchlist {
    .slick-dots {
      bottom: 35px;
    }
    .slick-carousel-caption {
      padding: 10px 25px;
      height: 115px;
    }
    .slick-carousel-control {
        top: 35px;
        bottom: 36px;
        .glyphicon-chevron-left, .glyphicon-chevron-right {
          top: 50px;
        }
    }
}
.event-recommend {
    .slick-dots {
        bottom: 35px;
    }

    .slick-carousel-caption {
        padding: 10px 25px;
        height: 115px;
    }

    .slick-carousel-control {
        top: 35px;
        bottom: 36px;

        .glyphicon-chevron-left, .glyphicon-chevron-right {
            top: 50px;
        }
    }
}

@media (max-width: $screen-lg) {
  .event-panel-container { 
     //margin: -50px 11px 0 11px;
     > hr {
        display: none;
     }
     > div > .event-panel {
        flex: 1 1 100%;
     }
  }
}

@media (max-width: $screen-sm) {
    .event-panel-container {
        //margin: -65px 0px 0px 0px;
        > div {
            padding: 10px 0 0 0;
        }
    }
    .event-panel {
        margin: 10px 0;
        border-radius: 0;
    }
}

@media (max-width: $screen-note) {
    .event-panel > h2 {
      margin: 10px 10px 10px 15px;
      padding: 5px;
    }

    .slick-carousel-control {
      display: none;
    }
}

@media (max-width: $screen-xtn) {
    .event-panel-banner {
        margin: 0;
        padding: 10px 0 0 1px;
        font-size: 10px;
    }
    .event-panel > h2 {
        margin: 10px 2px 10px 2px;
    }
}